import React from "react";
import {
  ActivityIndicator,
  View,
  StyleSheet,
  Text,
  FlatList,
  Dimensions,
  Image,
  ScrollView,
  TouchableOpacity,
  TextInput,
} from "react-native";
import {
  heightPercentageToDP as hp,
} from "react-native-responsive-screen";
import axios from "axios";
import { env } from "../../../config/systemConfigs";

import {
  Button,
  Card,
  Modal,
  Menu,
  Provider,
} from "react-native-paper";
const { width, height } = Dimensions.get("window");
import { CreateNewOrderModal } from "./child-component/CreateNewOrderModal";
import BulkOrderUploadComponent from "./child-component/BulkOrderUploadComponent.web";
import {getSearchJsonOnSearchString, formatAmount, formatMaturityDate, formatCoupon} from "../../../shared/services/utilServices";
import MenuAppBar from "../../../shared/component/appBar/header";
import AlertDialog from "../../../shared/component/dialog/alertDialog";
import Footer from "../../../shared/component/appBar/footer";
import UserPrefContext from "../../../shared/context/UserPrefContextProvider";

interface OrderBookProps {
  navigation: any;
}
//inteface for component state
interface OrderBookState {}

export class OrderBook extends React.Component<
  OrderBookProps,
  OrderBookState
> {
  //get context.
  static contextType = UserPrefContext;

  state = {
    Orderbonds: [],
    orderToEdit: null,
    showMenu: false,
    searchQuery: "",
    registerInterestMessage: "",
    loader: true,
    orderToDelete: [],
    showBulkOrderModal: false,
    viewMyOrders: false,
    showModal: false,
  };

  async componentDidMount() {
    //if page is refresh or context is empty, so then it can be set in context
    if (this.context.userData.userName == "") {
      await this.context.apiToGetAuthenticatedData();
    }
    this.getOrders(false, this.state.searchQuery);
  }

  searchOrders(text: string){
    this.setState({searchQuery: text});
    if((text.length > 5) || (text == "")){
      this.getOrders(this.state.viewMyOrders, text);
    }
  }

  //getOrders : Single function to get corresponding orders
  getOrders(myOrders: boolean, searchQuery: string){
    let postJson = {};
    this.setState({loader: true, showMenu: false});

    if(myOrders && this.context.userData.userId !== ""){
      postJson["userName"] = this.context.userData.userName;
      this.setState({ viewMyOrders: true, }); 
    }
    else{
      this.setState({viewMyOrders: false});
    }
    if(searchQuery.length > 5 ){
      postJson = {...postJson, ...getSearchJsonOnSearchString(searchQuery)};
    }
    axios({
      method: "POST",
      data: postJson,
      withCredentials: true,
      url: env.API_TRADING_MODULE + "/tradingManagement/getOrders",
    })
    .then((result) => {
      this.setState({ loader: false });
      //seting the bonds array in the bonds
      if (result.status === 200) {
        this.setState({ Orderbonds: result.data });
      }
    })
    .catch((err) => {
      alert("Oops! Something went wrong. Please try again.")
      console.log("Error console:", err);
      this.setState({ loader: false });
    });
  }

  //here we set interested as there bond and user id.
  registerInterest(orderId: string, interested: any) {
    let userId = this.context.userData.userName;
    //logic for when user need to create only once
    if (!interested.some((el: any) => el.userName === userId)) {
      this.setState({ loader: true });
      axios({
        method: "POST",
        data: { _id: orderId, date: Date.now(), },
        withCredentials: true,
        url: env.API_TRADING_MODULE + `/tradingManagement/interestedUsers`,
      })
        .then((result) => {
          if (result.status === 200) {
            this.setState({registerInterestMessage: "Thank you for your interest. Our representative will get back to you shortly.", loader: false, });
          }
        })
        .catch((err) => {
          alert("Oops! Something went wrong. Please try again.")
          console.log("Error console:", err);
          this.setState({ loader: false });
        });
    } else {
      this.setState({registerInterestMessage: "Your interest has been already registered. Kindly give us time to respond.", });
    }
  }

  cancelOrder() {
    if(this.state.orderToDelete.length > 0){
      this.setState({loader: true, });
      axios({
        method: "POST",
        withCredentials: true,
        data: { orders: this.state.orderToDelete },
        url: env.API_TRADING_MODULE + `/tradingManagement/deleteOrder`,
      })
        .then((result) => {
          this.setState({ Orderbonds: [], orderToDelete: [], loader: false, });
          if (result.status === 200) {
            this.getOrders(this.state.viewMyOrders, this.state.searchQuery);
          }
        })
        .catch((err) => {
          alert("Oops! Something went wrong. Please try again.")
          console.log("Error console:", err);
          this.setState({ loader: false });
        }); 
    }
  }

  HideShowBulkOrderModal(value: boolean) {
    if(this.context.userData.roleId > 0){
      this.setState({ showBulkOrderModal: value, showMenu: false, });
      if(value == false){ //refresh orderbook if closing the modal
        this.getOrders(this.state.viewMyOrders, this.state.searchQuery);
      }
    }
  }

  HideShowOrderModal(orderDetail: Object, hideOrShow: boolean) {
    if(this.context.userData.roleId > 0){
      this.setState({ orderToEdit: orderDetail, showMenu: false, showModal: hideOrShow});
      if(hideOrShow == false){
        this.getOrders(this.state.viewMyOrders, this.state.searchQuery);
      }
    }
  }

  HideShowDeleteOrderModal(value: any[]){
      this.setState({orderToDelete: value})
  }

  deleteAllMyOrders() {
    let deletAllBondsArray: String[] = [];
    if(this.state.Orderbonds.length > 0){
      this.state.Orderbonds.map((element: any) => {
        if(element.userName == this.context.userData.userName){
          deletAllBondsArray.push(element._id);
        }
    });
    this.HideShowDeleteOrderModal(deletAllBondsArray);
    }
  }

  showIntrestedUsers(bond: any) {
    //navigate to intrested user screen by getting user list and bond name copone and etc.
    this.props.navigation.navigate("intresteduser", JSON.stringify(bond));
  }

  render() {
    return(
      <Provider>
      <View style={{ height: height, width: width }} >
      <ScrollView style={{ backgroundColor: "#FBFCFF", height: height }}>
        <MenuAppBar navigate={this.props.navigation} />
        <View style={{ backgroundColor: "#FBFCFF", paddingLeft: "8%", paddingRight: "8%", }} >
          <View style={{ flex: 1, paddingTop: "1%", flexDirection: "row", justifyContent: "space-between", }} >
            <View style={{ flex: 1 }}>
              <Text style={{fontWeight: "bold", fontSize: 26, alignSelf: "flex-start", }} > Order Book </Text>
            </View>
          </View>
          <View style={{ flexDirection: "row" }}>
            <View style={styles.searchView}>
              <Image style={styles.searchImage} source={require("../../../assets/icons/search.svg")} />
              <TextInput 
                style={styles.searchTextInput} 
                placeholder="Search" 
                onChangeText={(text) => {this.searchOrders(text); }} 
                value={this.state.searchQuery} 
              />
            </View>
            <View style={{marginTop: "2%"}}> 
            <Menu
              visible={this.state.showMenu}
              onDismiss={() =>{this.setState({ showMenu: false, });}}
              anchor={
                <Button uppercase={false} style={{ backgroundColor: "#FFF" }} onPress={() => { this.setState({ showMenu: true, });; }} >
                  <Image style={{ height: 20, width: 20 }} source={require("../../../assets/menu-open.svg")} />
                </Button>
              }
            >
              <Menu.Item onPress={() => { this.getOrders(false, this.state.searchQuery ); }} title="View All Orders" />
              <Menu.Item onPress={() => { this.HideShowOrderModal(null, true); }} title="Create Single Order" />
              <Menu.Item onPress={() => { this.HideShowBulkOrderModal(true); }} title="Create Bulk Orders" />
              <Menu.Item onPress={() => { this.getOrders(true, this.state.searchQuery); }} title="View My Orders" />
            </Menu>
            </View>
            {this.state.viewMyOrders ? (
              <Button 
                style={{ width: "17%", height: "55%", marginTop: "2%", backgroundColor: "#F95E68" }} 
                color={"#FFFF"} onPress={() => { this.deleteAllMyOrders(); }} >
                Cancel All Orders
              </Button>
            ) : null}
          </View>
        </View>
        {(!this.state.loader && this.context.userData.roleId === -1) ?  (
          <AlertDialog open={true} navigation={this.props.navigation} title={""}
            message={ "You need to Sign In to view details. Click here to Sign In." }
            bttonText={"Sign In"}
          />
        ) : (
          <>
            {this.state.loader && <ActivityIndicator size="large" color="#00ff00" />}
            {(this.state.Orderbonds.length == 0 && !this.state.loader ) ? (
              <View style={{ flexDirection: "row", width: "100%", alignItems: "center", justifyContent: "space-evenly", }} >
                <Text style={{ fontSize: 15, fontFamily: "Inter", marginTop: 20, }} > No orders to display. </Text>
              </View>
            ) : (
              <FlatList
                data={this.state.Orderbonds}
                renderItem={(bond: any) => (
                  <Card style={styles.cardItem}>
                    <View style={styles.bondView}>
                      <View style={{ width: "20%", flexDirection: "row" }} >
                        <Text style={styles.bondNameText}> {bond.item.issuerShortName + " "} </Text>
                        {bond.item.couponRate === undefined ? ( " " ) : (
                          <Text style={styles.bondNameText}> {formatCoupon(bond.item.couponRate)} </Text>
                        )}
                        <Text style={styles.bondNameText}> {formatMaturityDate(bond.item.dateOfMaturity, true)} </Text>
                      </View>
                      <View style={styles.cell}>
                        <Text style={styles.BondNameTextSubTitel}> Rating </Text>{" "}
                        <View style={{ flexDirection: "row" }}>                                   
                          <Text style={{ textAlign: "right", color: "#454F5B", }} > {bond.item.creditRating} </Text>
                        </View>
                      </View>
                      <Image style={{ height: "100%", width: 2, resizeMode: "contain", }} source={require("../../../assets/icons/Line.png")} /> 
                      <View style={styles.cell}>
                        <Text style={{ textAlign: "right", color: "#454F5B", fontWeight: "500", }} >
                          {bond.item.flags === undefined ? "-" : bond.item.flags}
                        </Text>
                      </View>
                      <Image style={{ height: "100%", width: 2, resizeMode: "contain", }} source={require("../../../assets/icons/Line.png")} />
                      <View style={styles.cell}>
                        <Text style={styles.BondNameTextSubTitel}> YTM </Text>{" "}
                        <Text style={{ textAlign: "right", color: "#454F5B", fontWeight: "500", }} > 
                          {bond.item.yield + " %"}
                        </Text>
                      </View>
                      <Image style={{ height: "100%", width: 2, resizeMode: "contain", }} source={require("../../../assets/icons/Line.png")} />
                      <View style={styles.cell}>
                        <Text style={styles.BondNameTextSubTitel}> Amount </Text>{" "}
                        <Text style={{ textAlign: "right", color: "#454F5B", fontWeight: "500", }} >
                          {formatAmount(bond.item.amount)}
                        </Text>
                      </View>
                      <Image style={{ height: "100%", width: 2, resizeMode: "contain", }} source={require("../../../assets/icons/Line.png")} />
                      {formatMaturityDate(bond.item.dateOfMaturity, false) === "PERP" ?(
                        <View style={styles.cell}>
                          <Text style={styles.BondNameTextSubTitel}> Call Date </Text>{" "} 
                          <Text style={{ textAlign: "right", color: "#454F5B", fontWeight: "500", }} >
                            {formatMaturityDate(bond.item.callDate, false)}
                          </Text>
                        </View>
                      ):(
                        <View style={styles.cell}>
                          <Text style={styles.BondNameTextSubTitel}> Maturity Date </Text>{" "} 
                          <Text style={{ textAlign: "right", color: "#454F5B", fontWeight: "500", }} >
                            {formatMaturityDate(bond.item.dateOfMaturity, false)}
                          </Text>
                        </View>
                      )}
                      <View style={styles.cellBlank}>
                        {bond.item.userName === this.context.userData.userName ? (
                          <View style={{ flexDirection: "row" }}>
                            <View style={{ marginTop: 25 }}>
                              <TouchableOpacity onPress={() => this.HideShowOrderModal(bond.item, true)} >
                                <Image style={{ height: 15, width: 15 }} source={require("../../../assets/icons/edit.svg")} />
                              </TouchableOpacity>
                            </View>
                            <View style={{ marginTop: 25, marginLeft: 10 }} >
                              <TouchableOpacity onPress={() => this.HideShowDeleteOrderModal([bond.item._id]) } >
                                <Image style={{ height: 15, width: 15 }} source={require("../../../assets/close.svg")} />
                              </TouchableOpacity>
                            </View>
                            {bond.item.interestedUsers.length > 0 ? (
                              <View style={{ marginTop: 20, marginLeft: 10 }} >
                                <TouchableOpacity onPress={() => this.showIntrestedUsers(bond.item) } >
                                  <Image style={{ height: 25, width: 25 }} source={require("../../../assets/intrested_user.svg")} />
                                </TouchableOpacity>
                              </View>
                            ) : ( null )}
                          </View>
                        ) : (
                          <Button 
                            style={{ backgroundColor: "#5C6AC4" }}
                            color="#FFF"
                            onPress={() => this.registerInterest( bond.item._id, bond.item.interestedUsers ) }
                          >
                            {bond.item.isBuy ? "Sell" : "Buy"}
                          </Button>
                        )}
                      </View> 
                    </View>
                  </Card>
                )}
              />
            )}
          </>
          )}
          <Footer />
      </ScrollView>
      <CreateNewOrderModal 
        OrderToEdit={this.state.orderToEdit} 
        BondToOrder={null} 
        showModal={this.state.showModal}
        hideModal={()=> {this.HideShowOrderModal(null, false);}}
      />
  
      <BulkOrderUploadComponent ModalHideShow={this.HideShowBulkOrderModal.bind(this)} isHide={this.state.showBulkOrderModal} />
              
      <Modal visible={this.state.registerInterestMessage != ""}>
        <Card style={styles.card}>
          <View style={styles.cardView}>
            <Text style={{ width: "100%", fontFamily: "Inter", fontSize: 20, fontStyle: "normal", padding: 10, }} >
              {this.state.registerInterestMessage}
            </Text>
          </View>
          <Card.Actions style={styles.cardAction}>
            <View style={{ width: 5 }} />
            <Button mode={"contained"} style={styles.submitButton} uppercase={false} onPress={() => { this.setState({ registerInterestMessage: "",  }); }} >
              Ok
            </Button>
          </Card.Actions>
        </Card>
      </Modal>
      <Modal visible={this.state.orderToDelete.length > 0}>
        <Card style={styles.cardDelete}>
          <View style={styles.cardView}>
            <Text
              style={{ width: "100%", fontFamily: "Inter", fontSize: 15, fontStyle: "normal", padding: 10,
                alignContent: "center", justifyContent: "center", textAlign: "center",
              }}
            >
              Do you really want to cancel orders?
            </Text>
          </View>
          <Card.Actions style={styles.cardAction}>
            <View style={{ width: 5 }} />
              <Button mode={"contained"} style={styles.submitButton} uppercase={false} onPress={() => { this.cancelOrder(); }} >
                Ok
              </Button>
              <Button mode={"contained"} style={styles.cancelButton} uppercase={false} onPress={() => { this.HideShowDeleteOrderModal([]) } } >
                Cancel
              </Button>
          </Card.Actions>
        </Card>
      </Modal>
    </View>
    </Provider>
    );
  }
}

const styles = StyleSheet.create({
  headCell: {
    height: hp("5%"),
    justifyContent: "center",
    alignItems: "center",
    flex: 0.2,
  },
  cell: {
    height: hp("10%"),
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "#FFF",
    flex: 0.2,
  },
  cellBlank: {
    height: hp("10%"),
    justifyContent: "center",
    alignItems: "center",
    flex: 0.2,
  },
  cardItem: {
    marginLeft: "8%",
    marginRight: "10%",
    elevation: 5,
    marginTop: "1%",
  },
  bondNameText: {
    textAlign: "left",
    fontSize: 15,
    fontWeight: "500",
    paddingTop: "10%",
    color: "#454F5B",
  },
  BondNameTextSubTitel: {
    color: "#637381",
  },
  bondView: {
    flexDirection: "row",
    justifyContent: "center",
    margin: 2,
    paddingLeft: 20,
    paddingRight: 20,
  },
  RiskImage: {
    height: 20,
    width: 20,
    marginRight: 5,
  },
  searchView: {
    width: "70%",
    height: "44px",
    flexDirection: "row",
    backgroundColor: "#EFF2F5",
    borderRadius: 6,
    elevation: 10,
    marginRight: "5%",
    marginTop: "2%",
  },
  searchImage: {
    height: "30px",
    width: "30px",
    marginLeft: 15,
    marginTop: 5,
    marginRight: 15,
  },
  searchTextInput: {
    color: "#808C9A",
    fontSize: 20,
    lineHeight: 24,
    letterSpacing: -0.339623,
    width: "80%",
    paddingLeft: 10,
    borderColor: "#EFF2F5",
    borderWidth: 0.1,
  },
  card: {
    marginTop: "15%",
    marginLeft: "30%",
    marginRight: "30%",
    marginBottom: "15%",
  },
  cardAction: {
    alignContent: "center",
    justifyContent: "center",
  },
  cardView: {
    height: 50,
    flexDirection: "row",
    alignContent: "space-around",
    justifyContent: "space-evenly",
    marginBottom: 20,
    padding: 20,
  },
  cardLabelText: {
    height: 22,
    width: 70,
    fontSize: 18,
    letterSpacing: -0.30566,
    lineHeight: 22,
    marginLeft: 20,
  },
  cardInputText: {
    height: 50,
    width: 130,
    backgroundColor: "#EFF2F5",
    color: "#525252",
    marginLeft: 20,
    padding: 5,
    fontSize: 20,
    lineHeight: 24,
    marginTop: 10,
    marginBottom: 10,
  },
  submitButton: {
    width: 100,
    height: 45,
    paddingTop: 5,
    backgroundColor: "#27AE60",
    color: "#FFFFFF",
    letterSpacing: -0.271698,
    fontSize: 16,
    marginTop: 35,
  },
  cancelButton: {
    width: 100,
    height: 45,
    paddingTop: 5,
    backgroundColor: "#F95E68",
    color: "#FFFFFF",
    letterSpacing: -0.271698,
    fontSize: 16,
    marginTop: 35,
    marginLeft: "2%",
  },
  cardDelete: {
    marginTop: "15%",
    marginLeft: "40%",
    marginRight: "40%",
    marginBottom: "15%",
  },
});
