import React, { useState } from "react";
import {
  ActivityIndicator,
  View,
  StyleSheet,
  ScrollView,
  FlatList, Dimensions,
  Picker,
  TouchableOpacity,
} from "react-native";
const { width, height } = Dimensions.get("window");
import { TextInput, Button, Card, Text, Snackbar } from "react-native-paper";
import axios from "axios";
import { env } from "../../config/systemConfigs";
import MultiSelect from "react-multi-select-component";
import { SnackBarFunction, SnackBarFunction1 } from "./SnackBar";
import $ from "jquery";
import AsyncStorage from "@react-native-community/async-storage";
import { red } from "@material-ui/core/colors";
import MenuAppBar from "../../shared/component/appBar/header";
import Footer from "../../shared/component/appBar/footer";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import AlertDialog from "../../shared/component/dialog/alertDialog";

import { userContextDataChange } from '../../shared/context/UserPrefContextProvider';

//this is ArticleManagement function component
export const SaveArticle = (props: any) => {

  //VK: currently hardcoded as shortcut. Actually this whole should be re-written
  //only saving Knowledge and MarketData categories, rest all is Research by default
  let reportCategorization = {"Blogs": "Knowledge", "Support": "Knowledge", "Liquidity Cheat Sheet": "MarketData",
  "Primary Corporate Bond Market": "MarketData", "Corporate Bond and Money Market Traded Data": "MarketData",
  "Government Market Borrowing": "MarketData", "RBI OMO": "MarketData", "Government Bond conversion": "MarketData", "Deposit and Credit Data": "MarketData",
  "Forex Data": "MarketData", "FII investment": "MarketData"} ;
  
  // here we create the context to get user data which store in context
  const { userData, setUserData, apiToGetAuthenticatedData } = userContextDataChange()!;


  //to set api data of category,metaData and similar articles in this state
  const [allArticles, setArticleGetData] = useState({
    title: [
      {
        label: "",
        value: "",
      },
    ],
    category: [
      {
        label: "",
        value: "",
      },
    ],
    metaData: [
      {
        label: "",
        value: "",
      },
    ],
  });
  //state to submit on preview/publish
  const [articleData, setArticleData] = React.useState({
    userName: "",
    userId: "",
    title: "",
    synopsis: "",
    author: "",
    category: "",
    newCategory: "",
    similarArticles: {},
    relevantArticles: [],
    similarArticlesUpdates: [{ _id: "", title: "" }],
    articleIcon: "",
    categoryDescription: "",
    articleJSX: "",
    authorImage: "",
    metaData: "",
    message: "",
    articleDate: Date.now(),
    categoryCheckbox: false,
    checkboxLabelText: "Enter new Category",
    researchReports: [],
    reportType: "Research",
    iconSize: 0,
    authImageSize: 0,
    htmlDocSize: 0,
    buttonDisable: false,
    bondId: "",
    imagesToShow: "",
    articleId: "",
    isToUpdate: "",
    iconExt: "",
    authImgExt: "",
    docZip: "",
    docZipName: "",
    docTimeStamp: "",
    previewTimeStamp: "",
    priorityOrder: 0
  });

  //for selected articles during replacement
  const [selectedSimilarArticle, setSelectedSimilarArt] = useState([
    { label: "", value: "" },
  ]);



  //to show present similar articles
  const [metaDataResearchDetails, setMetaDataResearchReport] = React.useState({
    metaData: "",
    researchReports: [],
  });

  //to show description on basis of category selection
  const [catDescription, setCatDescription] = React.useState({
    description: [],
  });

  //to disable buttons
  const [buttonDisabled, setButtonDisable] = React.useState({
    buttonDisable: false,
  });

  //to hide fields on category selection

  const [selectedCategory, setselectedCategory] = React.useState({
    showHide: false,
  });

  //to show loader
  const [loader, setLoader] = React.useState(false);

  //for bond details/data - searched result
  const [bondData, setBondData] = React.useState({
    searchedData: [],
    searchQuery: "",
    bondId: "",
    _id: "",
  });

  //to show selected multiselect article data
  const [showArticleData, setShowArticleData] = React.useState([
    { label: "", value: "" },
  ]);


  //to get loggedIn user details
  const getLoggedInUser = () => {

    //if user is not loggedIn
    if (userData.userName === "") {
      return;
    }

    //this view is only for admin
    if (userData.roleId === 2) {
      toast("You dont have access to this page.", {
        position: "top-center",
        type: "warning",
        hideProgressBar: false,
        closeOnClick: false,
        pauseOnHover: true,
        draggable: true,
      });

      window.history.back();

      return false;
    }


  };//end of getLoggedInUser()
  // getLoggedInUser();

  React.useEffect(() => {
    useEffectForSync();
  }, [1]);

  //called from useEffect
  async function useEffectForSync() {


    //if page is refresh or context is empty, so then it can be set in context
    if (userData.userName == "") {
      await apiToGetAuthenticatedData();
    }


    let getUrlSegments = window.location.search.split("=");
    //if for replacement its from home page then it will be editable
    if (
      getUrlSegments &&
      getUrlSegments[0] == "?id" &&
      getUrlSegments[1] != ""
    ) {
      axios({
        method: "GET",
        withCredentials: true,
        url:
          `${env.API_INFRA_MODULE}/adminManagement/getSpecificArtDetails?_id=` +
          getUrlSegments[1],
      }).then((result) => {

        //if no record available
        if (result.data.length == 0) {
          toast("Sorry it seems to be the requested report is not available.", {
            position: "top-center",
            type: "error",
            hideProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            draggable: true,
          });
          setTimeout(() => {
            window.location.href = env.API_INRB_FE + "/save-article";
          }, "2000");
          return false;
        }

        setArticleData({
          ...articleData,
          userName: result.data.responseData.userName,
          title: result.data.responseData.title,
          synopsis: result.data.responseData.synopsis,
          author: result.data.responseData.author,
          similarArticles: result.data.responseData.similarArticles,
          articleIcon: result.data.responseData.articleIcon,
          similarArticlesUpdates: result.data.responseData.similarArticles,
          category: result.data.responseData.category,
          newCategory: result.data.responseData.category,
          categoryDescription: result.data.responseData.categoryDescription,
          articleJSX: result.data.responseData.articleJSX,
          authorImage: result.data.responseData.authorImage,
          metaData: result.data.responseData.metaData,
          docTimeStamp: result.data.responseData.docTimeStamp,
          articleDate: Date.now(),
          // categoryCheckbox: false,
          // checkboxLabelText: "Enter new Category",
          reportType: result.data.responseData.reportType,//"Research",
          bondId: result.data.responseData.bondId, // selected will be compare with this and this json will be passed in the smilarArticles
          imagesToShow: "true",
          articleId: result.data.responseData._id,
          isToUpdate: "yes",
          priorityOrder: result.data.responseData.priorityOrder
        });

        //for selected articles to show
        var multiSelectedData = [];
        for (
          var i = 0;
          i < result.data.responseData.similarArticles.length;
          i++
        ) {
          multiSelectedData.push({
            label: result.data.responseData.similarArticles[i].title,
            value: result.data.responseData.similarArticles[i]._id,
          });
        }

        //to set seleccted similar article
        setSelectedSimilarArt(multiSelectedData);

        setBondData({
          ...bondData,
          bondId: result.data.responseData.bondId,
          searchQuery: result.data.nameOfIssuer,
        });

        if (result.data.responseData.category == "Market Gauge") {
          setselectedCategory({
            ...selectedCategory,
            showHide: true,
          });
        }


      });
    }
    axios({
      method: "GET",
      withCredentials: true,
      url: `${env.API_INFRA_MODULE}/adminManagement/getArticles`,
    })
      .then((result) => {
        setArticleGetData({
          ...allArticles,
          title: result.data.titleData,
          category: result.data.categoryData,

        });

        //to save descriptions in array of state
        setCatDescription({
          ...catDescription,
          description: result.data.categoryData,
        });

        if (
          getUrlSegments &&
          getUrlSegments[0] == "?id" &&
          getUrlSegments[1] != ""
        ) {
          //to set in meta data text
          setMetaDataResearchReport({
            ...metaDataResearchDetails,
            metaData: result.data.metaData,
            researchReports: result.data.articleData, // selected will be compare with this and this json will be passed in the smilarArticles
          });
        } else {
          setMetaDataResearchReport({
            ...metaDataResearchDetails,
            metaData: result.data.metaData,
            researchReports: result.data.articleData, // selected will be compare with this and this json will be passed in the smilarArticles
          });
          //to set in meta data text
          setArticleData({
            ...articleData,
            metaData: result.data.metaData,
            researchReports: result.data.articleData, // selected will be compare with this and this json will be passed in the smilarArticles
          });
        }
      })
      .catch((err) => {
        console.log("Error console:", err);
      });
  }

  //this is visibitity state of snackbar
  const [visible, setVisible] = React.useState(false);
  const reportTypeData = ["Select Report Type", "research", "analysis"];
  var multiSelectSnackbar = {
    visibilityDropDown: false,
    visibilityDropDown1: false,
    message: "",
    message1: "",
  };

  const [selectedRecord, setSelected] = useState({ title: [], metaData: [] });


  //state is for multiselect dropdown
  const selectedValue = (selectedArticle: any, multiSelectFlag: any) => {
    //onload it should not call, thats why not equal to zero

    if (multiSelectFlag == "title") {
      if (selectedArticle.length !== 0) {
        if (selectedArticle.length <= 4) {
          var data = selectedArticle;
        } else if (selectedArticle.length >= 4) {
          multiSelectSnackbar.message = "Only four articles are allowed.";
          multiSelectSnackbar.visibilityDropDown = true;

          var data = selectedArticle.slice(0, -1);
        } else if (selectedArticle.length > 4) {
          multiSelectSnackbar.message = "Only four articles are allowed.";
          multiSelectSnackbar.visibilityDropDown = true;
          return [];
        }

        return data;
      }
    } //if ends
    if (selectedSimilarArticle.length > 0) {
      selectedArticle =
        articleData.isToUpdate == "" ? [] : selectedSimilarArticle;
      return articleData.isToUpdate == "" ? [] : selectedSimilarArticle;
    } //end of selected articles
  };

  //to convert images file to binary
  const readFileDataAsBase64 = async (e: any, imageFlag: any) => {
    //it undefined
    if (e.target.files[0] === undefined) {
      if (imageFlag == "icon") {
        articleData.articleIcon = "";
      }
      if (imageFlag == "author") {
        articleData.authorImage = "";
      }
      return false;
    }

    console.log(e.target.files[0]);
    if (
      e.target.files[0].type == "image/jpeg" ||
      e.target.files[0].type == "image/png" ||
      e.target.files[0].type == "image/jpg"
    ) {
      if (e.target.files[0].size > 1000000) {
        //icon image
        if (imageFlag == "icon") {
          setArticleData({
            ...articleData,
            iconSize: e.target.files[0].size,
            iconExt: e.target.files[0].type,
          });
        }
        if (imageFlag == "author") {
          setArticleData({
            ...articleData,
            authImageSize: e.target.files[0].size,
            authImgExt: e.target.files[0].type,
          });
        }
        return false;
      } else {
        if (imageFlag == "icon") {
          articleData.iconSize = e.target.files[0].size;
          articleData.iconExt = e.target.files[0].type;
        }
        if (imageFlag == "author") {
          articleData.authImageSize = e.target.files[0].size;
          articleData.authImgExt = e.target.files[0].type;
        }
      }

      var reader = new FileReader();
      reader.readAsDataURL(e.target.files[0]);
      reader.onload = function () {
        if (imageFlag == "icon") {
          setArticleData({
            ...articleData,
            articleIcon: JSON.stringify(reader.result),
          });
        } else if (imageFlag == "author") {
          setArticleData({
            ...articleData,
            authorImage: JSON.stringify(reader.result),
          });
        }
        // else if(imageFlag == "zip"){
        // 	setArticleData({
        // 		...articleData,
        // 		docZip:JSON.stringify(reader.result),
        // 		docZipName:e.target.files[0].name
        // 	});
        // }
      };
      reader.onerror = function (error) {
        console.log("Error: ", error);
      };
    }
    //for zip file
    if (e.target.files[0].type == "application/x-zip-compressed") {
      var reader = new FileReader();
      reader.readAsDataURL(e.target.files[0]);
      var fileName = e.target.files[0].name;

      fileName = fileName.replace(".zip", "");

      reader.onload = function () {
        if (imageFlag == "zip") {
          setArticleData({
            ...articleData,
            docZip: JSON.stringify(reader.result),
            docZipName: fileName,
          });
        }
      };
      reader.onerror = function (error) {
        console.log("Error: ", error);
      };
    }
  };

  //show snackbar with message function.
  const showMessage = (msg: string) => {
    setArticleData({
      ...articleData,
      message: msg,
    });
    onToggleSnackBar();
  };

  //this is snackbar visibility show and hide function.
  const onToggleSnackBar = () => setVisible(!visible);
  const onDismissSnackBar = () => setVisible(false);

  //submit article data to DB to save
  const onSubmit = (flag: any) => {
    var year = new Date().getFullYear();
    var month = new Date().toLocaleString("default", { month: "long" });
    var articleObject;
    //conditions for specific category to set reportType
    if (articleData.category == "Market Gauge") {
      articleData.reportType = "";
    }
    else{ //VK : currently using hardcoded values
      articleData.reportType = reportCategorization[articleData.category];
    }


    articleData.metaData = metaDataResearchDetails.metaData;
    if (selectedRecord.title.length > 4) {
      articleObject = selectedRecord.title.slice(0, -1);
      articleData.relevantArticles = articleObject;
    } else {
      articleData.relevantArticles = selectedRecord.title;
    }

    if (articleData.synopsis.length > 1000) {
      showMessage("Synopsis should be less than 1000 characters");
      return false;
    }

    if (articleData.categoryCheckbox == true && !articleData.newCategory) {
      showMessage("Please enter new category");
      return false;
    }
    if (articleData.categoryCheckbox == false && !articleData.category) {
      showMessage("Please select category");
      return false;
    }
    if (!articleData.docZip && flag == "preview") {
      showMessage("Please choose report zip file.");
      return false;
    }

    if (articleData.iconSize > 1000000) {
      showMessage("Article Icon size should be less than 1 MB.");
      return false;
    }
    if (articleData.authImageSize > 1000000) {
      showMessage("Author image size should be less than 1 MB.");
      return false;
    }

    articleData.categoryCheckbox === true
      ? (articleData.category = articleData.newCategory)
      : "";

    var result;

    // if for updating report
    if (
      articleData.similarArticlesUpdates.length > 0 &&
      articleData.relevantArticles.length == 0 &&
      articleData.isToUpdate == "yes"
    ) {
      var idModified: any;
      idModified = articleData.similarArticlesUpdates.map((obj) => {
        return {
          value: obj._id,
          label: obj.title,
        };
      });
      articleData.relevantArticles = idModified;
    }

    result = metaDataResearchDetails.researchReports
      .filter(function (o1: any) {
        // filter out (!) items in articleData.smilarArticles
        return articleData.relevantArticles.some(function (o2: any) {
          return o1._id === o2.value; // assumes unique id
        });
      })
      .map(function (o: any) {
        return o;
      });

    articleData.similarArticles = result;

    setButtonDisable({
      ...buttonDisabled,
      buttonDisable: true,
    });
    setLoader(true);

    var priorityOrderFor = articleData.priorityOrder
    if (articleData.categoryCheckbox == true) {
      priorityOrderFor = allArticles.category.length
    }



    //to save selected bondId in JSON
    articleData.bondId = bondData.bondId;

    var toSaveReport = {
      userName: articleData.userName,
      title: articleData.title,
      synopsis: articleData.synopsis,
      author: articleData.author,
      category: articleData.category,
      similarArticles: articleData.similarArticles,
      articleIcon:
        articleData.articleIcon != ""
          ? articleData.articleIcon
          : articleData.articleIcon,
      categoryDescription: articleData.categoryDescription,
      articleJSX: articleData.articleJSX,
      authorImage:
        articleData.authorImage != ""
          ? articleData.authorImage
          : articleData.authorImage,
      metaData: articleData.metaData,
      articleDate: Date.now(),
      reportType: articleData.reportType,//"research","research",
      bondId: bondData.searchQuery ? articleData.bondId : "",
      articleId: articleData.articleId,
      userId: articleData.userId,
      iconExt: articleData.iconExt,
      authImgExt: articleData.authImgExt,
      docZip: articleData.docZip,
      docZipName: articleData.docZipName,
      flag: flag,
      docTimeStamp: articleData.docTimeStamp,
      year,
      month,
      previewTimeStamp: articleData.previewTimeStamp,
      priorityOrder: priorityOrderFor
    };


    if (flag == "preview") {
      var toSavePreviewDoc = {
        userName: articleData.userName,
        articleIcon:
          articleData.articleIcon != ""
            ? articleData.articleIcon
            : articleData.articleIcon,
        articleJSX: articleData.articleJSX,
        authorImage:
          articleData.authorImage != ""
            ? articleData.authorImage
            : articleData.authorImage,
        previewDate: Date.now(),
        iconExt: articleData.iconExt,
        authImgExt: articleData.authImgExt,
        docZip: articleData.docZip,
        docTimeStamp: articleData.docTimeStamp,
      };
      axios({
        method: "POST",
        data: toSavePreviewDoc,
        withCredentials: true,
        url: `${env.API_INFRA_MODULE}/adminManagement/preview`,
      }).then((result) => {
        setLoader(false);
        if (result.data.status == 200) {
          toSaveReport.previewTimeStamp = result.data.result.docTimeStamp;
          const toPassDataToPreview = {
            toPublish: toSaveReport,
            previewDoc: result.data.result.articleJSX,
            previewId: result.data.result._id,
            previewDocTimestamp: result.data.result.docTimeStamp,
            updation: articleData.isToUpdate,
          };

          //result.data.result.articleJSX
          setButtonDisable({
            ...buttonDisabled,
            buttonDisable: false,
          });
          props.navigation.navigate("preview", toPassDataToPreview);
        }

        //
      });
    } else {
      var toSaveData = {
        userName: articleData.userName,
        title: articleData.title,
        synopsis: articleData.synopsis,
        author: articleData.author,
        category: articleData.category,
        similarArticles: [] as any[],
        articleIcon: "",
        categoryDescription: "",
        articleJSX: "",
        authorImage: "",
        metaData: articleData.metaData,
        articleDate: Date.now(),
        reportType: "",
        bondId: "",
        articleId: "",
        userId: articleData.userId,
        flag: flag,
        docTimeStamp: articleData.docTimeStamp,
      };
      if (articleData.isToUpdate == "yes") {
        var toUpdateData = {
          userName: articleData.userName,
          title: articleData.title,
          synopsis: articleData.synopsis,
          author: articleData.author,
          category: articleData.category,
          similarArticles: [] as any[],
          articleIcon: "",
          categoryDescription: "",
          articleJSX: "",
          authorImage: "",
          metaData: articleData.metaData,
          articleDate: Date.now(),
          reportType: "",
          bondId: "",
          articleId: articleData.articleId,
          userId: articleData.userId,
          flag: flag,
          docTimeStamp: articleData.docTimeStamp,
        };

        axios({
          method: "PUT",
          data: toUpdateData,
          withCredentials: true,
          url: `${env.API_INFRA_MODULE}/adminManagement/saveArticle`,
        }).then((result) => {
          setLoader(false);
          //here we check the status code and as per the code we show navigation
          if (result.data.status === 409) {
            showMessage(result.data.message);
            setArticleData({
              ...articleData,
              message: result.data.message,
            });
          } else if (result.data.status === 200) {

            toast(result.data.message, {
              position: "top-center",
              type: "success",
              hideProgressBar: false,
              closeOnClick: false,
              pauseOnHover: true,
              draggable: true,
            });
            //refreshed the page
            setTimeout(() => {
              window.location.href = env.API_INRB_FE + "/save-article";
              // location.reload();
            }, 3000);
          } else if (result.data.status === 100) {
            toast(result.data.message, {
              position: "top-center",
              type: "success",
              hideProgressBar: false,
              closeOnClick: false,
              pauseOnHover: true,
              draggable: true,
            });
          }
        }); //end of post
      } else {
        axios({
          method: "POST",
          data: toSaveData,
          withCredentials: true,
          url: `${env.API_INFRA_MODULE}/adminManagement/saveArticle`,
        }).then((result) => {
          setLoader(false);
          //here we check the status code and as per the code we show navigation
          if (result.data.status === 409) {
            showMessage(result.data.message);
            setArticleData({
              ...articleData,
              message: result.data.message,
            });
          } else if (result.data.status === 200) {

            toast(result.data.message, {
              position: "top-center",
              type: "success",
              hideProgressBar: false,
              closeOnClick: false,
              pauseOnHover: true,
              draggable: true,
            });
            //refreshed the page
            setTimeout(() => {
              location.reload();
            }, 3000);
          } else if (result.data.status === 100) {
            toast(result.data.message, {
              position: "top-center",
              type: "success",
              hideProgressBar: false,
              closeOnClick: false,
              pauseOnHover: true,
              draggable: true,
            });
          }
        }); //end of post
      }
    } //end of else
  }; //end of onSubmifdt method


  //on keypress to get bond data
  const getSearchedBonds = (text: any, eventFlag: any) => {
    //after 4 char lenght api nis called

    setBondData({
      ...bondData,
      searchQuery: text,
    });

    if (text != "" && text.length > 3) {
      bondData.searchQuery = text;

      axios({
        method: "GET",
        withCredentials: true,
        url:
          env.API_INFRA_MODULE +
          "/bondManagement/searchForReport?query=" +
          text,
      }).then((result) => {
        //seting the bonds array in the bonds
        if (result.status === 200) {
          if (result.data.length == 0) {
            //to update state
            setBondData({
              ...bondData,
              searchedData: result.data,
              bondId: bondData.searchQuery,
            });
          } else {
            //to update state
            setBondData({
              ...bondData,
              searchedData: result.data,
            });
          }
        } else {
          setBondData({
            ...bondData,
            searchQuery: "",
            searchedData: [],
          });
          setArticleData({
            ...articleData,
            bondId: "",
          });
        }
      });
    } else if (eventFlag == "enter") {
      bondData.searchQuery = text;

      axios({
        method: "GET",
        withCredentials: true,
        url:
          env.API_INFRA_MODULE +
          "/bondManagement/searchForReport?query=" +
          text,
      }).then((result) => {
        //seting the bonds array in the bonds

        if (result.status === 200) {
          if (result.data.length == 0) {
            //to update state
            setBondData({
              ...bondData,
              searchedData: result.data,
              bondId: bondData.searchQuery,
            });
          } else {
            //to update state
            setBondData({
              ...bondData,
              searchedData: result.data,
            });
          }

          /*//to update state
          setBondData({
            ...bondData,
            // searchQuery: empty,
            searchedData: result.data,
          });*/
        } else {
          setBondData({
            ...bondData,
            searchQuery: "",
            searchedData: [],
          });
          setArticleData({
            ...articleData,
            bondId: "",
          });
        }
      });
    } else if (text.length === 0) {
      setBondData({
        ...bondData,
        searchQuery: "",
        searchedData: [],
        bondId: "",
      });
    }
  };

  //when hit/enter on inputtext
  const onEnterPress = (input: any, event: any) => {
    if (event.nativeEvent.key == "Enter") {
      getSearchedBonds(input, "enter");
    }
  };

  //JSX UI
  return (
    <View style={{ backgroundColor: "#FCFCFC" }}>
      <MenuAppBar navigate={props.navigation} />

      {userData.userName == "notlogin" ? (
        <View style={{ height: height, width: width, }}>
          <AlertDialog
            open={true}
            navigation={props.navigation}
            title={""}
            message={
              "You need to Sign In to view details. Click here to Sign In."
            }
            bttonText={"Sign In"}
          />
        </View>
      ) : (
          <View style={styles.container}>
            <ToastContainer />
            <View style={{ flexDirection: "row", marginBottom: 12 }} />
            <Card style={styles.card}>
              {/* <br /><br /><br /><br /> */}
              <Card.Content
                style={
                  selectedCategory.showHide
                    ? styles.card_contentForMarketGuage
                    : styles.card_content
                }
              >

                <Text>
                  <TouchableOpacity onPress={() => { window.location.href = env.API_INFO_RECOM_MODULE + "/admin" }}>
                    <View style={styles.titleText} >Admin</View>
                  </TouchableOpacity>

                  <View style={styles.breadcrumbTitle}> \ Research Report {userData.userName && getLoggedInUser()}</View>
                </Text>
                <br />
                <View>
                  {selectedCategory.showHide ? null : (
                    <View style={{ marginTop: 6 }}>
                      {/* here todo color change of DD */}
                      {/* <Picker
                      style={{
                        color: "rgb(0, 0, 0)",
                        height: 58,
                        marginTop: 3,
                        paddingBottom: "0px",
                        paddingTop: "0px",
                        backgroundColor: "rgb(242, 242, 242)",
                        fontSize: 17,
                        paddingLeft: 8,
                      }}
                      selectedValue={articleData.reportType}
                      mode="dropdown"
                      onValueChange={(itemValue, itemIndex) =>
                      {  
                        alert(articleData.reportType)
                        setArticleData({
                          ...articleData,
                          reportType: itemValue,
                        })
                      }
                      }
                    >
                      {reportTypeData.map((item: any, index: any) => {
                        return (
                          <Picker.Item
                            color="rgb(0, 0, 0)"
                            label={item.charAt(0).toUpperCase() + item.slice(1)}
                            value={item}
                            key={item}
                          />
                        );
                      })}
                    </Picker> */}
                    </View>
                  )}

                  <View style={{ marginTop: 6 }}>
                    <View style={{ flexDirection: "row" }}>
                      {!articleData.categoryCheckbox && (
                        <View>
                          <Picker
                            style={{
                              color: "rgb(0, 0, 0)",
                              height: 58,
                              marginTop: 3,
                              paddingBottom: "0px",
                              paddingTop: "0px",
                              backgroundColor: "rgb(242, 242, 242)",
                              fontSize: 17,
                              paddingLeft: 8,
                              width: 600,
                            }}
                            selectedValue={articleData.category}
                            mode="dropdown"
                            onValueChange={(itemValue, itemIndex) => {
                              var catDescriptionData = "";
                              var priorityOrder: any;
                              //to set for match cate for its description
                              catDescription.description.forEach((e: any) => {
                                if (e.label === itemValue) {
                                  catDescriptionData = e.description;
                                  priorityOrder = e.priorityOrder;
                                }
                              }); //end of foreach

                              setArticleData({
                                ...articleData,
                                category: itemValue,
                                categoryDescription: catDescriptionData,
                                priorityOrder: priorityOrder
                              });

                              if (itemValue == "Market Gauge") {
                                setselectedCategory({
                                  ...selectedCategory,
                                  showHide: true,
                                });
                              } else {
                                setselectedCategory({
                                  ...selectedCategory,
                                  showHide: false,
                                });
                              }
                            }}
                          >
                            <Picker.Item label="Select Category" value="" />
                            {allArticles.category.map((item: any, index: any) => {
                              return (
                                <Picker.Item
                                  color="rgb(0, 0, 0)"
                                  label={item.label}
                                  value={item.label}
                                  key={item.label}
                                />
                              );
                            })}
                          </Picker>
                        </View>
                      )}
                      {articleData.categoryCheckbox && (
                        <View>
                          {" "}
                          <View style={{ width: 545 }}>
                            <TextInput
                              placeholder="New category"
                              underlineColor="white"
                              selectionColor="#004433"
                              mode="outlined"
                              onChangeText={(val) =>
                                setArticleData({
                                  ...articleData,
                                  newCategory: val,
                                })
                              }
                              value={articleData.newCategory}
                            />
                          </View>
                        </View>
                      )}

                      {selectedCategory.showHide ? null : (
                        <View style={{ marginTop: 13, marginLeft: 20 }}>
                          <input
                            type="checkbox"
                            defaultChecked={articleData.categoryCheckbox}
                            onChange={(e) => {
                              const { checked } = e.target;

                              setArticleData({
                                ...articleData,
                                categoryCheckbox: checked,
                                checkboxLabelText:
                                  articleData.categoryCheckbox === false
                                    ? "Select existing Category"
                                    : "Enter new Category",
                                newCategory:
                                  articleData.categoryCheckbox === true
                                    ? ""
                                    : articleData.newCategory,
                                categoryDescription:
                                  articleData.categoryCheckbox === true
                                    ? ""
                                    : articleData.categoryDescription,
                                category:
                                  articleData.categoryCheckbox === false
                                    ? ""
                                    : articleData.category,
                              });
                            }}
                          />
                        </View>
                      )}
                      {selectedCategory.showHide ? null : (
                        <Text style={styles.checkboxLabel}>
                          {articleData.checkboxLabelText}
                        </Text>
                      )}
                    </View>
                  </View>

                  {/* {selectedCategory.showHide?null:} */}

                  {/* <View> */}

                  {selectedCategory.showHide ? null : (
                    <View style={{ marginTop: 6 }}>
                      <TextInput
                        // style={{ height: 140, textAlign: "auto" }}
                        placeholder="Category Description"
                        maxLength={1000}
                        multiline={true}
                        numberOfLines={5}
                        textAlignVertical="top"
                        underlineColor="white"
                        selectionColor="#004433"
                        mode="outlined"
                        onChangeText={(val) =>
                          setArticleData({
                            ...articleData,
                            categoryDescription: val,
                          })
                        }
                        value={articleData.categoryDescription}
                      />
                    </View>
                  )}

                  <View>
                    <TextInput
                      placeholder="Title"
                      maxLength={255}
                      underlineColor="white"
                      selectionColor="#004433"
                      mode="outlined"
                      onChangeText={(val) =>
                        setArticleData({
                          ...articleData,
                          title: val,
                        })
                      }
                      value={articleData.title}
                    />
                  </View>
                  <View>
                    <TextInput
                      placeholder="Author"
                      underlineColor="white"
                      selectionColor="#004433"
                      mode="outlined"
                      onChangeText={(val) =>
                        setArticleData({
                          ...articleData,
                          author: val,
                        })
                      }
                      value={articleData.author}
                    />
                  </View>

                  {selectedCategory.showHide ? null : (
                    <View style={{ zIndex: 12, position: "relative" }}>
                      <TextInput
                        placeholder="Search Issuer Name"
                        underlineColor="white"
                        selectionColor="#004433"
                        mode="outlined"
                        onChangeText={(text: any) => {
                          getSearchedBonds(text, "keyPress");
                        }}
                        onKeyPress={(event: any) =>
                          onEnterPress(bondData.searchQuery, event)
                        }
                        value={bondData.searchQuery}
                      />
                      {bondData.searchedData.length > 0 && (
                        <ScrollView
                          style={{
                            zIndex: 10,
                            position: "absolute",
                            height: 250,
                            marginTop: 62,
                            width: "100%",
                          }}
                        >
                          <FlatList
                            data={bondData.searchedData}
                            renderItem={(element: any) => (
                              <View style={styles.orderitem}>
                                <Text
                                  style={{ fontWeight: "bold", paddingLeft: 45 }}
                                  onPress={() => {
                                    setBondData({
                                      ...bondData,
                                      searchQuery: element.item._id,
                                      bondId: element.item._id,
                                      searchedData: [],
                                    });
                                  }}
                                >
                                  {/* {element.item.nameOfIssuer +
                              " " +
                              ("     (" + element.item.issuerShortName + ")")} */}
                                  {element.item._id}
                                </Text>
                              </View>
                            )}
                          />
                        </ScrollView>
                      )}{" "}
                    </View>
                  )}

                  <TextInput
                    placeholder="Synopsis"
                    maxLength={1000}
                    multiline={true}
                    numberOfLines={6}
                    underlineColor="white"
                    selectionColor="#004433"
                    mode="outlined"
                    onChangeText={(val) =>
                      setArticleData({
                        ...articleData,
                        synopsis: val,
                      })
                    }
                    value={articleData.synopsis}
                  />

                  {selectedCategory.showHide ? null : (
                    <View
                      style={{ marginTop: 6, position: "relative", zIndex: 11 }}
                    >
                      <Text style={styles.text}>Similar Articles</Text>

                      <MultiSelect
                        options={allArticles.title}
                        value={selectedValue(selectedRecord.title, "title")}
                        onChange={(val: any) =>
                          setSelected({
                            ...selectedRecord,
                            title: val,
                          })
                        }
                        labelledBy={"Select"}
                        hasSelectAll={false}
                      />
                    </View>
                  )}

                  <View style={{ marginTop: 6, position: "relative", zIndex: 9 }}>
                    <Text style={styles.text}>Meta Data</Text>
                    <TextInput
                      placeholder="Meta Data"
                      underlineColor="white"
                      selectionColor="#004433"
                      mode="outlined"
                      onChangeText={(val) =>
                        setMetaDataResearchReport({
                          ...metaDataResearchDetails,
                          metaData: val,
                        })
                      }
                      value={metaDataResearchDetails.metaData}
                    />
                  </View>
                  {selectedCategory.showHide ? null : (
                    <View>
                      <View style={{ marginTop: 25, marginLeft: 5 }}>
                        <Text style={styles.text}>Upload Report Zip File</Text>
                        <input
                          type="file"
                          onChange={(e) => readFileDataAsBase64(e, "zip")}
                          accept=".zip,.ZIP"
                        />
                      </View>
                      <View style={{ marginTop: 15, marginLeft: 5 }}>
                        <Text style={styles.text}>Upload Article Icon</Text>
                        <input
                          type="file"
                          onChange={(e) => readFileDataAsBase64(e, "icon")}
                          accept=".png,.jpeg,.jpg,.PNG,.JPEG,.JPG"
                        />
                      </View>
                      <View style={{ marginTop: 15, marginLeft: 5 }}>
                        <Text style={styles.text}>Upload Author Image</Text>
                        <input
                          type="file"
                          onChange={(e) => readFileDataAsBase64(e, "author")}
                          accept=".png,.jpeg,.jpg,.PNG,.JPEG,.JPG"
                        />
                      </View>
                    </View>
                  )}

                  {/* </View> */}

                  {loader && <ActivityIndicator size="large" color="#00ff00" />}

                  <View
                    style={{
                      flexDirection: "row",
                      justifyContent: "space-evenly",
                      alignContent: "center",
                      marginBottom: "5%",
                    }}
                  >
                    {selectedCategory.showHide ? (
                      <Button
                        style={{
                          marginTop: "10px",
                          width: "30%",
                        }}
                        mode="contained"
                        color="#27AE60"
                        uppercase={false}
                        onPress={() => onSubmit("publish")}
                        contentStyle={{
                          height: "50px",
                        }}
                        disabled={
                          buttonDisabled.buttonDisable == true ||
                            // articleData.buttonDisable == true ||
                            articleData.title == "" ||
                            articleData.synopsis == "" ||
                            articleData.author == "" ||
                            articleData.metaData == "" ||
                            metaDataResearchDetails.metaData == "" ||
                            (articleData.categoryCheckbox == false &&
                              articleData.category == "") ||
                            (articleData.categoryCheckbox == true &&
                              articleData.newCategory == "")
                            ? true
                            : false
                        }
                      >
                        <Text
                          style={{
                            color: "white",
                            fontWeight: "bold",
                          }}
                        >
                          Publish
                      </Text>
                      </Button>
                    ) : (
                        <Button
                          style={{
                            marginTop: "10px",
                            width: "30%",
                          }}
                          mode="contained"
                          color="#27AE60"
                          uppercase={false}
                          onPress={() => onSubmit("preview")}
                          contentStyle={{
                            height: "50px",
                          }}
                          disabled={
                            buttonDisabled.buttonDisable == true ||
                              // articleData.buttonDisable == true ||
                              articleData.title == "" ||
                              articleData.synopsis == "" ||
                              articleData.author == "" ||
                              articleData.categoryDescription == "" ||
                              articleData.metaData == "" ||
                              metaDataResearchDetails.metaData == "" ||
                              articleData.docZip == "" ||
                              (articleData.categoryCheckbox == false &&
                                articleData.category == "") ||
                              (articleData.categoryCheckbox == true &&
                                (articleData.newCategory == "" ||
                                  articleData.categoryDescription == ""))
                              ? true
                              : false
                          }
                        >
                          <Text
                            style={{
                              color: "white",
                              fontWeight: "bold",
                            }}
                          >
                            Preview
                      </Text>
                        </Button>
                      )}
                  </View>
                </View>
              </Card.Content>
            </Card>

            {multiSelectSnackbar.visibilityDropDown && (
              <SnackBarFunction
                hideShow={{
                  visibilityDropDown: multiSelectSnackbar.visibilityDropDown,
                  message: multiSelectSnackbar.message,
                }}
              />
            )}

            {multiSelectSnackbar.visibilityDropDown1 && (
              <SnackBarFunction1
                hideShow={{
                  visibilityDropDown: multiSelectSnackbar.visibilityDropDown1,
                  message: multiSelectSnackbar.message1,
                }}
              />
            )}

            <Snackbar
              visible={visible}
              duration={3000}
              onDismiss={onDismissSnackBar}
              action={{
                label: "Ok",
                onPress: () => {
                  onDismissSnackBar;
                },
              }}
            >
              {articleData.message}
            </Snackbar>
          </View>
        )}

      <Footer />
    </View>
  );
};

//this is styleSheet
const styles = StyleSheet.create({
  container: {
    backgroundColor: "rgb(251, 252, 255)",
    alignItems: "center",
    justifyContent: "center",
  },
  card: {
    backgroundColor: "#FCFCFC",
    padding: "5vh",
    flexWrap: "wrap-reverse",
  },

  card_content: {
    // marginTop: 135,
    backgroundColor: "#fff",
    justifyContent: "center",
    textAlignVertical: "center",
    // height: "110vh",
    width: "130vh",
    verticalAlign: "center",
  },
  card_contentForMarketGuage: {
    backgroundColor: "#fff",
    justifyContent: "center",
    textAlignVertical: "center",
    // height: "110vh",
    width: "130vh",
    verticalAlign: "center",
  },
  titleText: {
    alignSelf: "center",
    color: "#393846",
    fontWeight: 600,
    fontSize: 26,
    fontFamily: "Inter"
  },
  breadcrumbTitle: {

    alignSelf: "center",
    color: "#75737C", fontWeight: 600, fontSize: "24px", fontFamily: "Inter"
  },
  text: {
    fontSize: 16,
    fontHeight: 56,
    fontFamily: 'Inter',
  },
  checkboxLabel: {
    fontSize: 16,
    fontHeight: 56,
    fontFamily: 'Inter',
    marginTop: 13,
    marginLeft: 5,
  },
  // button css
  button: {
    backgroundColor: "#F9FAFB",
    color: "black",
    marginTop: "10px",
    tintColor: "transperent",
  },
  buttonText: {
    height: "50px",
    color: "black",
    tintColor: "transparent",
  },
  orderSearchTextInput: {
    color: "#808C9A",
    fontSize: 20,
    lineHeight: 24,
    letterSpacing: -0.339623,
    width: "70%",
    paddingLeft: 10,
    borderColor: "#fff",
    borderWidth: 0.1,
  },
  orderitem: {
    width: "100%",
    height: "50px",
    backgroundColor: "#EFF2F5",
    padding: 10,
    marginVertical: 2,
    borderColor: "#000",
    borderWidth: 0.5,
  },
});


