import React from "react";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import axios from "axios";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import { Image, View, Text, StyleSheet, TouchableOpacity } from "react-native";
import { Button } from "react-native-paper";
import { env } from "../../../config/systemConfigs";
import { ListItem, ListItemText } from "@material-ui/core";

import { userContextDataChange } from "../../context/UserPrefContextProvider";
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      flexGrow: 1,
    },
  })
);



export default function menuAppBar(props: any) {
  const {
    userData,
    setUserData,
    apiToGetAuthenticatedData,
  } = userContextDataChange()!;

  React.useEffect(() => {
    useEffectForSync();
  }, []);

  var width = $(window).width();
  $(window).on('resize', function() {
    if ($(this).width() !== width) {
      width = $(this).width();
    }
  });


  async function useEffectForSync() {
    if (userData.userName == "" ) {
      await apiToGetAuthenticatedData();
    }
  }

  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  
  const handleMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = (name: any) => {
    setAnchorEl(null);
    if (name === "subscribe") {
      props.navigate.navigate("subscribe");
    } else if (name === "changePassword") {
      props.navigate.navigate("change-password");
    } else if (name === "signout") {
      removeData();
      window.location.href = env.FE_DOMAIN + "Login";
      // window.location.href = env.API_INFO_RECOM_MODULE;
    }
  };

  function getShortName() {
    if(userData.userFullName!="notlogin"&&userData.userFullName!=""){
      const shortN = userData.userFullName.split(" ");
      const fs = shortN[0].charAt(0);
      const sd = shortN[1].charAt(0);
      return fs+sd;
    }else{
      return "";
    }   
  }

  const removeData = async () => {
  
    try {
      axios({
        method: "GET",
        withCredentials: true,
        url: env.API_INFRA_MODULE + "/auth/logout",
      }).then((result: any) => {
        
      });
    } catch (error) {
      // Error saving data
      console.log(error.message);
    }
  };

  return (
    <View>
      <AppBar position="static" style={{ background: "#FFF", zIndex: -1 }}>
        <Toolbar
          style={{
            width: "93%",
            backgroundColor: "#FFF",
            flexDirection: "row",
            paddingRight: "2%",
            zIndex: -1,
          }}
        >
          <TouchableOpacity /*onPress={() => {window.location.href = `${env.API_INFO_RECOM_MODULE}`; }}*/ >
            <Image
              style={{
                width: "150px",
                height: "46px",
                resizeMode: "center",
                marginLeft: "1%",
              }}
              source={require("../../../assets/logo.svg")}
            />
          </TouchableOpacity>

          <View
            style={{
              width: "93%",
              flexDirection: "row",
              justifyContent: "flex-end",
              alignContent: "flex-end",
            }}
          >
            <Menu
              id="menu-appbar"
              anchorEl={anchorEl}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "right",
              }}
              keepMounted
              transformOrigin={{
                vertical: "bottom",
                horizontal: "left",
              }}
              open={open}
              onClose={handleClose}
            >
              <ListItem>
                <ListItemText
                  primary={userData.userFullName}
                  secondary={userData.userName}
                />
              </ListItem>
              <MenuItem onClick={() => handleClose("subscribe")}>
                Subscription
              </MenuItem>
              {userData.roleId !== 0 && userData.roleId === 1 ? (
                <MenuItem
                  onClick={() =>
                    (window.location.href =
                      env.API_INFO_RECOM_MODULE + "/admin")
                  }
                >
                  Admin
                </MenuItem>
              ) : null}

              <MenuItem onClick={() => handleClose("signout")}>
                Sign Out
              </MenuItem>
            </Menu>
            {/* <Button
              mode="text"
              color="white"
              uppercase={false}
              onPress={() =>
                (window.location.href = `${env.API_INFO_RECOM_MODULE}/training`)
              }
            >
              <Text style={styles.navTextOption}>Training</Text>
            </Button> */}

            {/* <Button
              mode="text"
              color="white"
              uppercase={false}
              onPress={() =>
                (window.location.href = `${env.API_INFO_RECOM_MODULE}/reports/Research`)
              }
            >
              <Text style={styles.navTextOption}>Research</Text>
            </Button> */}

            {/* <Button
              mode="text"
              color="white"
              uppercase={false}
              onPress={() =>
                (window.location.href = `${env.API_INFO_RECOM_MODULE}/reports/Knowledge`)
              }
            >
              <Text style={styles.navTextOption}>Knowledge</Text>
            </Button> */}

            <Button
              mode="text"
              onPress={
                () =>
                  props.navigate.reset({
                    index: 0,
                    routes: [
                      {
                        name: "marketwatch",
                      },
                    ],
                  })
                // props.navigate.navigate("marketwatch")
              }
              color="white"
              uppercase={false}
            >
              <Text style={styles.navTextOption}>MarketWatch</Text>
            </Button>

            <Button
              mode="text"
              uppercase={false}
              onPress={() => console.log("Pressed")}
              color="white"
            >
              <Text style={styles.dividerClass}>|</Text>
            </Button>

            {/* <Button
              mode="text"
              uppercase={false}
              onPress={() =>
                (window.location.href = `${
                  env.API_INFO_RECOM_MODULE
                }/reports/MarketData`)
              }
              color="white"
            >
              <Text style={styles.navTextOptionLight}>MarketData</Text>
            </Button> */}

            {/* <Button mode="text" uppercase={false} color="white">
              <Text style={styles.navTextOptionLight}>Events</Text>
            </Button> */}

            {userData.userName != "" ? (
              <View
                style={{
                  flexDirection: "row",
                  justifyContent: "flex-start",
                  alignContent: "flex-start",
                }}
              >
                <IconButton style={{ padding: 0 }} onClick={handleMenu}>
                  <View style={styles.circle}>
                    <Text style={styles.avetarText}>
                      {userData.userFullName != ""
                        ? getShortName()
                        : ""}
                    </Text>
                  </View>
                </IconButton>
              </View>
            ) : (
              <View style={{ flexDirection: "row" }}>
                <Button
                  mode="contained"
                  uppercase={false}
                  style={{
                    alignSelf: "flex-end",
                    backgroundColor: "white",
                    borderColor: "#31BC98",
                    borderWidth: 1.5,
                    borderBottomRightRadius: 0,
                    borderTopRightRadius: 0,
                  }}
                  onPress={() => {
                    props.navigate.navigate("login");
                  }}
                >
                  <Text style={{ color: "#31BC98" }}>
                    <b> Login</b>
                  </Text>
                </Button>
                <Button
                  mode="contained"
                  uppercase={false}
                  style={{
                    alignSelf: "flex-end",
                    backgroundColor: "#31BC98",
                    borderColor: "#31BC98",
                    borderWidth: 1.5,
                    borderBottomLeftRadius: 0,
                    borderTopLeftRadius: 0,
                    marginRight: 10,
                  }}
                  onPress={() =>
                    props.navigate.reset({
                      index: 0,
                      routes: [
                        {
                          name: "subscribe",
                        },
                      ],
                    })
                  }
                >
                  <b>Signup</b>
                </Button>
              </View>
            )}
          </View>
        </Toolbar>
      </AppBar>
    </View>
  );
}
const styles = StyleSheet.create({
  container: {
    width: "100%",
    flex: 1,
    backgroundColor: "#F2F2F2",
    alignItems: "center",
    justifyContent: "center",
  },
  navTextOption: {
    color: "#383E56",
    fontWeight: "600",
    letterSpacing: -0.30566,
    lineHeight: 22,
    fontFamily: "Inter",
    fontStyle: "normal",
    fontSize: 16,
  },
  dividerClass: {
    color: "#383E56",
    fontWeight: "600",
    letterSpacing: -0.30566,
    lineHeight: 22,
    fontFamily: "Inter",
    fontStyle: "normal",
    fontSize: 10,
  },
  navTextOptionLight: {
    color: "#383E56",
    fontWeight: "normal",
    letterSpacing: -0.30566,
    lineHeight: 22,
    fontFamily: "Inter",
    fontStyle: "normal",
    fontSize: 16,
  },
  card: {
    marginTop: 10,
    backgroundColor: "#FCFCFC",
    padding: "5vh",
  },
  card_content: {
    backgroundColor: "#fff",
    justifyContent: "center",
    textAlignVertical: "center",
    height: "60vh",
    width: "90%",
    verticalAlign: "center",
  },
  titleText: {
    fontSize: 20,
    fontWeight: "bold",
    width: "90%",
    textAlign: "center",
  },
  textInput: {
    marginTop: 10,
    fontWeight: 500,
    fontSize: "22px",
    height: "50px",
  },

  // button css
  button: {
    backgroundColor: "#F9FAFB",
    color: "black",
    marginTop: "10px",
    tintColor: "transperent",
  },
  buttonText: {
    height: "50px",
    color: "black",
    tintColor: "transparent",
  },
  circle: {
    width: 40,
    height: 40,
    borderRadius: 100 / 2,
    borderColor: "#DCE1FF",
    borderWidth: 1.5,
    alignSelf: "flex-end",
    alignContent: "center",
    justifyContent: "center",
  },
  avetarText: {
    fontSize: 15,
    fontWeight: "bold",
    color: "#5C6AC4",
    textAlign: "center",
  },
  nameToolbar: {
    color: "#383E56",
    fontWeight: "600",
    letterSpacing: -0.30566,
    lineHeight: 22,
    fontFamily: "Inter",
    fontStyle: "normal",
    marginLeft: 5,
    fontSize: 18,
    marginTop: 10,
  },
});
