import React from "react";
import {
  View,
  StyleSheet,
  Dimensions,
  Text,
  Image,
  TouchableOpacity,
  ScrollView,
  FlatList,
  ActivityIndicator,
} from "react-native";
import { Button } from "react-native-paper";
import {
  widthPercentageToDP as wp,
  heightPercentageToDP as hp,
} from "react-native-responsive-screen";
import axios from "axios";
import { WatchListComponent } from "./child-component/WatchListComponent";
import { BondSearchBar } from "./child-component/BondSearchBar";
import { CreateNewOrderModal } from "../orderbook/child-component/CreateNewOrderModal";
import { FilterComponent } from "./child-component/FilterComponent";
const { width, height } = Dimensions.get("window");
import { env } from "../../../config/systemConfigs";
import BondGraph from "./child-component/BondGraph.web";
import MenuAppBar from "../../../shared/component/appBar/header";
import AlertDialog from "../../../shared/component/dialog/alertDialog";
import Footer from "../../../shared/component/appBar/footer";
import CategorySelectionList from "../../../shared/component/ListComponent/CategorySelectionList";
import BondCardView from "../../../shared/component/ListComponent/CardViewComponent";
import { TableRowComponent } from "../../../shared/component/ListComponent/TableRowComponent";
import UserPrefContext from "../../../shared/context/UserPrefContextProvider";

//inteface for component props
interface MarketWatchProps {
  navigation: any;
}
//inteface for component state
interface MarketWatchState {}

export default class MarketWatch extends React.Component<
  MarketWatchProps,
  MarketWatchState
> {
  orderRef = React.createRef();
  //to get context
  static contextType = UserPrefContext;
  static dataDict: {[id: string] : any; } = {};

  state = {
    items: [] as any[],
    watchListItems: [] as any[],
    showFilter: false,
    itemView: "Card",
    tab: "B",           //b - bonds, p - portfolio, s - specialProduct, e - ExchangeTraded
    loader: true,
    orderItem: null,
    showModal: false,
  };

  constructor(props: any) {
    super(props);
    MarketWatch.dataDict["B"]  = {items: [], categories: [], selectedCategories: [], watchList: []}; 
    MarketWatch.dataDict["S"]  =  {items: [], categories: [], selectedCategories: [], watchList: []};
    MarketWatch.dataDict["P"]  =  {items: [], categories: [], selectedCategories: [], watchList: []};
    MarketWatch.dataDict["E"]  =  {items: [], categories: [], selectedCategories: [], watchList: []};
  }

  //lazy loading of items to be shown on the page
  updateDictItems(categories: string[], tab: string){
      let query =  { type: tab, selectedCategory: categories, pageFlag: "" };
      console.log(env.API_INFRA_MODULE)
      axios({
        method: "POST",
        data: query,
        withCredentials: true,
        url: env.API_INFRA_MODULE + "/bondManagement/getAllBonds",
      })
      .then((result) => {
        if (result.status === 200) {
          MarketWatch.dataDict[this.state.tab]["items"] = result.data;
          this.setState({items: MarketWatch.dataDict[this.state.tab]["items"], loader: false});
          console.log(result)
        }
      })
      .catch((err) => {
        alert("Oops! Something went wrong. Please try again. A");
        console.log("Error console:", err);
      }); 
  }

  updateDictCategories(){
    if(MarketWatch.dataDict[this.state.tab]["categories"].length === 0){
      axios({
        method: "GET",
        withCredentials: true,
        url: env.API_INFRA_MODULE + `/bondManagement/getCategoryListByProductType?type=${this.state.tab}`,
      })
      .then((result) => {
        if(result.status === 200){
          MarketWatch.dataDict[this.state.tab]["categories"] = result.data;
          //Since we got new list of categories, let's do a default selection first
          this.onCategoryChange([result.data[0]]);
        }
      })
      .catch((err) => {
        alert("Oops! Something went wrong. Please try again. C");
        console.log("Error console:", err);
      });  
    }
  }

  updateDictWatchList(){
    if(MarketWatch.dataDict[this.state.tab]["watchList"].length === 0){
      axios({
        method: "GET",
        withCredentials: true,
        url: env.API_INFRA_MODULE + `/bondManagement/getMyWatchlist?type=${this.state.tab}`,
      })
      .then((result) => {
        if (result.status === 200) {
          MarketWatch.dataDict[this.state.tab]["watchList"] = result.data;
          this.setState({watchListItems: MarketWatch.dataDict[this.state.tab]["watchList"],});
        }
      })
      .catch((err) => {
        alert("Oops! Something went wrong. Please try again. B")
        console.log("Error console:", err);
      }); 
    }
  }

  async componentDidMount() {
    //if page is refresh or context is empty, so then it can be set in context
    if (this.context.userData.userName == "") {
      await this.context.apiToGetAuthenticatedData();
    }
    // update the categories. Items to display will be queried once category list is available
    if(this.context.userData.userName != ""){
      this.updateDictCategories();
      this.updateDictWatchList();  
    }
  }

  showOrderModal(orderItem: any) {
    this.setState({orderItem: orderItem, showModal: true});
  }

  searchResults = (resultItems: any[]) => {
    if(resultItems.length > 0){
      this.setState({ items: resultItems});
    }
    else{
      this.setState({items: MarketWatch.dataDict[this.state.tab]["items"]});
    }
  }

  isWatchListItem = (bond: any) => {
    return (this.state.watchListItems.findIndex( (x: any) => x._id === bond._id ) >= 0);
  }

  //if requested bond is in the watchlist then remove else add
  manageWatchList = (bond: any) => {
    if(this.context.userData.userName != null){
      let index = MarketWatch.dataDict[this.state.tab]["watchList"].findIndex( (x: any) => x._id === bond._id );

      if(index < 0){
        axios({
          method: "POST",
          data: { myWatchList: bond._id, type: bond.specialProduct},
          withCredentials: true,
          url: env.API_INFRA_MODULE + "/userManagement/addToMyWatchList",
        })
        .then((result) => {
          MarketWatch.dataDict[this.state.tab]["watchList"].push(bond);
          this.setState({watchListItems: MarketWatch.dataDict[this.state.tab]["watchList"]}); 
        })
        .catch((err) => {
          alert("Oops! Something went wrong. Please try again. D")
          console.log("Error console:", err);
        });
      }
      else{   
        axios({
          method: "POST",
          data: {myWatchList: bond._id, type: bond.specialProduct},
          withCredentials: true,
          url: env.API_INFRA_MODULE + "/userManagement/removeFromMyWatchList",
        })
          .then(() => {
            let newWatchList = JSON.parse(JSON.stringify(this.state.watchListItems)).filter((_: any, i: number) => i !== index);
            MarketWatch.dataDict[this.state.tab]["watchList"] = newWatchList;
            this.setState({watchListItems: newWatchList, });
          })
          .catch((err) => {
            alert("Oops! Something went wrong. Please try again. E")
            console.log("Error console:", err);
          });   
      }
    }
  }

  hideFilterComponunt() {
    this.setState({ showFilter: !this.state.showFilter, });
  }

  //VK: This should only filter from what is shown already and not go to server to fetch new results
  getFilterBonds(filterobj: any) {
    axios({
      method: "POST",
      data: filterobj,
      withCredentials: true,
      url: env.API_INFRA_MODULE + "/bondManagement/getFilteredBonds",
    })
      .then((result) => {
        this.setState({ items: result.data.result, });
      })
      .catch((err) => {
        alert("Oops! Something went wrong. Please try again. F")
        console.log("Error console:", err);
      });
  }

  showTab(selected: any) {
    if(this.state.tab !== selected){
      this.setState({tab: selected});
      // if items for selected tab aren't loaded yet, load those items & show
      if(MarketWatch.dataDict[selected]["items"].length == 0){
        this.updateDictItems(MarketWatch.dataDict[selected]["selectedCategories"], selected);
      }
      else{
        this.setState({items: MarketWatch.dataDict[selected]["items"]});
      }
    }
  }

  onCategoryChange(selectedCategories: any) {
    // ideally selectedCategories will be non-zero but putting this condition just to avoid any unexpected behaviour
    if((selectedCategories != null) && (selectedCategories.length > 0)){
      MarketWatch.dataDict[this.state.tab]["selectedCategories"] = selectedCategories;
      this.updateDictItems(selectedCategories, this.state.tab);  
    }
  }

  render() {
    let col = 1;
    if (width >= 1024) {
      col = 3;
    } else if (width >= 500) {
      col = 2;
    }
    
    return (
      <View style={{ height: height, width: width }}>
        <ScrollView style={{ backgroundColor: "#FBFCFF", height: height }}>
          <MenuAppBar navigate={this.props.navigation} />
          <View style={{ backgroundColor: "#FBFCFF", paddingLeft: "8%", paddingRight: "8%", }} >
            <View style={{ flex: 1, paddingTop: "1%", flexDirection: "row", justifyContent: "space-between", }} >
                <Text style={{fontWeight: "bold", fontSize: 26, alignSelf: "flex-start", }} > Market Watch </Text>
                <Button mode="contained" uppercase={false} style={{ alignSelf: "flex-end", backgroundColor: "#27AE60", }}
                  onPress={() => {this.props.navigation.reset({ index: 0, routes: [ { name: "orderbook", }, ], });}}
                >
                  Order Book
                </Button>
            </View>

            <View style={styles.container}>
                <View style={styles.first_section}>
                  <View style={ this.state.tab === "B" ? [styles.first_tab, styles.active_tab] : styles.first_tab } >
                    <Text 
                      style={ this.state.tab === "B" ? { color: "#5C6AC4" } : { color: "#637381", fontFamily: "inter", fontStyle: "normal", }}
                      onPress={() => this.showTab("B")}
                    >
                      Bonds
                    </Text>
                  </View>
                  <View style={{ margin: "20px", borderRightWidth: 1, borderRightColor: "#DFE3E8", }} />
                  <View style={ this.state.tab === "S" ? [styles.first_tab, styles.active_tab] : styles.first_tab } >
                    <Text
                      style={ this.state.tab === "S" ? { color: "#5C6AC4" } : { color: "#637381" } }
                      onPress={() => this.showTab("S")}
                    >
                      Special Products
                    </Text>
                  </View>
                  <View style={{ margin: "20px", borderRightWidth: 1, borderRightColor: "#DFE3E8", }} />
                  <View style={ this.state.tab === "P" ? [styles.first_tab, styles.active_tab] : styles.first_tab } >
                    <Text
                      style={ this.state.tab === "P" ? { color: "#5C6AC4" } : { color: "#637381" } }
                      onPress={() => this.showTab("P")}
                    >
                      Factor Portfolio
                    </Text>
                  </View>
                </View>

                <View style={styles.toggle_tabs}>
                  <TouchableOpacity onPress={() => this.setState({itemView: "Card"})} >
                    <Image
                      style={{ height: "27px", width: "27px", marginLeft: "5px", alignSelf: "center", }}
                      source={ this.state.itemView == "Card" ? require("../../../assets/Groupcardselected.svg") : require("../../../assets/Groupcardnotselected.svg") }
                    />
                  </TouchableOpacity>
                  <TouchableOpacity onPress={() => this.setState({itemView: "Row"})} >
                    <Image
                      style={{ height: "27px", width: "27px", marginLeft: "5px", alignSelf: "center", }}
                      source={
                        this.state.itemView == "Row" ? require("../../../assets/Grouprowselected.svg") : require("../../../assets/Grouprownotselected1.svg") }
                    />
                  </TouchableOpacity>
                  {this.state.tab != "B" ? null : (
                    <TouchableOpacity onPress={() => this.setState({itemView: "Graph"})} >
                      <Image
                        style={{height: "27px", width: "27px", marginLeft: "5px", alignSelf: "center", }}
                        source={
                          this.state.itemView === "Graph" ? require("../../../assets/Groupgraphselected.svg") : require("../../../assets/Groupgraphnotselected.svg") }
                      />
                    </TouchableOpacity>
                  )}
                </View>
            </View>

            {this.state.tab === "B" ? ( //Show search bar only for Bonds
              <View style={{ flexDirection: "row" }}>
                <View style={styles.searchStyle}>
                  <BondSearchBar onParentGetItem={this.searchResults} context={this.state.tab}/>
                </View>
              </View>
            ) : null}
            {this.state.showFilter ? ( <FilterComponent sendFilterJsontoParent={this.getFilterBonds.bind(this)} /> ) : null}
          </View>

          {(!this.state.loader && this.context.userData.roleId === -1) ?  (
            <AlertDialog
              open={true}
              navigation={this.props.navigation}
              title={""}
              message={ "You need to Sign In to view details. Click here to Sign In." }
              bttonText={"Sign In"}
            />
          ) : (!this.state.loader && !this.context.userData.isValidSubscription) ? (
            <AlertDialog
              open={true}
              navigation={this.props.navigation}
              title={""}
              message={ "Your subscription has expired, kindly renew your subscription." }
              bttonText={"Subscribe"}
            />
          ) : (
            <>
                <View style={{ marginTop: "5%", marginLeft: "8%", marginRight: "8%", zIndex: -1, }} >
                  {this.state.itemView == "Graph" ? null : (
                    <WatchListComponent
                      watchList={this.state.watchListItems}
                      navigation={this.props.navigation}
                      view={this.state.itemView}
                      showOrderModal={this.showOrderModal.bind(this)}
                      manageWatchList={this.manageWatchList.bind(this)}
                    />
                  )}
                  <CategorySelectionList 
                    categories={MarketWatch.dataDict[this.state.tab]["categories"]}
                    selectedCategories = {MarketWatch.dataDict[this.state.tab]["selectedCategories"]} 
                    onCategoryChange={this.onCategoryChange.bind(this)}
                  />
                </View>
              {this.state.loader && <ActivityIndicator size="large" color="#00ff00" />}
              {(this.state.items.length == 0 && !this.state.loader ) ? (
                <View style={{ width: width, justifyContent: "center", alignItems: "center", }} >
                  <Text style={{ fontSize: 20 }}>
                    Sorry we couldnt find the information you are looking for. If you need it please contact Ketan
                    Verma(ketan.verma@zephyr.org.in).
                  </Text>
                </View>
              ) : (
                <View style={{ zIndex: -1 }}>
                  {this.state.itemView == "Graph" ? (
                    <BondGraph
                      manageWatchList={this.manageWatchList.bind(this)}
                      isWatchListItem={this.isWatchListItem}
                      showOrderModal={this.showOrderModal.bind(this)}
                      navigation={this.props.navigation}
                      bondListWithSection={this.state.items}
                    />
                  ) : null}
                  {this.state.itemView == "Card" ? (
                    <View style={{ flexDirection: "column" }}>
                      <FlatList
                        data={this.state.items}
                        contentContainerStyle={{ alignContent: "center", paddingLeft: "8%", paddingHorizontal: "8%", }}
                        renderItem={(bond: any) => (
                          <BondCardView
                          bondObject={bond.item}
                          navigation={this.props.navigation}
                          manageWatchList={this.manageWatchList.bind(this)}
                          isWatchListItem={this.isWatchListItem}
                          showOrderModal={this.showOrderModal.bind( this )}
                        />
                        )}
                        numColumns={col}
                        key={col}
                      />
                    </View>
                  ) : null}
                  {this.state.itemView == "Row" ? (
                    <View style={{ marginLeft: "8%", marginRight: "8%" }}>
                      <FlatList
                        data={this.state.items}
                        contentContainerStyle={{ paddingRight: 2, paddingLeft: 2, }}
                        renderItem={(bond: any) => (
                          <TableRowComponent
                            bondObject={bond.item}
                            navigation={this.props.navigation}
                            manageWatchList={this.manageWatchList.bind(this)}
                            isWatchListItem={this.isWatchListItem}
                            showOrderModal={this.showOrderModal.bind( this )}
                          />
                        )}
                      />
                    </View>
                  ) : null}
                </View>
              )} 
            </>
          )}
          <Footer />
        </ScrollView>
        <CreateNewOrderModal
          BondToOrder={this.state.orderItem}
          OrderToEdit={null}
          showModal={this.state.showModal}
          hideModal={() => {this.setState({showModal: false})}}
        />
      </View>
    );
  }
}

