import React from "react";
import {
  StyleSheet,
} from "react-native";

import { NavigationContainer } from "@react-navigation/native";
import { createStackNavigator } from "@react-navigation/stack";

import { LoginScreen } from "./views/user-management/LoginScreen";
import { Registration } from "./views/user-management/RegistrationScreen";
import { ForgotPassword } from "./views/user-management/ForgotPassword";
import MarketWatch from "./views/bond-management/marketwatch/MarketWatch";
import { OrderBook } from "./views/bond-management/orderbook/OrderBook";
import Membership from "./views/user-management/Membership";
import BondDetails from "./views/bond-management/marketwatch/child-component/BondDetails";
import FactorPortfolioDetails from "./views/bond-management/marketwatch/child-component/FactorPortfolioDetails";
import { CookiesProvider } from "react-cookie";
import { SaveArticle } from "./views/admin-management/SaveArticle.web";
import { env } from "./config/systemConfigs";
import { Logout } from "./shared/component/services/Logout";
import { preViewComponent } from "./views/admin-management/previewComponent";
import { PassordToAuth } from "./views/user-management/PwdToAuthentication";
import { ChangePasswordToAuth } from "./views/user-management/NewPasswordExistsUser";
import { NotFoundPage } from "./shared/component/services/NotFoundPage";
import { UserVerification } from "./views/user-management/UserVerification";
import IntrestedUser from "./views/bond-management/orderbook/child-component/InterestedUserView"

import { UserPrefContextProvider } from './shared/context/UserPrefContextProvider';
const Stack = createStackNavigator();
const linking = {
  prefixes: [],
  enabled: true,
};

export function App() {
  var getUrlSegments: any
  getUrlSegments = window.location.href;

  return (

    <CookiesProvider>

      {/* context */}
      
       <UserPrefContextProvider>
        <NavigationContainer linking={linking}>
          {/* //<MenuAppBar /> */}
          <Stack.Navigator>


            {getUrlSegments != env.FE_DOMAIN &&
              <Stack.Screen
                name="pagenotfound"
                component={NotFoundPage}
                options={{ headerShown: false, title: "INRBonds - Not Found" }}
              />}

            <Stack.Screen
              name="login"
              component={LoginScreen}
              options={{ headerShown: false, title: "INRBonds - login" }}
            />
            <Stack.Screen
              name="pwd"
              component={PassordToAuth}
              options={{ headerShown: false }}
            />
            <Stack.Screen
              name="verification"
              component={UserVerification}
              options={{ headerShown: false, title: "INRBonds - Verification" }}
            />
            <Stack.Screen
              name="npwd"
              component={ChangePasswordToAuth}
              options={{ headerShown: false }}
            />
            <Stack.Screen
              name="registration"
              component={Registration}
              options={{ headerShown: false, title: "INRBonds - Registration" }}
            />
            <Stack.Screen
              name="forgot-password"
              component={ForgotPassword}
              options={{ headerShown: false }}
            />
            <Stack.Screen
              name="marketwatch"
              component={MarketWatch}
              options={{ headerShown: false, title: "INRBonds - Marketwatch" }}
            />
            <Stack.Screen
              name="orderbook"
              component={OrderBook}
              options={{ headerShown: false, title: "INRBonds - Orderbook" }}
            />
            <Stack.Screen
              name="subscribe"
              component={Membership}
              options={{
                headerShown: false,
                title: "INRBonds - Subscribe",
                headerTitleStyle: {
                  textAlign: "left",
                  flex: 1,
                },
              }}
            />

            <Stack.Screen
              name="bonddetails"
              component={BondDetails}
              options={{
                headerShown: false
              }}
            />

            <Stack.Screen
              name="intresteduser"
              component={IntrestedUser}
              options={{
                headerShown: false
              }}
            />
            <Stack.Screen
              name="FactorPortfolioDetails"
              component={FactorPortfolioDetails}
              options={{
                headerShown: false,
              }}
            />
            <Stack.Screen
              name="save-article"
              component={SaveArticle}
              options={{ headerShown: false, title: "INRBonds - Save Article", }}
            />
            <Stack.Screen
              name="logout"
              component={Logout}
              options={{ headerShown: false }}
            />
            <Stack.Screen
              name="preview"
              component={preViewComponent}
              options={{ headerShown: false }}
            />
          </Stack.Navigator>

        </NavigationContainer>
      </UserPrefContextProvider>
    
    </CookiesProvider>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#F2F2F2",
    alignItems: "center",
    justifyContent: "center",
  },
  navTextOption: {
    color: "#383E56",
    fontWeight: "600",
    letterSpacing: -0.30566,
    lineHeight: 22,
  },
  card: {
    marginTop: 10,
    backgroundColor: "#FCFCFC",
    padding: "5vh",
  },
  card_content: {
    backgroundColor: "#fff",
    justifyContent: "center",
    textAlignVertical: "center",
    height: "60vh",
    width: "50vh",
    verticalAlign: "center",
  },
  titleText: {
    fontSize: 20,
    fontWeight: "bold",
    width: "100%",
    textAlign: "center",
  },
  textInput: {
    marginTop: 10,
    fontWeight: 500,
    fontSize: "22px",
    height: "50px",
  },

  // button css
  button: {
    backgroundColor: "#F9FAFB",
    color: "black",
    marginTop: "10px",
    tintColor: "transperent",
  },
  buttonText: {
    height: "50px",
    color: "black",
    tintColor: "transparent",
  },
});
declare var global: any;
