import { View } from "react-native";
import React, { useState } from "react";
import { Snackbar } from "react-native-paper";

var abc;
var xyz;

export function SnackBarFunction(props: any) {
	abc = props.hideShow.visibilityDropDown;
	// alert(abc);
	const [visible1, setVisible1] = React.useState(
		props.hideShow.visibilityDropDown ? true : false,
	);

	const onDismissSnackBar1 = () => {
		setVisible1(false);
	};
	return (
		<>
			<Snackbar
				visible={visible1}
				duration={3000}
				onDismiss={onDismissSnackBar1}
				action={{
					label: "Ok",
					onPress: () => {
						onDismissSnackBar1;
					},
				}}>
				{props.hideShow.message}
			</Snackbar>
		</>
	);
}

