import React, { Component, useState } from "react";
import { View, StyleSheet, Platform, Image } from "react-native";
import { TextInput, Button, Card, Text, Snackbar } from "react-native-paper";
import axios from "axios";
import { env } from "../../config/systemConfigs";
import {
  TextField,
  withStyles,
  makeStyles,
  createStyles,
  Theme,
  ThemeProvider,
} from "@material-ui/core";
//this is ChangeUserPassword Function component
export const ForgotPassword = ({ navigation }: any) => {
	//this is visibitity state of snackbar
	const [visible, setVisible] = useState(false);
	const [disabling, setDisabled] = useState(false);
	//this is userName state
	const [userName, setUserName] = useState("");
	//storing the user enter monile no or not boolean value
	let isMobileNo: boolean = false;
	//use for setting the message
	const [message, setMassage] = useState("");
	//show snackbar with message function.
	const showMassage = (msg: string) => {
		setMassage(msg);
		onToggleSnackBar();
	};
	//this is snackbar visibility show and hide function.
	const onToggleSnackBar = () => setVisible(!visible);
	const onDismissSnackBar = () => setVisible(false);


	const onEnterPress = (event: any) => {
		// console.log(JSON.stringify(event.nativeEvent.key));
		if (event.keyCode == 13) {
			onSubmit();
		}
	  };


	//createing json object and send to server
	const onSubmit = () => {
		if (!userName) {
			showMassage("Enter valid email address.");
			return false;
		}
		setDisabled(true)
		axios
			.post(`${env.API_INFRA_MODULE}/auth/forgotPassword`, {
				userName: userName,
				isMobileNo: isMobileNo,
			})
			.then((result) => {
				setDisabled(false)
				if(result.data.status =="200"){
					showMassage(result.data.message);
					setTimeout(() => {
						navigation.navigate("login")
					}, 4000);
				}else{
					showMassage(result.data.message);
			
				}

				
			})
			.catch((err) => {
				console.log("Error console:", err);
			});
	};
	return (
		<View style={styles.container}>
			<Card style={styles.card}>
				{/* <Text style={styles.titleText}>Forgot Password</Text> */}
				<Card.Content style={styles.card_content}>
				<Image
					style={{
						width: "60%",
						height: "25%",
						resizeMode: "center",
						marginLeft: "22%",
						// marginBottom:"5%"
					}}
					source={require("../../assets/icons/INRBond-logo.png")}
					/>
					<Text style={styles.titleText}>Forgot Password</Text>
					<View>
					<TextField
						// className={classes.margin}
						// error={onValidateEmail}
						style={{ marginTop: 20 }}
						label="Email"
						type="Email"
						variant="outlined"
						// helperText={
						// onValidateEmail ? "Email should be minimum 6 characters." : ""
						// }
						// onFocus={() => setonValidateEmail(false)}
						onChange={(event) => setUserName(event.target.value)}
						onKeyDown={(event) => onEnterPress(event)}
					/>
						<Button
							style={{ marginTop: "10px" }}
							mode="contained"
							color="#27AE60"
							uppercase={false}
							contentStyle={{ height: "50px" }}
							onPress={() => onSubmit()}
							disabled={userName == "" || disabling==true }>
							<Text style={{ color: "white", fontWeight: "bold" }}>Submit</Text>
						</Button>
					</View>
					<View
						style={{
							marginTop: 15,
							// margin: 5,
							flexDirection: "row",
							alignContent: "center",
							justifyContent: "center",
						}}>
						{/* <Text
							style={{
								width: "132px",
								height: "19px",
								fontWeight: "500",
								fontSize: 16,
								lineHeight: 19,
								letterSpacing: -0.373585,
								alignSelf: "flex-end",
								marginRight: "15%",
								color: "blue",
							}}
							onPress={() => navigation.navigate("Login")}>
							Login
						</Text> */}
						<Text
							style={{ fontSize: 18, color: "#5C6AC4",fontWeight: "500",
							alignSelf: "center",
								// marginRight: "15%",
						 }}
							onPress={() => {
								navigation.navigate("login");
							}}
							>Login
							</Text>
					</View>
				</Card.Content>
			</Card>
			<Snackbar
				visible={visible}
				duration={3000}
				onDismiss={onDismissSnackBar}
				action={{
					label: "Ok",
					onPress: () => {
						onDismissSnackBar;
					},
				}}>
				{message}
			</Snackbar>
		</View>
	);
};
//this is styleSheet
const styles = StyleSheet.create({
	container: {
		flex: 1,
		backgroundColor: "#F2F2F2",
		alignItems: "center",
		justifyContent: "center",
	},
	card: {
		marginTop: 10,
		backgroundColor: "#FCFCFC",
		padding: "2vh",
		elevation: 10,
	},
	card_content: {
		backgroundColor: "#fff",
		justifyContent: "center",
		textAlignVertical: "center",
		height: "70vh",
		width: "50vh",
		verticalAlign: "center",
	},
	titleText: {
		fontSize: 20,
		marginTop: 20,
		marginBottom: 20,
		fontWeight: "bold",
		alignSelf: "center",
	},
	textInput: {
		marginTop: 10,
		fontWeight: 500,
		fontSize: 22,
		height: 50,
	},
	// button css
	button: {
		backgroundColor: "#F9FAFB",
		color: "black",
		marginTop: "10px",
		tintColor: "transperent",
	},
	buttonText: {
		height: "50px",
		color: "black",
		tintColor: "transparent",
	},
});