import React from "react";
import {
  View,
  Text,
  TextInput,
  StyleSheet,
  
} from "react-native";
import { Modal, Button, Card, Title } from "react-native-paper";
import { BondSearchBar } from "../../marketwatch/child-component/BondSearchBar";
import axios from "axios";
const bondCalculator = require("bond-calculator");
import { env } from "../../../../config/systemConfigs";
import UserPrefContext from "../../../../shared/context/UserPrefContextProvider";
import {dateDiffInYrs, formatMaturityDate, formatCoupon} from "../../../../shared/services/utilServices";
import Moment from "moment";

interface CreateNewOrderModalProps {
  BondToOrder: any;
  OrderToEdit: any;
  showModal: boolean;
  hideModal: any;
}
interface CreateNewOrderModalState {}

export class CreateNewOrderModal extends React.Component<
  CreateNewOrderModalProps,
  CreateNewOrderModalState
> {

  static contextType = UserPrefContext;

  state = {
    bondDetails: null, //Underlying bond details of the order
    orderQty: "",
    orderAmt: "",
    quotedYTM: "",
    quotedPrice: "",
    isBuy: true,
    orderId: "",  // if true then new order, otherwise edit order

    invalidQuantity: false,
    invalidYTM: false,
    invalidPrice: false,
    invalidAmount: false,
  };

  constructor(props: CreateNewOrderModalProps) {
    super(props);
  }

  //if props change then state will be updated
  componentDidUpdate(prevProps: any){
    if(this.props.BondToOrder != null){
      if((prevProps.BondToOrder != this.props.BondToOrder) || (this.state.bondDetails == null)){
        this.setState({bondDetails: this.props.BondToOrder})
      }
    }
    else if(this.props.OrderToEdit != null){
      if(prevProps.OrderToEdit != this.props.OrderToEdit){
        let editBond = {
          "faceValue": this.props.OrderToEdit.faceValue,
          "Head": {
            "BondName": this.props.OrderToEdit.issuerShortName,
            "DateOfMaturity": this.props.OrderToEdit.dateOfMaturity,
            "Coupon": this.props.OrderToEdit.couponRate,
          },     
          "frequencyOfInterestPayment": this.props.OrderToEdit.frequencyOfInterestPayment,
          "callDate": this.props.OrderToEdit.callDate,
        }
        this.setState({
          bondDetails: editBond,       
          orderId: this.props.OrderToEdit._id,
          orderAmt: this.props.OrderToEdit.amount,
          orderQty: this.props.OrderToEdit.quantity,
          quotedYTM: this.props.OrderToEdit.yield,
          quotedPrice: this.props.OrderToEdit.price,
          isBuy: this.props.OrderToEdit.isBuy,
        });  
      }
    }
  }

  async componentDidMount() {
    //if page is refresh or context is empty, so then it can be set in context
    if (this.context.userData.userName == "") {
      await this.context.apiToGetAuthenticatedData();
    }
  }

  BondFromSearchBar(bonds: any[]) {
    if(bonds.length == 1){
      this.setState({bondDetails: bonds[0], });
    }
  }

  close() {
    this.setState({
      bondDetails: null,
      orderId: "",
      showModal: false,
      orderQty: "",
      orderAmt: "",
      quotedYTM: "",
      quotedPrice: "",
      isBuy: true,
      invalidQuantity: false,
      invalidYTM: false,
      invalidPrice: false,
      invalidAmount: false,
    });
    this.props.hideModal();
  }

  submitOrder() {
    if(this.state.bondDetails != null){
      if(this.state.orderAmt === ""){
        this.setState({invalidAmount: true, });
      }
      else if(this.state.quotedYTM === ""){
        this.setState({invalidYTM: true,});
      }
      else{
        if(this.state.orderId === ""){    //populate the order and sent to server
          let orderObject = {
            "isinNo": this.state.bondDetails.isinNo,
            "yield": this.state.quotedYTM,
            "price": this.state.quotedPrice,
            "quantity": this.state.orderQty,
            "isActive": true,
            "isBuy": this.state.isBuy,
            "amount": this.state.orderAmt,
            "userName": this.context.userData.userName,
            "orderDate": Date.now()
          }
          axios({
            method: "POST",
            data: orderObject,
            withCredentials: true,
            url: env.API_TRADING_MODULE + "/tradingManagement/saveOrder",
          })
          .then((result) => {
            this.close();
          })
          .catch((err) => {
            alert("Oops, something went wrong. Please try again!")
            console.log("Error console:", err);
          });       
        }
        else{
          axios({
            method: "POST",
            data: {
              id: this.state.orderId,
              quantity: this.state.orderQty,
              amount: this.state.orderAmt,
              price: this.state.quotedPrice,
              yield: this.state.quotedYTM,
            },
            withCredentials: true,
            url: env.API_TRADING_MODULE + "/tradingManagement/updateOrder",
          })
          .then((result) => {
            alert("Sucessfully modified order.")
            this.close();
          })
          .catch((err) => {
            alert("Oops, something went wrong. Please try again!")
          });            
        }
      }
    }
  }

  calculate(value: string, from: string) {
    if(this.state.bondDetails != null){
      Moment.locale("en");

      let yld = "", price = "", amount = "", quantity = "", calc = null, currentDate = new Date();
      let rate = this.state.bondDetails.Head.Coupon? this.state.bondDetails.Head.Coupon / 100 : 0;
      let maturitydate = Moment(this.state.bondDetails.Head.DateOfMaturity).format("yyyy-MM-DD");
      //poor way to check for perp
      if(new Date(maturitydate) >= new Date("2121/12/31")){
        maturitydate = Moment(this.state.bondDetails.callDate).format("yyyy-MM-DD");
      }
      let yrdiff = dateDiffInYrs(maturitydate, currentDate);
      if (rate > 0 && yrdiff > 0 && this.state.bondDetails.frequencyOfInterestPayment){
        calc = bondCalculator({
          settlement: currentDate, //always settlement date is current Date
          maturity: maturitydate, //maturity date.
          rate: rate, // e.g. here is the actual value 2.625% we
          redemption: 100, //this.props.BuyOrSaleBondDetails.faceValue
          frequency: this.state.bondDetails.frequencyOfInterestPayment, // 1=Annual, 2=Semiannual, 4=Quarterly
          convention: "ACTUAL/ACTUAL",
        });
      }
      if(from === "Quantity"){
        if(this.state.quotedPrice !== ""){
          amount = (parseFloat(this.state.quotedPrice) * parseInt(value) * (this.state.bondDetails.faceValue)).toFixed(2)
        }
        this.setState({orderAmt: amount, orderQty: value});
      }
      else if (from === "Amount"){
        if(this.state.quotedPrice !== ""){
          quantity = (parseFloat(value) / (parseFloat(this.state.quotedPrice) * (this.state.bondDetails.faceValue / 100))).toFixed(0);
        }
        this.setState({orderAmt: value, orderQty: quantity});
      }
      else if(from === "Price"){
        if(calc){
          let result = calc.yield(parseFloat(value))*100;
          if(!isNaN(result)){
            yld = result.toFixed(4)
          }
        }
        if(this.state.orderQty !== ""){
          amount = (parseFloat(value) * parseInt(this.state.orderQty) * (this.state.bondDetails.faceValue / 100)).toFixed(2);
        }
        else{
          amount = this.state.orderAmt
        }
        this.setState({orderAmt: amount, quotedPrice: value, quotedYTM: yld })
      }
      else if(from === "Yield"){
          if(calc){
            let result = calc.price(parseFloat(value) / 100);
            if(!isNaN(result)){
              price = result.toFixed(2);
            }
          }
          if(this.state.orderQty !== "" && price !== ""){
            amount = (parseFloat(price) * parseInt(this.state.orderQty) * (this.state.bondDetails.faceValue / 100)).toFixed(2); 
          }
          this.setState({orderAmt: amount, quotedPrice: price, quotedYTM: value, })
      }
    }
  }

  //here we checking the value is input in the Price, Yeild and Quentity textInput
  Validate(from: string) {
    if (from === "Quantity" && parseInt(this.state.orderQty) <= 0) {
        this.setState({invalidQuantity: true, invalidAmount: false});
    } else {
        this.setState({invalidQuantity: false,});
    }
    if(from === "Amount" && parseFloat(this.state.orderAmt) <= 0.0){
      this.setState({invalidAmount: true, invalidQuantity: false});
    } else{
      this.setState({invalidAmount: false},);
    } 
    if (from === "Yield" && parseFloat(this.state.quotedYTM) <= 0.0) {
      this.setState({invalidYTM: true, invalidPrice: false,  });
    } else {
        this.setState({invalidYTM: false, });
    }
    if (from === "Price" && parseFloat(this.state.quotedPrice) <= 0.0) {
      this.setState({invalidPrice: true, invalidYTM: false, });
    } else {
        this.setState({invalidPrice: false, });
    }
  }

  isBuyOrSell(isBuyOrSell: boolean) {
    this.setState({isBuy: isBuyOrSell,});
  }
//VK: TODO : Show alert as coming soon for Factor Portfolio
  render() {
    return (
      <Modal visible={this.props.showModal} >
        <Card style={styles.card}>
          <Card.Title title={(this.props.OrderToEdit != null) ? "Edit Order" : "Create new Order"} />
          <Card.Content style={{ flexDirection: "row", marginLeft: 15, marginRight: 15, zIndex: 12, }} >
            {this.state.bondDetails != null ? (
              <View style={{ flex: 1, flexDirection: "row", justifyContent: "space-between", paddingLeft: 10 }}>
                <Title style={{flex: 1, fontWeight: "500", fontSize: 28, lineHeight: 34, }} >
					        {this.state.bondDetails.Head.BondName + " " + formatCoupon(this.state.bondDetails.Head.Coupon)+ formatMaturityDate(this.state.bondDetails.Head.DateOfMaturity, true)}
                </Title>
              </View>
            ) : (
              <View style={{ flexDirection: "row", width: "95%" }}>
                <BondSearchBar onParentGetItem={this.BondFromSearchBar.bind(this)} context={""} />
              </View>
            )}
          </Card.Content>
          <View style={{ width: "100%", height: 2, backgroundColor: "#DEE6ED", marginTop: 5, }} />
          <View style={styles.cardView}>
            <Text style={styles.iwantToText}>I want to </Text>
            <Text style={this.state.isBuy ? styles.activBuyText : styles.deactivBuyText} onPress={() =>this.isBuyOrSell(true) } >
              Buy
            </Text>
            {/* <Text style={this.state.isBuy ? styles.deactivSellText : styles.activSellText} onPress={() =>this.isBuyOrSell(false) } >
              Sell
            </Text> */}
          </View>
          <View style={styles.cardView}>
            <View style={{ flex: 1, flexDirection: "column" }}>
            <Text style={styles.cardLabelText}> Yield<Text style={{ fontSize: 15 }}> (%)</Text>
              </Text>
              <TextInput
                keyboardType={"numeric"}
                style={styles.cardInputText}
                value={this.state.quotedYTM}
                onBlur={() => { this.Validate("Yield"); }}
                onChangeText={(value) => {this.calculate(value, "Yield");}}
              />
              {this.state.invalidYTM ? (
                <Text style={{ color: "red", fontSize: 12, marginLeft: 18 }}> Please enter valid Yield </Text>
              ) : null}
              <Text style={styles.cardLabelText}>Amount</Text>
              <TextInput
                keyboardType={"numeric"}
                style={styles.cardInputText}
                value={this.state.orderAmt}
                onBlur={() => {this.Validate("Amount"); }}
                onChangeText={(value) => {this.calculate(value, "Amount")}}
              />
              {this.state.invalidAmount ? (
                <Text style={{ color: "red", fontSize: 12, marginLeft: 18 }}> Please enter valid Amount </Text>
              ) : null}
            </View>
            <View style={{ flex: 1, flexDirection: "column" }}>
              <Text style={styles.cardLabelText}> Price  <Text style={{ fontSize: 15 }}>(per Hundred)</Text> </Text>             
              <TextInput
                keyboardType={"numeric"}
                style={styles.cardInputText}
                value={this.state.quotedPrice}
                onBlur={() => {this.Validate("Price");}}
                onChangeText={(value) => { this.calculate(value, "Price"); }}
              />
              {this.state.invalidPrice ? (
                <Text style={{ color: "red", fontSize: 12, marginLeft: 18 }}> Please enter valid Price </Text>
              ) : null}
              <Text style={styles.cardLabelText}>Quantity</Text>
              <TextInput
                keyboardType={"numeric"}
                style={styles.cardInputText}
                value={this.state.orderQty}
                onBlur={() => {this.Validate("Quantity"); }}
                onChangeText={(value) => {this.calculate(value, "Quantity");}}
              />
              {this.state.invalidQuantity ? (
                <Text style={{ color: "red", fontSize: 12, marginLeft: 18 }}> Please enter valid Quantity </Text>
              ) : null}
            </View>
          </View>

          <Card.Actions style={styles.cardAction}>
            <Button mode={"contained"} style={styles.submitButton} uppercase={false} onPress={() => this.submitOrder()} >
              Submit
            </Button>
            <View style={{ width: 5 }} />
            <Button mode={"contained"} style={styles.submitButton} onPress={() => this.close()} uppercase={false} >
              Cancel
            </Button>
          </Card.Actions>
        </Card>       
      </Modal>
    );
  }
}
