//context state in three part
//first state is empty to show the loader
//second state is; when we call api then we get response if we set user data the set context data means user is login
// third state is user is not login means state is "notlogin"

import * as React from "react";
import axios from "axios";
import { env } from "../../config/systemConfigs";

//default json for context
const userJson = {
  firstName: "",
  lastName: "",
  userName: "",
  roleId: -1,
  userId: "",
  userFullName: "",
  isValidSubscription: false,
  subscriptions: [] as any[],
};

//context type for accessing variable and methods, 1 is user json and method to update it
type UserContextType = {
  userData: any;
  setUserData: (value: any) => void;
  apiToGetAuthenticatedData: () => void;
};

//to create context, in that context type is passed for the purpose of accessing methods above
const UserPrefContext = React.createContext<UserContextType | undefined>(
  undefined
);

//child property
type Props = {
  children: React.ReactNode;
};

//functional component for provider
export const UserPrefContextProvider = ({ children }: Props) => {
  const [userData, setUserData] = React.useState(userJson);

  //method to call when page is empty or context is empty
  const apiToGetAuthenticatedData = () => {
    //this function will be called when page is context is empty
    return getUserAuthData();

    //async function created for await/promis purpose
    async function getUserAuthData() {
      await axios({
        method: "POST",
        data: "",
        withCredentials: true,
        url: `${env.API_INFRA_MODULE}/userManagement/getUserData`,
      }).then((result) => {
        if (result.data.status == 200) {
          //json to set in context, will be updated
          var responseData = {
            firstName: result.data.authData.firstName,
            lastName: result.data.authData.lastName,
            userName: result.data.authData.userName,
            roleId: result.data.authData.roleId,
            userId: result.data.authData.userId,
            userFullName: result.data.authData.firstName + " " + result.data.authData.lastName,
            isValidSubscription: result.data.authData.isValidSubscription,
            subscriptions: result.data.authData.subscriptions,
          };
          setUserData(responseData);
        }
        //API fails
        else {
          setUserData(userJson);
        }
      }).catch((error: any) => {
        console.log(JSON.stringify(error));
        setUserData(userJson);
    });
    } //end of async function
  };

  return (
    <UserPrefContext.Provider
      value={{ userData, setUserData, apiToGetAuthenticatedData }}
    >
      {children}
    </UserPrefContext.Provider>
  );
};

//use this for fucnction commponte
export const userContextDataChange = () => React.useContext(UserPrefContext);
export default UserPrefContext;
