import { View, } from "react-native";
import React from "react";
import { env } from "../../../config/systemConfigs";


function Footer() {    

  const footerHtmlString = `   
    <style>
  
     .section {
      margin-top: 70px;
      background: #1b2b47;
      font-family: Inter !important;

    }
    
    @mixin contain {
      padding-left: 90px;
      padding-right: 90px;
      display: flex;
      @media (max-width: 600px) {
        padding-left: 30px;
        padding-right: 30px;
      }
    }
    
    .container {
      @include contain();
      padding-bottom: 46px;
      padding-top: 60px;
      display: flex;
      @media (max-width: 600px) {
        padding-bottom: 26px;
        padding-top: 40px;
      }
    }
    
    .company{
        margin-right: 110px;
        display: flex;
        flex-direction: column;
        @media (max-width: 600px) {
          margin-right: 60px;
          &:last-child {
            margin-right: 0;
          }
        }
        h4 {
          font-weight: 600;
          font-size: 18px;
          line-height: 22px;
          color: #ffffff;
          margin-bottom: 16px;
        }
        a {
          font-weight: 400;
          font-size: 16px;
          line-height: 22px;
          color: #acb6c9;
          margin-bottom: 16px;
          text-decoration: none;
          transition: 0.4s;
          &:hover {
            color: #31bc98;
          }
        }
      }
    .services {
      margin-right: 110px;
      display: flex;
      flex-direction: column;
      @media (max-width: 600px) {
        margin-right: 60px;
        &:last-child {
          margin-right: 0;
        }
      }
      h4 {
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        color: #ffffff;
        margin-bottom: 16px;
      }
      a {
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        color: #acb6c9;
        margin-bottom: 16px;
        text-decoration: none;
        transition: 0.4s;
        &:hover {
          color: #31bc98;
        }
      }
    }
    
    .bottom {
      @include contain();
    }
    
    .policy {
      width: 100%;
      padding-top: 26px;
      padding-bottom: 20px;
      border-top: 1px solid #40404e;
      display: flex;
      justify-content: space-between;
    
      p {
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        color: #acb6c9;
        margin-bottom: 16px;
        text-decoration: none;
      }
      a {
        color: #bfbdbd;
        font-weight: 400;
        font-size: 14px;
        color: #bfbdbd;
        text-decoration: none;
        padding-left: 54px;
        transition: 0.4s;
        &:hover {
          color: #31bc98;
        }
      }
      @media (max-width: 600px) {
        flex-direction: column;
        justify-content: flex-start;
        p {
          margin-bottom: 30px;
        }
        div {
          display: flex;
          flex-direction: column;
    
          a {
            padding-left: 0px;
            margin-bottom: 16px;
          }
        }
      }
    }
    
h4 {
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #ffffff;
    margin-bottom: 16px;  
}
a:hover {
    color: #31bc98;
    }
.serviceA{   
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #acb6c9;
    margin-bottom: 16px;
    text-decoration: none;
    transition: 0.4s;      
}
.policyA{   
    color: #bfbdbd;
    font-weight: 400;
    font-size: 14px;
    color: #bfbdbd;
    text-decoration: none;
    padding-left: 54px;
    transition: 0.4s;   
}
.policyP{
  
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        color: #acb6c9;
        margin-bottom: 16px;
        text-decoration: none;
        margin-top: 0.01px;
}
.companyA{
    
    font-weight: 100;
        font-size: 16px;
        line-height: 22px;
        color: #acb6c9;
        margin-bottom: 16px;
        text-decoration: none;
        transition: 0.4s;
}
</style>    
    
    <div class="section">
    <div class="container"  style="padding-left: 7%;padding-right: 7%;">
      <div class="company">
         
        
      </div>
      
    </div>
    <div class="bottom"  style="padding-left: 7%;padding-right: 7%;">
      <div class="policy">
        <p class="policyP"  > 2021 INRBonds</p>
        <div>
        
          <a href="" class="policyA">Privacy policy</a>
       </div>
      </div>
    </div>
  </div>
`;

  return (
    <View>
      <div>
        <div dangerouslySetInnerHTML={{ __html: footerHtmlString }} /></div>
    </View>
  )
}
export default Footer;
