import React from "react";
import { Image, View, StyleSheet, Alert } from "react-native";
import {
  Button,
  Card,
  Text
} from "react-native-paper";
import axios from "axios";

import { env } from "../../config/systemConfigs";
import {
  TextField,
  makeStyles,
  createStyles,
  Theme
} from "@material-ui/core";

import { userContextDataChange } from '../../shared/context/UserPrefContextProvider';



const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexWrap: "wrap",
    },
    margin: {
      margin: theme.spacing(1),
    },
    height: {
      height: 10,
    },
  })
);

//this is Login Function component
export const PassordToAuth = (props: any) => {

  if(props.route.params == undefined){
    window.location.href = `${env.API_INRB_FE}/login`
    return(<View></View>); 
  }else{
  // here we create the context to get user data which store in context
  const { userData, setUserData } = userContextDataChange()!;
 
  //this is userName state
  const [userName, setUserName] = React.useState(props.route.params.id !== undefined ? props.route.params.id : '')
  //this is passWord firstName state
  const [passWord, setPassword] = React.useState("");
  //this is message firstName state
  const [message, setMassage] = React.useState("");
  //show snackbar with message function.

  const [onValidateEmail, setonValidateEmail] = React.useState(false);
  const [onValidatePassword, setonValidatePassword] = React.useState(false);

  const classes = useStyles();
  const showMessage = (msg: string) => {
    setMassage(msg);
  };

   if (props.route.params.id == "") {
     window.location.href = `${env.API_INRB_FE}/login`
   }

  //createing json object and send to server
  const getAuthenticate = () => {
    axios({
      method: "POST",
      data: { username: userName, password: passWord, },
      withCredentials: true,
      url: `${env.API_INFRA_MODULE}/auth/login`,
    })
      .then((result) => {
        //if user already loggedIn in same browser, no other user can log-in
        if (result.data.status === "406") {
          if(props.route.params.redirect !== undefined){
            window.location.href = props.route.params.redirect;
          }
          else{
            window.location.href = `${env.FE_DOMAIN + "marketwatch"}`;
          }
        }

        if (result.data.status === "403") {
          showMessage(result.data.message);
        } else {

          //start of context
          //to set in context the reponse data
          var responseData = {
            firstName: result.data.result.firstName,
            lastName: result.data.result.lastName,
            userName: result.data.result.userName,
            roleId: result.data.result.roleId,
            userId: result.data.result.userId,
            userFullName: result.data.result.firstName + " " + result.data.result.lastName
          }
          setUserData(responseData)
          //end of context
          if(props.route.params.redirect !== undefined){
            window.location.href = props.route.params.redirect;
          }
          else{
            window.location.href = `${env.FE_DOMAIN+"marketwatch"}`;
          }
        }
      })
      .catch((err) => {
        console.log("Error console:", err);
      });
  };




  const onEnterPress = (event: any) => {
    // console.log(JSON.stringify(event.nativeEvent.key));
    if (event.keyCode == 13) {
      getAuthenticate();
    }
  };

  return (
    <View style={styles.container}>
      <Card style={styles.card}>

        <Card.Content style={styles.card_content}>
          <Image
            style={{
              width: "60%",
              height: "25%",
              resizeMode: "center",
              marginLeft: "22%",
              // marginBottom:"5%"
            }}
            source={require("../../assets/icons/INRBond-logo.png")}
          />
          <Text style={styles.titleText}>Sign In</Text>
          <Text style={{
            fontSize: 20,
            alignSelf: "center",
            marginTop: "15%"
          }}> {userName}</Text>
          {/* {userName} */}
          <TextField
            className={classes.margin}
            style={{ marginTop: 10 }}
            autoFocus={true}
            label="Password"
            type="Password"
            variant="outlined"
            // helperText={
            //   onValidatePassword
            //     ? "Password should be minimum 6 characters."
            //     : ""
            // }
            onFocus={() => setonValidatePassword(false)}
            onChange={(event) => setPassword(event.target.value)}
            onKeyDown={(event) => onEnterPress(event)}
          />
          <View
            style={{
              marginTop: 5,
              flexDirection: "row",
              alignContent: "flex-end",
              justifyContent: "flex-end",
            }}
          >
            <Text
              style={{
                width: "132px",
                height: "19px",
                fontWeight: "500",
                fontSize: 16,
                lineHeight: 19,
                letterSpacing: -0.373585,
                alignSelf: "flex-end",
                textAlign: "right",
              }}
              onPress={() => props.navigation.navigate("forgot-password")}
            >
              Forgot Password?
            </Text>
          </View>
          <Button
            style={{ marginTop: "10px" }}
            mode="contained"
            color="#27AE60"
            uppercase={false}
            contentStyle={{ height: "50px" }}
            onPress={() => getAuthenticate()}
            disabled={passWord == "" || userName == "" ? true : false}
          >
            <Text style={{ color: "white", fontWeight: "bold" }}>
              Login
            </Text>
          </Button>
          <View
            style={{
              marginTop: "2%",
              flexDirection: "row",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <Text style={{ color: "red" }}>{message}</Text>
          </View>
          <View
            style={{
              flexDirection: "row",
              marginTop: 15,
              justifyContent: "center",
            }}
          >
            <Text style={{ fontSize: 13, marginTop: 2.5 }}>
              Want to use another email?{" "}
            </Text>
            <Text
              style={{ fontSize: 18, color: "#5C6AC4" }}
              onPress={() => {
                // props.navigation.navigate("login");
                // window.location.href = env.API_INRB_FE + "/login"
                props.navigation.reset({
                  index: 0,
                  routes: [
                    {
                      name: 'login',
                    },
                  ],
                })


              }}
            >
              Sign-in
            </Text>
          </View>

        </Card.Content>
      </Card>


    </View>


  );
}
};
//this is styleSheet
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#F2F2F2",
    alignItems: "center",
    justifyContent: "center",
  },
  card: {
    marginTop: 5,
    backgroundColor: "#FCFCFC",
    padding: "2vh",
    elevation: 10,
  },
  card_content: {
    backgroundColor: "#fff",
    justifyContent: "center",
    textAlignVertical: "center",
    height: "70vh",
    width: "50vh",
    verticalAlign: "center",
  },
  titleText: {
    fontSize: 20,
    fontWeight: "bold",
    alignSelf: "center",

  },
  textInput: {
    marginTop: 10,
    fontWeight: 500,
    fontSize: 22,
    height: 50,
  },

  // button css
  button: {
    backgroundColor: "#F9FAFB",
    color: "black",
    marginTop: "10px",
    tintColor: "transperent",
  },
  buttonText: {
    height: "50px",
    color: "black",
    tintColor: "transparent",
  },
  circle: {
    width: 60,
    height: 60,
    borderRadius: 100 / 2,
    borderColor: "#DCE1FF",
    borderWidth: 2,
    alignSelf: "center",
    marginBottom: 10,
    alignContent: "center",
    justifyContent: "center",
  },
  avetarText: {
    fontSize: 25,
    fontWeight: "bold",
    color: "#5C6AC4",
    marginTop: 2.5,
    marginRight: 2.5,
    textAlign: "center",
  },
});
