import React from "react";
import { View, Text, Image, StyleSheet } from "react-native";
import { Button, Card, DataTable } from "react-native-paper";
import { makeStyles, TableCell } from "@material-ui/core";
import {formatCoupon, formatMaturityDate} from '../../services/utilServices';
const useStyles = makeStyles({
  cellHead: {
    minWidth: "10%",
    backgroundColor: "#FFF",
  },
  cell: {
    backgroundColor: "#FFF",
    maxWidth: "10%",
  },
});

export const TableRowComponent = (props: any) => {
  const classes = useStyles();
//  let Data: any;
//  let Headkey: any;
  let Data2: any;
  let Data3: any;
  const [overlayLayoutHideShow, setOverlayLayoutHideShow] = React.useState( false );
/* VK
  if (props.bondObject.Head != undefined || props.bondObject.Head != null) {
    Data = Object.entries(props.bondObject.Head).map(
      ([key, val]) => key + ":" + val
    );
    Headkey = Object.entries(props.bondObject.Head).map(([key, val]) => key);
  }
*/
  if (props.bondObject.Body != undefined || props.bondObject.Body != null) {
    Data2 = Object.entries(props.bondObject.Body).map(
      ([key, val]) => key + ":" + val
    );
  }

  if (props.bondObject.Tail != undefined || props.bondObject.Tail != null) {
    Data3 = Object.entries(props.bondObject.Tail).map(
      ([key, val]) => key + ":" + val
    );
  }

  const nav = () => {
    if (props.bondObject.SpecialProduct == 'F') {
      props.navigation.navigate("FactorPortfolioDetails", props.bondObject);
    } else {
      props.navigation.navigate("bonddetails", { bondData: JSON.stringify(props.bondObject), inWatchList: props.isWatchListItem(props.bondObject)});
    }
  };

  return (
    <DataTable>
      <Card style={styles.cardStyle}>
        <DataTable.Row
          style={styles.tableRow}
          onFocus={() => { setOverlayLayoutHideShow(!overlayLayoutHideShow);  }}
          onBlur={() => { setOverlayLayoutHideShow(!overlayLayoutHideShow); }}
        >
          <TableCell align="left" className={classes.cellHead}>
            <Text style={styles.bondName}>
              {props.bondObject.Head.BondName != null && props.bondObject.Head.BondName !== "" ? 
                props.bondObject.Head.BondName : "-"}
            </Text>
            <br />
            <Text style={styles.dateMaturity}>
              {props.bondObject.Head.DateOfMaturity != null && props.bondObject.Head.DateOfMaturity !== ""
                ? formatMaturityDate(props.bondObject.Head.DateOfMaturity, false) : "-"}
            </Text>
          </TableCell>

          <TableCell align="center" className={classes.cell}>
            <View>
              {props.bondObject.Head.PriceTrend != null && (
                ["U", "D", "V"].includes(props.bondObject.Head.PriceTrend)) ? (
                  <Image
                    style={styles.graphImage}
                    resizeMode={"center"}
                    source={require(`../../../assets/image/${props.bondObject.Head.PriceTrend}.png`)}
                  />
              ) : (
                <View style={styles.graphImage} />
              )}
              </View>
          </TableCell>
          <View style={{ height: "80%", width: 1, backgroundColor: "#E6E3E3", marginTop: "0.5%", }} />
          <TableCell align="center" className={classes.cell}>
            <View style={{ flexDirection: "column" }}>
              {props.bondObject.Head.Rating != null && props.bondObject.Head.Rating !== "" ? (
                <View>
                <Text style={styles.title}> Rating </Text>
                <Text>{props.bondObject.Head.Rating}</Text>
              </View>
              ) : (
                <View><Text style={styles.title} >-</Text></View>
              )}
            </View>
          </TableCell>
          <TableCell align="center" className={classes.cell}>
            <View style={{ flexDirection: "column" }}>
              {props.bondObject.Head.Coupon != null && props.bondObject.Head.Coupon !== "" ? (
                <View>
                <Text style={styles.title}> Coupon </Text>
                <Text>{formatCoupon(props.bondObject.Head.Coupon)}</Text>
              </View>
              ) : (
                <View > <Text style={styles.title} >-</Text></View>
              )}
            </View>
          </TableCell>
          <TableCell align="center" className={classes.cell}>
            <View style={{ flexDirection: "column" }}>
              {props.bondObject.Head.LTY != null && props.bondObject.Head.LTY !== "" ? (
                <View>
                <Text style={styles.title}> LTY </Text>
                <Text>{props.bondObject.Head.LTY}</Text>
              </View>
              ) : (
                <View > <Text style={styles.title} >-</Text></View>
              )}
            </View>
          </TableCell>
          {/* VK: Use this code instead of above for everything except BondName & DateOfMaturity 
          {Data.map((item: string, index: any) => {
            let keyVal = item.split(":");
            //
            if (
              keyVal[0] === "BondName" ||
              keyVal[0] === "DateOfMaturity" ||
              keyVal[0] === "RiskProfile"
            ) {
            } else if (keyVal[0] === "PriceTrend") {
            } else if (keyVal[0] === "Rating") {
            } else if (keyVal[0] === "Coupon") {
            } else if (keyVal[0] === "LTY") {
            } else {
              return (
                <TableCell align="center" className={classes.cell}>
                  <View style={{ flexDirection: "column" }}>
                    <Text style={styles.title}>{keyVal[0]}</Text>
                    <Text>{keyVal[1].trim()}</Text>
                  </View>
                </TableCell>
              );
            }
          })}
        */}
          {Data2.map((item: any, index: any) => {
            let keyVal = item.split(":");
            if (keyVal[0] === "SpreadValuation") {
              return (
                <TableCell align="center">
                  <View style={{ flexDirection: "row-reverse", backgroundColor: "#F4F6F8", }} >
                    {keyVal[1] !== null && ["O", "F", "UV"].includes(keyVal[1]) ? (
                      <Image style={{ height: 33, width: 22.79, alignSelf: "center", backgroundColor: "#F4F6F8", }}
                        resizeMode={"contain"} source={require(`../../../assets/image/${keyVal[1]}.svg`)}
                      />
                    ) : (
                      <View />
                    )}
                  </View>
                </TableCell>
              );
            } else {
              return (
                <TableCell align="center">
                  <View style={{ flexDirection: "column" }}>
                    <Text style={styles.title}>{keyVal[0]}</Text>
                    <Text style={{ textAlign: "center" }}>
                      {keyVal[1] == null || keyVal[1].trim() === "" ? "-" : keyVal[1].trim()}
                    </Text>
                  </View>
                </TableCell>
              );
            }
          })}
          <TableCell align="center" className={classes.cell}>
            <View style={{ flexDirection: "column" }}>
                <Text style={{fontFamily: "Inter", fontStyle: "normal", fontSize: 12, fontWeight: "bold", lineHeight: 19,
                              letterSpacing: -0.271698, color: "#637381", }}>{props.bondObject.Tail.flags}</Text>
            </View>
          </TableCell>
        </DataTable.Row>

        {overlayLayoutHideShow ? (
          <View style={{ width: "100%", paddingLeft: "65%", position: "absolute", }} >
            <View style={styles.overlya}>
              <Button
                icon={ props.isWatchListItem(props.bondObject) ? require("../../../assets/quit.svg") : require("../../../assets/addwithcircal.svg") }
                uppercase={false}
                color={"#FFF"}
                onPress={() => { props.manageWatchList(props.bondObject); }}
              >
                WatchList
              </Button>
              <Button
                style={{ backgroundColor: "#383E56", marginLeft: 15 }}
                uppercase={false}
                color={"#FFFFFF"}
                onPress={() => { nav(); }}
              >
                View
              </Button>
                <Button
                  onPress={() => { props.showOrderModal(props.bondObject); }}
                  style={{ backgroundColor: "#F95E68", marginLeft: 15 }}
                  uppercase={false}
                  color={"#FFFFFF"}
                >
                  Buy / Sell
                </Button>
            </View>
          </View>
        ) : null}
      </Card>
    </DataTable>
  );
};

const styles = StyleSheet.create({
  cardStyle: {
    marginBottom: "1%",
    flexDirection: "row",
    zIndex: 10,
  },
  tableRow: {
    height: 65,
    borderWidth: 0,
    backgroundColor: "#F4F6F8",
    width: "100%",
  },
  title: {
    fontFamily: "Inter",
    fontStyle: "normal",
    fontSize: 16,
    fontWeight: "100",
    lineHeight: 19,
    letterSpacing: -0.271698,
    color: "#637381",
  },
  value: {
    fontFamily: "Inter",
    fontStyle: "normal",
    fontSize: 16,
    fontWeight: "100",
    lineHeight: 19,
    letterSpacing: -0.271698,
    color: "#232328",
    marginTop: 10,
  },
  bondName: {
    fontFamily: "Inter",
    fontStyle: "normal",
    fontSize: 18,
    fontWeight: "600",
    lineHeight: 24,
    letterSpacing: -0.339623,
    color: "#232328",
  },
  dateMaturity: {
    fontFamily: "Inter",
    fontStyle: "normal",
    fontSize: 16,
    fontWeight: "normal",
    lineHeight: 19,
    letterSpacing: -0.271698,
    color: "#525252",
  },
  graphImage: {
    width: 33,
    height: 22.79,
    marginLeft: "10%",
    marginTop: "10%",
    backgroundColor: "#FFF",
  },
  overlya: {
    flexDirection: "row",
    backgroundColor: "rgba(80, 80, 107, 0.86)",
    paddingTop: "3.8%",
    paddingBottom: "3%",
    borderWidth: 0.5,
    borderTopRightRadius: 5,
    borderBottomRightRadius: 5,
    paddingHorizontal: 5,
    justifyContent: "center",
  },
});
