import React from "react";
import { Image, View, StyleSheet } from "react-native";
import { Button, Card, Text } from "react-native-paper";
import axios from "axios";
import { env } from "../../config/systemConfigs";
import { TextField, makeStyles, createStyles, Theme } from "@material-ui/core";

import { userContextDataChange } from "../../shared/context/UserPrefContextProvider";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexWrap: "wrap",
    },
    margin: {
      margin: theme.spacing(1),
    },
    height: {
      height: 10,
    },
  })
);

//this is Login Function component
export const ChangePasswordToAuth = (props: any) => {
  if (
    props.route.params == undefined &&
    props.route.params.id == undefined &&
    props.route.params.ser == undefined
  ) {
    window.location.href = `${env.API_INRB_FE}/login`;
    return <View />;
  } else {
    // here we create the context to get user data which store in context
    const { userData, setUserData } = userContextDataChange()!;

    const [dataSubmit, setPasswordData] = React.useState({
      newPassword: "",
      confirmPassword: "",
      userName: props.route.params
        ? props.route.params.ser || props.route.params.id
        : "",
    });
    //this is message firstName state
    const [message, setmessage] = React.useState("");
    const [PassWordValid, setPasswordValid] = React.useState(false);
    const [ConfirmPassWordValid, setConfirmPasswordValid] = React.useState(
      false
    );
    const classes = useStyles();

    let getUrlKey = window.location.search.split("=");
    
    //createing json object and send to server
    const getAuthenticate = () => {


      //here we do password validation for registration
      let passwordMatchString = /^(?=.{8,})(?=.*[a-z])(?=.*[A-Z])(?=.*[^\w\d]).*$/;
      if (!dataSubmit.newPassword.match(passwordMatchString)) {
        setPasswordValid(true);
        return false;
      }
      if (!dataSubmit.confirmPassword.match(passwordMatchString)) {
        setConfirmPasswordValid(true);
        return false;
      }
      if (dataSubmit.newPassword !== dataSubmit.confirmPassword) {
        setmessage("New and confirm password doesn't match.");
        setTimeout(() => {
          setmessage("");
        }, 4000);
        return false;
      }

      if (props.route.params.ser != undefined) {
        setPasswordData({ ...dataSubmit, userName: props.route.params.ser });
      } else if (props.route.params.ser != undefined) {
        setPasswordData({ ...dataSubmit, userName: props.route.params.id });
      }
      axios({
        method: "POST",
        data: dataSubmit,
        withCredentials: true,
        url: `${env.API_INFRA_MODULE}/auth/newPassword`,
      })
        .then((result) => {
          //if user already loggedIn in same browser, no other user can log-in
          if (result.data.status === "406") {
            setmessage(result.data.message);
            setTimeout(() => {
              setmessage("");
              window.location.href = `${env.API_INFO_RECOM_MODULE}`;
            }, 5000);
          }
          //now to login it.
          else if (result.data.status == "200") {
            axios({
              method: "POST",
              data: {
                username: dataSubmit.userName,
                password: dataSubmit.confirmPassword,
              },
              withCredentials: true,
              url: `${env.API_INFRA_MODULE}/auth/login`,
            })
              .then((result) => {
                if (result.data.status === "403") {
                  setmessage(result.data.message);
                  setTimeout(() => {
                    setmessage("");
                  }, 4000);
                } else {
                  //start of context
                  //to set in context the reponse data
                  var responseData = {
                    firstName: result.data.result.firstName,
                    lastName: result.data.result.lastName,
                    userName: result.data.result.userName,
                    roleId: result.data.result.roleId,
                    userId: result.data.result.userId,
                    userFullName:
                      result.data.result.firstName +
                      " " +
                      result.data.result.lastName,
                  };
                  setUserData(responseData);
                  //end of context
                  window.location.href = `${env.API_INFO_RECOM_MODULE}`;
                } //end else
              })
              .catch((err) => {
                console.log("Error console:", err);
              });
          } else {
            alert("not eligible");
          }
        })
        .catch((err) => {
          console.log("Error console:", err);
        });
    }; //end of getAuthenticate()

    const onEnterPress = (event: any) => {
      if (event.nativeEvent.key == "Enter") {
        getAuthenticate();
      }
    };

    return (
      <View style={styles.container}>
        <Card style={styles.card}>
          <Card.Content style={styles.card_content}>
            <Image
              style={{
                width: "60%",
                height: "25%",
                resizeMode: "center",
                marginLeft: "22%",
                // marginBottom:"5%"
              }}
              source={require("../../assets/icons/INRBond-logo.png")}
            />
            <Text style={styles.titleText}>Set Password</Text>

            {props.route.params.id != undefined && (
              <Text
                style={{
                  color: "red",
                  marginHorizontal: "4%",
                  marginTop: "6%",
                }}
              >
                It seems that you are logging first time or your password has
                been expired.
              </Text>
            )}

            <Text
              style={{
                fontSize: 20,
                alignSelf: "center",
                marginTop: "8%",
              }}
            >
              {dataSubmit.userName}
            </Text>

            <TextField
              className={classes.margin}
              autoFocus={true}
              error={PassWordValid}
              style={{ marginTop: 10 }}
              label="New Password"
              type="Password"
              variant="outlined"
              helperText={
                PassWordValid
                  ? "Password should contain 8 letter,one Uppercase character ,one special ,and one digit."
                  : ""
              }
              onFocus={() => setPasswordValid(false)}
              onChange={(event) =>
                setPasswordData({
                  ...dataSubmit,
                  newPassword: event.target.value,
                })
              }
              onKeyDown={(event) => onEnterPress(event)}
            />
            <TextField
              className={classes.margin}
              error={ConfirmPassWordValid}
              style={{ marginTop: 10 }}
              label="Confirm Password"
              type="Password"
              variant="outlined"
              helperText={
                ConfirmPassWordValid
                  ? "Password should contain 8 letter,one Uppercase character ,one special ,and one digit."
                  : ""
              }
              onFocus={() => setConfirmPasswordValid(false)}
              onChange={(event) =>
                setPasswordData({
                  ...dataSubmit,
                  confirmPassword: event.target.value,
                })
              }
              onKeyDown={(event) => onEnterPress(event)}
            />
            <Button
              style={{ marginTop: "10px" }}
              mode="contained"
              color="#27AE60"
              uppercase={false}
              contentStyle={{ height: "50px" }}
              onPress={() => getAuthenticate()}
              disabled={
                dataSubmit.newPassword == "" || dataSubmit.confirmPassword == ""
                  ? true
                  : false
              }
            >
              <Text style={{ color: "white", fontWeight: "bold" }}>Login</Text>
            </Button>
            <View
              style={{
                marginTop: "2%",
                flexDirection: "row",
                alignContent: "center",
                justifyContent: "center",
              }}
            >
              <Text style={{ color: "red" }}>{message}</Text>
            </View>
            <View
              style={{
                flexDirection: "row",
                marginTop: 9,
                justifyContent: "center",
              }}
            >
              <Text style={{ fontSize: 13, marginTop: 3 }}>
                Want to use another email?{" "}
              </Text>
              <Text
                style={{ fontSize: 18, color: "#5C6AC4" }}
                onPress={() => {
                  window.location.href = env.API_INRB_FE + "/login";
                  // props.navigation.navigate("login");
                }}
              >
                Sign-in
              </Text>
            </View>
          </Card.Content>
        </Card>
      </View>
    );
  }
};
//this is styleSheet
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#F2F2F2",
    alignItems: "center",
    justifyContent: "center",
  },
  card: {
    marginTop: 5,
    backgroundColor: "#FCFCFC",
    padding: "2vh",
    elevation: 10,
  },
  card_content: {
    backgroundColor: "#fff",
    justifyContent: "center",
    textAlignVertical: "center",
    height: "70vh",
    width: "50vh",
    verticalAlign: "center",
  },
  titleText: {
    fontSize: 20,
    fontWeight: "bold",
    alignSelf: "center",
  },
  textInput: {
    marginTop: 10,
    fontWeight: 500,
    fontSize: 22,
    height: 50,
  },

  // button css
  button: {
    backgroundColor: "#F9FAFB",
    color: "black",
    marginTop: "10px",
    tintColor: "transperent",
  },
  buttonText: {
    height: "50px",
    color: "black",
    tintColor: "transparent",
  },
  circle: {
    width: 60,
    height: 60,
    borderRadius: 100 / 2,
    borderColor: "#DCE1FF",
    borderWidth: 2,
    alignSelf: "center",
    marginBottom: 10,
    alignContent: "center",
    justifyContent: "center",
  },
  avetarText: {
    fontSize: 25,
    fontWeight: "bold",
    color: "#5C6AC4",
    marginTop: 2.5,
    marginRight: 2.5,
    textAlign: "center",
  },
});
