//this is ArticleManagement function component
export const NotFoundPage = (props: any) => {
    //to get set cookie
    const [cookies, setCookie, remove] = useCookies(["name"]);

    //to go back to previous page
    const goBack = () => {
         
        history.go(-1)
      };


    //JSX UI
    return (
        <View style={{ backgroundColor: "#ffff" }}>
           
            <View style={{ height: height, width: width, marginTop:"15%" }}>   
            
              <View style={styles.cardView}>
                <Text
                  style={{
                    width: "100%",
                    fontFamily: "Inter",
                    fontSize: 15,
                    fontStyle: "normal",
                    padding: 10,
                    textAlign:'center',
                    color:'#454F5B',
                    letterSpacing:-0.30566,
                    fontWeight:'600',
                   
                  }}
                >
                    
                 Oops!! Something went wrong!!
                </Text>
              </View>
              <Card.Actions style={styles.cardAction}>
                <View style={{ width: 5,  }} />
                <Button
                  mode={"contained"}
                  style={styles.submitButton}
                  uppercase={false}
                  onPress={() =>
                    goBack()
                  }
                >
                 Go Back
                </Button>
              </Card.Actions>
            
            </View>
           
           
        </View>
    );
};

