import React from "react";
import axios from "axios";

import { ActivityIndicator, View, StyleSheet, ScrollView, FlatList, Picker } from "react-native";
import { TextInput, Button, Card, Text, Snackbar } from "react-native-paper";
import { env } from "../../config/systemConfigs";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import MenuAppBar from "../../shared/component/appBar/header";
import Footer from "../../shared/component/appBar/footer";
export const preViewComponent = (props: any) => {



  //to disable buttons
  const [buttonDisabled, setSubmitDisable] = React.useState({
    buttonDisable: false,
  });


  if (props.route.params == undefined) {
    window.location.href = env.API_INRB_FE + "/save-article";
  }
  const artivalPreView = props.route.params.previewDoc;
  //to show loader
  const [loader, setLoader] = React.useState(false);

  //on submit of publish
  const onSubmit = (flag: any) => {

    setLoader(true)
    //todo loader
    toast('Report uploading is in progress.', {
      position: "top-center",
      type: "info",
      hideProgressBar: true,
      closeOnClick: true,
      pauseOnHover: false,
      draggable: false,
      autoClose: 2000,
    });


    setSubmitDisable({
      ...buttonDisabled,
      buttonDisable: true,
    });

    goBack('publish');

    if (props.route.params.updation != "yes") {

      axios({
        method: "POST",
        data: props.route.params.toPublish,
        withCredentials: true,
        url: `${env.API_INFRA_MODULE}/adminManagement/saveArticle`,
      }).then((result) => {
        setLoader(false)
        //here we check the status code and as per the code we show navigation
        if (result.data.status === 409) {

          toast(result.data.message, {
            position: "top-center",
            type: "success",
            hideProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            draggable: true
          });

        } else if (result.data.status === 200) {

          toast(result.data.message, {
            position: "top-center",
            type: "success",
            hideProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            draggable: true
          });

          window.location.href = env.API_INRB_FE + "/save-article";



        } else if (result.data.status === 100) {

          toast(result.data.message, {
            position: "top-center",
            type: "success",
            hideProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            draggable: true
          });

        }
      });//end of axios
    } else {


      axios({
        method: "PUT",
        data: props.route.params.toPublish,
        withCredentials: true,
        url: `${env.API_INFRA_MODULE}/adminManagement/saveArticle`,
      }).then((result) => {
        setLoader(false)
        //here we check the status code and as per the code we show navigation
        if (result.data.status === 409) {

          toast(result.data.message, {
            position: "top-center",
            type: "success",
            hideProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            draggable: true
          });

        } else if (result.data.status === 200) {

          toast(result.data.message, {
            position: "top-center",
            type: "success",
            hideProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            draggable: true
          });

          window.location.href = env.API_INRB_FE + "/save-article";



        } else if (result.data.status === 100) {

          toast(result.data.message, {
            position: "top-center",
            type: "success",
            hideProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            draggable: true
          });

        }
      });//end of axios
    }//end of else

  }

  //go back to save article
  const goBack = (flag: any) => {


    const reqData = {
      _id: props.route.params.previewId,
      previewDocTimestamp: props.route.params.previewDocTimestamp
    };

    //to remove preview unused data from db and server
    axios({
      method: "POST",
      data: reqData,
      withCredentials: true,
      url: `${env.API_INFRA_MODULE}/adminManagement/revertPreview`,
    }).then((result) => {
      if (flag == 'back') {
        window.history.back();
      }

    })

  }

  return <View style={{ height: "100%", width: "100%", backgroundColor: "#FBFCFF" }}>
    <MenuAppBar navigate={props.navigation} />
    <View style={{ marginLeft: "20%", marginRight: "20%", marginTop: "5%", backgroundColor: "#FBFCFF" }}>
      <ToastContainer />
      <div>
        <style>
          {`
                font-family: Inter !important;
            
                 table
                 {
                   padding: 5px !important;
                   width:100% !important;
                 }
                 th
                 {
                       padding: 5px !important;
                       border: 1px solid black !important;
                 }
                  td
                 {
                   padding: 5px !important;
                   border: 1px solid black !important;
                 }
          `}
        </style>
        <div dangerouslySetInnerHTML={{ __html: artivalPreView }} /></div>
      {loader && <ActivityIndicator size="large" color="#00ff00" />}
      <View style={{ flexDirection: "row", justifyContent: "space-evenly", marginBottom: "5%" }}>  <Button
        style={{
          marginTop: "10px",
          width: "30%"
        }}

        mode="contained"
        color="#27AE60"
        uppercase={false}
        contentStyle={{
          height: "40px",
        }}
        onPress={() => onSubmit("publish")}

        disabled={
          buttonDisabled.buttonDisable == true}
      >

        <Text
          style={{
            color: "white",
            fontWeight: "bold",
          }}>
          Publish
								</Text>
      </Button>

        <Button
          style={{
            marginTop: "10px",
            width: "30%",
          }}
          mode="contained"
          color="#27AE60"
          uppercase={false}
          disabled={
            buttonDisabled.buttonDisable == true}
          onPress={() => goBack('back')}
          contentStyle={{
            height: "40px",
          }}>
          <Text
            style={{
              color: "white",
              fontWeight: "bold",
            }}>
            Back
								</Text>
        </Button>

      </View>


    </View>

    <Footer />
  </View>;
};



