import React from "react";
import { useState } from "react";
import {
  View,
  Text,
  StyleSheet,
  TouchableOpacity,
  Image,
  ScrollView,
  FlatList,
  ActivityIndicator,
} from "react-native";
import { Button, Card, IconButton, Modal, Title } from "react-native-paper";
import * as xlsx from "xlsx";
import { env } from "../../../../config/systemConfigs";
import * as download from "../../../../shared/services/downloadFile";
import axios from "axios";
import Moment from "moment";
//here is the bond calculator lib added.
const bondCalculator = require("bond-calculator");
import { userContextDataChange } from "../../../../shared/context/UserPrefContextProvider";

export default function BulkOrderUploadComponent(props: any) {
  const [invalidRows, setInvalidRows] = useState<string[]>([]);   //to show invalid generic messages
  const [disable, setDisable] = useState(false)                   //for loader to show if high amount data in csv
  const [orderJosnArray, setOrderJosnArray] = useState<any[]>([]);//to save order data
  // here we create the context to get user data which store in context
  const { userData, setUserData } = userContextDataChange()!;

  //on import now button click this below call.
  function handleImportNow() {
    
    setDisable(true) //to disable import now button

    if (orderJosnArray.length > 0) {
      
        axios({
          method: "POST",
          data:orderJosnArray,
          withCredentials: true,
          url:
          `${env.API_TRADING_MODULE}/tradingManagement/uploadOrders`,
        })
        .then((response) => {
          if (response.status == 200) {
            alert("Sucessfully uploaded orders");
            resetAndCloseModal();
          }
          else{
            alert("Oops, something went wrong. Please try again!");  
            setDisable(false);
          }
        })
        .catch((err) => {
          alert("Oops, something went wrong. Please try again!");
          setDisable(false);
        });     
    } else {
      alert("Nothing to upload");
      setDisable(false);
    }
  }
  // validate each row from the uploaded csv file
  function validateRows(inputRows: Object[]) {
    let inValidRows: string[] = [];
    inputRows.forEach((element: any) => {
      //to check buy/sell
      if (typeof element.isBuy != "boolean") {
        if (typeof element.isBuy == "string" && (element.isBuy.toLowerCase() == "true" || element.isBuy.toLowerCase() == "false")) {
          element.isBuy = JSON.parse(element.isBuy)
        }
        else {
          inValidRows.push("Row No : " + (element.__rowNum__ + 1) + " - Improper isBuy defined");
        }
      }
      if (typeof element.isinNo != "string" || (!element.isinNo.toUpperCase().startsWith("INE") && !element.isinNo.toUpperCase().startsWith("INF"))) {
        inValidRows.push("Row No : " + (element.__rowNum__ + 1) + " - Improper ISIN number defined");
      }
      if (typeof element.yield != "number" && typeof element.price != "number") {
        inValidRows.push("Row No : " + (element.__rowNum__ + 1) + " - Input either Yield or Price");
      }
      else {
        if (typeof element.yield == "number" && element.yield <= 0) {
          inValidRows.push("Row No : " + (element.__rowNum__ + 1) + " - Invalid Yield");
        }
        if (typeof element.price == "number" && element.price <= 0) {
          inValidRows.push("Row No : " + (element.__rowNum__ + 1) + " - Invalid Price");
        }
      }
      if (typeof element.amount != "number" && typeof element.quantity != "number") {
        inValidRows.push("Row No : " + (element.__rowNum__ + 1) + " - Input either Amount or Quantity");
      }
      else {
        if (typeof element.amount == "number" && element.amount <= 0) {
          inValidRows.push("Row No : " + (element.__rowNum__ + 1) + " - Invalid Amount");
        }
        if (typeof element.quantity == "number" && element.quantity <= 0) {
          inValidRows.push("Row No : " + (element.__rowNum__ + 1) + " - Invalid Quantity");
        }
      }
    });
    if (inValidRows.length > 0) {
      setInvalidRows(inValidRows);
      return false;
    }
    return true;
  }

  function calculateFields(bond: any, inputRow: any) {
    var orderObject: any = null;

    let currentDate = new Date(), yld = 0, price = 0, amount = 0, doCalc = false, isValid = true;

    if (inputRow.yield == undefined || inputRow.yield <= 0) { //if yield is not given calculate from price
      doCalc = true;
    }
    else {
      yld = inputRow.yield;
    }
    if (inputRow.amount == undefined || inputRow.amount <= 0) { //if amount is not given calculate
      if (inputRow.price == undefined || inputRow.price <= 0) { //if price is not given calculate from yield
        doCalc = true;
      }
      else {
        amount = inputRow.price * inputRow.quantity * (bond.faceValue / 100); //price is per hundred
      }
    }
    else {
      amount = inputRow.amount;
    }
    Moment.locale("en");
    let maturitydate = Moment(bond.Head.DateOfMaturity).format("yyyy-MM-DD");
    //poor way to check for perp
    if(new Date(maturitydate) >= new Date("2121/12/31")){
      maturitydate = Moment(bond.callDate).format("yyyy-MM-DD");
    }
    
    if(doCalc && (bond.Head.Coupon != undefined) && (bond.frequencyOfInterestPayment != undefined) && (new Date(maturitydate) > currentDate)){
      let calc = bondCalculator({
        settlement: currentDate, //always settlement date is current Date
        maturity: maturitydate, //maturity date.
        rate: bond.Head.Coupon / 100, // e.g. here is the actual value 2.625% we
        redemption: 100, //this.props.BuyOrSaleBondDetails.faceValue
        frequency: bond.frequencyOfInterestPayment, // 1=Annual, 2=Semiannual, 4=Quarterly
        convention: "ACTUAL/ACTUAL",
      });
      if (inputRow.yield != undefined && inputRow.yield > 0) {
        price = calc.price(inputRow.yield / 100);
        if (isNaN(price)) {
          isValid = false;
        }
        else {
          amount = inputRow.quantity * price * (bond.faceValue / 100);
        }
      }
      else {
        yld = calc.yield(inputRow.price) * 100;
        if (isNaN(yld)) {
          isValid = false;
        }
      }
    }
    else {
      isValid = false;
    }

    if (isValid) {
      orderObject = {
        "isinNo": bond.isinNo,
        /*
        "issuerShortName": bond.Head.BondName,
        "faceValue": bond.faceValue,
        "dateOfMaturity": bond.Head.DateOfMaturity,
        "couponRate": bond.Head.Coupon,
        "frequencyOfInterestPayment": bond.frequencyOfInterestPayment,
        "creditRating": bond.Head.Rating,
        "flags": bond.Tail.flags,
        "callDate": bond.callDate,
        */
        "yield": yld.toFixed(2),
        "price": price.toFixed(2),
        "quantity": inputRow.quantity,
        "isActive": true,
        "isBuy": inputRow.isBuy,
        "amount": amount.toFixed(2),
        "userName": userData.userName,
        "orderDate": Date.now()
      };
    }

    return orderObject;
  }
  function populateOrders(inputRows: any[]) {
    let inValidRows: string[] = [];
    let isinArray: string[] = [];

    //getting one by one isin and push in array.

    inputRows.forEach((element: any) => {
      isinArray.push(element.isinNo);
    });

    //to verify ISIN no in BE
    axios({
      method: "POST",
      data: { isinNo: isinArray },
      withCredentials: true,
      url:
        env.API_INFRA_MODULE +
        `/bondManagement/getBondsByIsinNo`,
    })
      .then((result) => {
        let found: any[] = [];

        inputRows.forEach((element: any) => {
          let bond = result.data.find((item: any) => item.isinNo == element.isinNo)

          if (typeof bond != "undefined") {
            let order = calculateFields(bond, element);

            if (order != null) {
              found.push(order)
            } else {
              inValidRows.push(" No Information for ISIN - " + element.isinNo)
            }
          } else {
            inValidRows.push(" No Information for ISIN - " + element.isinNo)
          }
        });//foreach end


        if (inValidRows.length > 0) {
          setInvalidRows(inValidRows);
          return false;
        }


        if (found) {
          setOrderJosnArray(found);
          return true;
        }
      })
      .catch((err) => {
        setInvalidRows(["Error" + err]);
        return false;
      });
  }//end of function

  //on file choice this below function is call 
  function handleFileChange(e: any) {
    //checking is file selected 
    if (!e || !e.target || !e.target.files || e.target.files.length === 0) {
      alert("Please select CSV file.");
      return;
    }
    const file = e.target.files[0];
    if (file.name.substring(file.name.lastIndexOf('.') + 1).toLowerCase() != 'csv') {
      alert("Please select CSV file.");
      return;
    }
   
    setDisable(true);
    let workBook: xlsx.WorkBook | null;
    let jsonData = null;
    const reader = new FileReader();

    reader.onload = async (event) => {
      let workBook = xlsx.read(reader.result, { type: "binary" });
      let jsonData = workBook.SheetNames.reduce((initial: any, name: any) => {
        if (workBook != null) {
          initial[name] = xlsx.utils.sheet_to_json(workBook.Sheets[name]);
        }
        return initial;
      }, {});
      let jsonDataToSave = jsonData["Sheet1"];

      if (jsonDataToSave != null && jsonDataToSave.length > 0) {

        (validateRows(jsonDataToSave) && populateOrders(jsonDataToSave));

      }
    };
    reader.readAsBinaryString(file);
    setDisable(false);
  }

  //to retry again if any error occurs for any invalid
  function toRetryUploadAgain() {
    setOrderJosnArray([]);
    setInvalidRows([])
    setDisable(false)
  }

  //to reset on close button
  function resetAndCloseModal() {

    props.ModalHideShow(false);
    setOrderJosnArray([]);
    setInvalidRows([])
    setDisable(false)

  }
  //try again button.
  return (

    <Modal visible={props.isHide}>

      <Card style={styles.card}>
        <Card.Content
          style={{ flexDirection: "row", marginLeft: 15, marginRight: 15 }}
        >
          <Title
            style={{
              flex: 1,
              fontWeight: "500",
              letterSpacing: -0.475472,
              fontSize: 28,
              lineHeight: 34,
            }}
          >
            {/* {invalidRows.length}-{orderJosnArray.length}-{disable ? 't' : 'f'} */}
            {(invalidRows.length > 0) ? <Text>Bulk Upload Orders - Error(s)</Text> : <Text>Bulk Upload Orders</Text>}
          </Title>
          <IconButton
            color="#75737C"
            style={{
              width: 15,
              height: 15,
              alignSelf: "flex-end",
              marginBottom: 15,
            }}
            onPress={() => resetAndCloseModal()}
            icon={require("../../../../assets/close.svg")}
          />
        </Card.Content>
        <View
          style={{
            width: "100%",
            height: 2,
            backgroundColor: "#DEE6ED",
            marginTop: 5,
          }}
        />
        {(invalidRows.length == 0 && orderJosnArray.length > 0) ?
          <View style={{ height: 150, maxHeight: 160, minHeight: 100 }}><Text style={{ fontFamily: "Inter", fontSize: 15, fontWeight: "600", alignSelf: "center", marginBottom: "2%", marginVertical: 25 }}>
            Ready To Upload data.</Text></View>
          : (disable) ?

            <View style={{ height: 150, maxHeight: 160, minHeight: 100 }}><ActivityIndicator size="large" color="#00ff00" style={{ marginTop: 10, marginVertical: 25 }} />
              <Text style={{ fontFamily: "Inter", fontSize: 15, fontWeight: "600", alignSelf: "center", marginBottom: "2%" }}>Please Wait..</Text></View>
            :
            <View style={{ height: 150, maxHeight: 160, minHeight: 100 }}>
              {/**
           * here we check if error is found show errors else dont show any thing.
           */}
              {(invalidRows.length > 0) ?
                <View style={{ flex: 1, marginTop: 5, }}>
                  <Text style={{ fontFamily: "Inter", fontSize: 15, fontWeight: "600", marginLeft: "2%", marginBottom: "2%" }}>Following record(s) contain invalid data.</Text>
                  <ScrollView style={{ padding: 5 }}>

                    <FlatList
                      data={invalidRows}
                      renderItem={(item) =>
                        <Text style={{ fontFamily: "Inter", fontSize: 15, fontWeight: "200", marginLeft: "5%" }}>{item.item}</Text>
                      } />

                  </ScrollView>


                  <View style={{ padding: 8, justifyContent: 'flex-end', flex: 1, flexDirection: "row", alignContent: 'flex-end', alignItems: "flex-end" }}>
                    <Button
                      mode={"contained"}
                      style={styles.submitButton}
                      uppercase={false}
                      onPress={() => toRetryUploadAgain()}
                    >TryAgain</Button>
                  </View>
                </View> : <View
                  style={{
                    flexDirection: "row",
                    alignContent: "center",
                    justifyContent: "center",
                  }}
                >

                  <View
                    style={{
                      marginBottom: "5%",
                      marginTop: "5%",
                      alignItems: "center",
                      justifyContent: "center",
                      flex: 5,
                    }}
                  >
                    <Text style={styles.downlodFileTitle}>Download Template</Text>
                    <View style={{ alignContent: "center", justifyContent: "center" }}>
                      <TouchableOpacity
                        style={styles.downloadImage}
                        onPress={() => download.download()}
                      >
                        <Image
                          style={styles.downloadImage}
                          resizeMode="center"
                          source={require("../../../../assets/csv_file.svg")}
                        />
                      </TouchableOpacity>
                    </View>
                  </View>
                  <View style={styles.separatorView} />
                  <View
                    style={{
                      alignItems: "center",
                      justifyContent: "center",
                      flex: 5,
                      margin: 5,
                    }}
                  >
                    <Text style={styles.downlodFileTitle}>Import .csv file</Text>
                    <View
                      style={{
                        alignItems: "center",
                        justifyContent: "center",
                        marginTop: "10%",
                        marginLeft: "25%",
                      }}
                    >
                      <input
                        type="file"
                        id="file"
                        accept=".csv"
                        onChange={(e) => handleFileChange(e)}
                      />
                    </View>
                  </View>
                </View>
              }
            </View>}
        <View
          style={{
            width: "100%",
            height: 2,
            backgroundColor: "#DEE6ED",
            marginTop: 5,
          }}
        />
        {/**
         * here we hide card.Actions layout if error found.
         */}
        {/* start of main template */}
        {(invalidRows.length > 0) ? null : <Card.Actions style={styles.cardAction}>
          <Text
            style={{ fontFamily: "Inter", fontSize: 15, fontWeight: "200" }}
          >
            Download CSV Template for bulk import of{" "}
            <Text
              style={{
                fontWeight: "300",
                fontFamily: "Inter",
                fontSize: 18,
                color: "#27AE60",
              }}
            >
              Order Book.
            </Text>
            Fill correct data and upload file.
          </Text>
          <Button
            mode={"contained"}
            disabled={(invalidRows.length == 0 && (orderJosnArray.length == 0 || disable)) ? true : false}
            style={(invalidRows.length == 0 && (orderJosnArray.length == 0 || disable)) ? styles.submitButtonDisable : styles.submitButton}
            uppercase={false}
            onPress={() => handleImportNow()}
          >
            Import Now
          </Button>
        </Card.Actions>

        }
        {/* end of main template */}
      </Card>
    </Modal>



  );
}

const styles = StyleSheet.create({
  card: {
    marginLeft: "30%",
    marginTop: "5%",
    marginRight: "30%",
    marginBottom: "5%",
  },
  cardAction: {
    alignContent: "flex-end",
    justifyContent: "flex-end",
  },
  submitButton: {
    width: 150,
    height: 45,
    paddingTop: 5,
    backgroundColor: "#27AE60",
    color: "#FFFFFF",
    letterSpacing: -0.271698,
    fontSize: 16,
  }, submitButtonDisable: {
    width: 150,
    height: 45,
    paddingTop: 5,
    backgroundColor: "#DEDEDE",
    color: "#C0C0C0",
    letterSpacing: -0.271698,
    fontSize: 16,
  }
  ,
  downlodFileTitle: {
    fontFamily: "Inter",
    fontSize: 20,
    fontWeight: "600",
    margin: 5,
  },
  downloadImage: {
    height: 50,
    width: 50,
  },
  separatorView: {
    height: "100%",
    width: 1,
    backgroundColor: "#DEE6ED",
  },
  underline: { textDecorationLine: 'underline', color: "rgb(39, 174, 96)" }
});