import React from "react";

import {
  Text,
  StyleSheet,
  View,
  FlatList,
  ScrollView,
} from "react-native";

import { IconButton, Card } from "react-native-paper";
import axios from "axios";
import { env } from "../../../../config/systemConfigs";
import { StackActions } from "@react-navigation/native";
import {formatAmount, formatMaturityDate, formatCoupon} from "../../../../shared/services/utilServices";

const IntrestedUser = (props: any) => {
  let orderObject: any = JSON.parse(props.route.params);

  const [intrestedUserData, setIntrestedUserData] = React.useState([ { _id: "", firstName: "", lastName: "", email: "", phoneNo: "", }, ]);
  React.useEffect(() => {
    //here we call api for user detail info.
    getUserDetails();
  }, [1]);

  function getUserDetails() {
    let intrestedUser:any = orderObject.interestedUsers.map((element:any)=>{
      return element.userName;
    });

    if (orderObject.interestedUsers.length > 0) {
      axios({
        method: "POST",
        withCredentials: true,
        data:  intrestedUser ,
        url: env.API_INFRA_MODULE + `/userManagement/getIntrestedUserDetails`,
      })
        .then((result) => {
          //seting the bonds array in the bonds
          if (result.status === 200) {
            setIntrestedUserData(result.data);
          }
        })
        .catch((err) => {
          alert("Oops, something went wrong. Please try again!");
          console.log("Error console:", err);
        });
    }
  }

  return (
    <View style={styles.container}>
      <View
        style={{flexDirection: "row", alignSelf: "flex-start", justifyContent: "center", marginLeft: "130px", }} >
        <IconButton
          style={{height: 20, width: 15, paddingLeft: 10, }}
          icon={require("../../../../assets/backarrow.png")}
          onPress={() =>
            props.navigation.dispatch(StackActions.replace("orderbook"))
          }
        />
        <Text style={{fontStyle: "normal", fontWeight: "600", fontSize: 18, textAlign: "left", marginTop: 2, }} > Back </Text>
      </View>
      <Card style={styles.card_container}>
        <View>
          <View style={styles.row_one}>
            <View style={styles.row_one_section_one}>
              <View style={styles.row_one_section_one_part_one}>
                <Text style={{ fontFamily: "Inter", fontStyle: "normal", fontWeight: "600", fontSize: 15, color: "#454F5B", textAlign: "left", marginTop: 15, }} >
                  <Text>{orderObject.issuerShortName + " "}</Text>
                  {orderObject.couponRate === undefined ? ("-") : ( <Text>{formatCoupon(orderObject.couponRate)}</Text> )}
                  <Text> {formatMaturityDate(orderObject.dateOfMaturity, true)} </Text>
                </Text>
                <View style={styles.cell}>
                  <Text style={styles.BondNameTextSubTitel}>ISIN No.</Text>{" "}
                  <Text style={styles.title}> {orderObject.isinNo === undefined ? "-" : orderObject.isinNo} </Text>
                </View>
                <View style={styles.cell}>
                  <Text style={styles.BondNameTextSubTitel}>Rating</Text>{" "}
                  <Text style={styles.title}> {orderObject.creditRating === undefined ? "-" : orderObject.creditRating} </Text>
                </View>
                <View style={styles.cell}>
                  <Text style={styles.BondNameTextSubTitel}>Coupon</Text>{" "}
                  <Text style={styles.title}> {orderObject.couponRate === undefined ? "-" : formatCoupon(orderObject.couponRate)}
                  </Text>
                </View>
                <View style={styles.cell}>
                  <Text style={styles.BondNameTextSubTitel}>YTM</Text>{" "}
                  <Text style={styles.title}> {orderObject.yield === undefined ? "-" : orderObject.yield + "%"}
                  </Text>
                </View>
                <View style={styles.cell}>
                  <Text style={styles.BondNameTextSubTitel}>Amount</Text>{" "}
                  <Text style={styles.title}> {orderObject.amount === undefined ? "-" : formatAmount(orderObject.amount)} </Text>
                </View>
                <View style={styles.cell}>
                  <Text style={styles.BondNameTextSubTitel}>Maturity Date</Text>{" "}
                  <Text style={styles.title}> {formatMaturityDate(orderObject.dateOfMaturity, true)} </Text>
                </View>
              </View>
            </View>
          </View>

          <View style={{ height: 1, width: "100%", backgroundColor: "#DFE3E8" }} />

          {/* second */}
          <View style={styles.row_two}>
            <ScrollView style={{ height: 350 }}>
              {intrestedUserData.length != 0 ? (
                <View>
                  <View style={{ width: "100%", flexDirection: "row", marginBottom: 5, }} >
                    <Text style={{ fontFamily: "Inter", fontStyle: "normal", fontSize: 15, fontWeight: "600", marginLeft: "5%", width: "3%", }} >
                      No.
                    </Text>
                    <Text style={styles.userInfoTitle}>First Name</Text>
                    <Text style={styles.userInfoTitle}>Last Name</Text>
                    <Text style={styles.userMobileInfoTitle}>Phone</Text>
                    <Text style={styles.userEmailInfoTitle}>Email</Text>
                  </View>
                  <View style={{ height: 1, width: "100%", backgroundColor: "#DFE3E8", }} />
                  <FlatList
                    data={intrestedUserData}
                    renderItem={({ item, index }) => (
                      <View>
                        <View
                          style={{ width: "100%", flexDirection: "row", marginBottom: 3, }} >
                          <Text style={{ fontFamily: "Inter", fontSize: 15, fontWeight: "200", marginLeft: "5%", width: "3%", }} >
                            {`${index + 1}`}
                          </Text>
                          <Text style={styles.uesrInfoValue}> {`${item.firstName}`} </Text>
                          <Text style={styles.uesrInfoValue}> {`${item.lastName}`} </Text>
                          <Text style={styles.userMobileInfoValue}> {item.phoneNo === "false" ? "-" : item.phoneNo} </Text>
                          <Text style={styles.userEmailInfoValue}> {`${item.email}`} </Text>
                        </View>
                        <View style={{height: 1, width: "100%", backgroundColor: "#DFE3E8", }} />
                      </View>
                    )}
                  />
                </View>
              ) : (
                <Text style={{ alignSelf: "center" }}> No interested users. </Text>
              )}
            </ScrollView>
          </View>
        </View>
      </Card>
    </View>
  );
};
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#F2F2F2",
    alignItems: "center",
    justifyContent: "center",
  },
  card_container: {
    display: "flex",
    backgroundColor: "#FFFFFF",
    borderRadius: 4,
    flexDirection: "column",
    width: "80%",
    paddingLeft: "30px",
    paddingRight: "30px",
    elevation: 5,
  },
  row_one: {
    flexDirection: "row",
    alignItems: "center",
    marginTop: "25px",
    marginBottom: "25px",
  },
  row_one_section_one: {
    flex: 1,
    flexDirection: "column",
    justifyContent: "flex-start",
  },
  row_one_section_one_part_one: {
    marginLeft: "20px",
    flexDirection: "row",
    justifyContent: "flex-start",
  },
  row_one_section_one_part_two: {
    marginTop: "10px",
    marginLeft: "20px",
    flexDirection: "row",
    justifyContent: "flex-start",
  },
  row_one_section_two: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
  },
  row_one_section_two_button: {
    marginLeft: "10px",
    marginRight: "10px",
    flexDirection: "row",
    alignItems: "center",
  },
  row_one_section_two_button_text: {
    fontSize: 16,
    lineHeight: 19,
    textAlign: "right",
    color: "#5C6AC4",
  },
  row_two: {
    height: 350,
    marginTop: "15px",
    flexDirection: "row",
    justifyContent: "space-around",
    alignItems: "center",
    marginBottom: "25px",
  },
  row_two_section: {
    marginLeft: "",
  },
  row_three: {
    flexDirection: "row",
    justifyContent: "space-around",
    alignItems: "center",
    marginTop: "10px",
    marginBottom: "25px",
  },
  row_three_section_one: {
    marginLeft: "20px",
    flex: 1,
    // alignItems: "flex-start",
    flexDirection: "row",
  },
  // row_three_section_two: {
  //   flex: "1",
  //   justifyContent: "flex-start",
  // },
  row_three_section_one_text: {
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: 18,
    color: "#454F5B",
    textAlign: "left",
    marginTop: "10px",
  },
  row_three_section_two_text: {
    fontWeight: "500",
    fontSize: 18,
    lineHeight: 22,
    color: "#454F5B",
    textAlign: "right",
    marginLeft: "80px",
    marginTop: "10px",
  },

  row_four: {
    flexDirection: "row",
    marginTop: "15px",
    marginBottom: "25px",
  },
  row_four_section: {
    marginTop: "10px",
    marginLeft: "20px",
    textAlignVertical: "center",
  },
  row_four_header: {
    fontStyle: "normal",
    fontWeight: "600",
    fontSize: 28,
    color: "#454F5B",
  },
  row_four_value: {
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: 18,
    lineHeight: 36,
    letterSpacing: -0.271698,
    color: "#637381",
    textAlign: "left",
    marginTop: 10,
  },
  row_four_value_text: {
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: 18,
    lineHeight: 22,
    color: "#454F5B",
    textAlign: "right",
    marginLeft: "50px",
    marginTop: "20px",
  },
  cell: {
    height: "10%",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "#FFF",
    flex: 0.2,
  },
  BondNameTextSubTitel: {
    color: "#637381",
  },
  title: {
    fontFamily: "Inter",
    fontStyle: "normal",
    textAlign: "right",
    color: "#454F5B",
    fontWeight: "600",
  },
  userInfoTitle: {
    fontFamily: "Inter",
    fontStyle: "normal",
    fontSize: 15,
    fontWeight: "600",
    marginLeft: "5%",
    width: "15%",
  },
  uesrInfoValue: {
    fontFamily: "Inter",
    fontSize: 15,
    fontWeight: "200",
    marginLeft: "5%",
    width: "15%",
    marginTop: 3,
  },
  userEmailInfoTitle: {
    fontFamily: "Inter",
    fontStyle: "normal",
    fontSize: 15,
    fontWeight: "600",
    marginLeft: "5%",
    width: "40%",
  },
  userEmailInfoValue: {
    fontFamily: "Inter",
    fontSize: 15,
    fontWeight: "200",
    marginLeft: "5%",
    width: "40%",
    marginTop: 3,
  },
  userMobileInfoTitle: {
    fontFamily: "Inter",
    fontStyle: "normal",
    fontSize: 15,
    fontWeight: "600",
    marginLeft: "5%",
    width: "10%",
  },
  userMobileInfoValue: {
    fontFamily: "Inter",
    fontSize: 15,
    fontWeight: "200",
    marginLeft: "5%",
    width: "10%",
    marginTop: 3,
  },
});
export default IntrestedUser;
