import React from "react";
import { Image, View, StyleSheet } from "react-native";
import {
  Button,
  Card,
  Text,
} from "react-native-paper";
import {
  Modal,
  Title,
  IconButton,
} from "react-native-paper";
import axios from "axios";
// import { FacebookLoginButton } from "../../shared/component/social-buttons/FacebookLoginButton.web";
// import { GoogleLogInButton } from "../../shared/component/social-buttons/GoogleLoginButton.web";
//import { SocialMedias } from "../../shared/component/social-buttons/SocialMedias.web";

import { env } from "../../config/systemConfigs";
import {
  TextField,
  makeStyles,
  createStyles,
  Theme,
} from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexWrap: "wrap",
    },
    margin: {
      margin: theme.spacing(1),
    },
    height: {
      height: 10,
    },
  })
);

//this is Login Function component
export const LoginScreen = ({ route, navigation }: any, props: any) => {

  //this is visibitity state of snackbar
  const [visible, setVisible] = React.useState(false);
  //this is userName state
  const [userName, setUserName] = React.useState("");
  //this is passWord firstName state
  const [passWord, setPassword] = React.useState("");
  //this is message firstName state
  const [message, setMessage] = React.useState("");

  const [onValidateEmail, setonValidateEmail] = React.useState(false);
  
  //to handle modal
  const [modalDisplay, setmodalDisplay] = React.useState(false);
  const classes = useStyles();
  const showMessage = (msg: string) => {
    setMessage(msg);
    onToggleSnackBar();
  };
  //this is snackbar visibility show and hide function.
  const onToggleSnackBar = () => setVisible(!visible);

  //to resend email for verification
  const toResendEmailVerification = () => {

    axios({
      method: "POST",
      data: {
        userName,
        hash: btoa(userName),
      },
      url: env.API_INFRA_MODULE + '/auth/resend',
    })
      .then((result) => {
        //to hide modal
        setmodalDisplay(false)
        window.location.reload()
      })
  }


  //createing json object and send to server
  const onLogin = () => {
    axios({
      method: "POST",
      data: {
        email: userName,
      },
      withCredentials: true,
      url: `${env.API_INFRA_MODULE}/auth/signin`,
    })
      .then((result) => {
        //email address exists
        if (result.data.status === "403") {
          showMessage(result.data.message);
        }
        //password exists then go ahead
        if (result.data.status === "200" && result.data.message === "Password exists") {
          //to  navigate to password
          let params;
          if(route.params && route.params.redirect){
            params = {id:userName, redirect: route.params.redirect}
          }
          else{
            params = {id:userName}
          }
          navigation.navigate("pwd",params);
        }
        //pasword doesnt exists asking for to set it
        if (result.data.status === "200" && result.data.message === "Password does not exists") {
          //to  navigate to password
          const properties = {
            id: userName
          }
        
          navigation.navigate("npwd", properties);
          //todo
        }
        //user has not verified email
        if (result.data.status === "200" && result.data.message === "unverified") {
          setmodalDisplay(true)
        }
      })
      .catch((err) => {
        console.log("Error console:", err);
      });

  };



  const onEnterPress = (event: any) => {
    // console.log(JSON.stringify(event.nativeEvent.key));
    if (event.keyCode == 13) {
      onLogin();
    }
  };

  return (
    <View style={styles.container}>
      <Card style={styles.card}>

        <Card.Content style={styles.card_content}>
          <Image
            style={{
              width: "60%",
              height: "25%",
              resizeMode: "center",
              marginLeft: "22%",
              // marginBottom:"5%"
            }}
            source={require("../../assets/icons/INRBond-logo.png")}
          />
          <Text style={styles.titleText}>Sign In</Text>
          <Text style={{
            fontSize: 20,
            alignSelf: "center",
            marginTop: "15%"
          }}></Text>
          <TextField
            className={classes.margin}
            error={onValidateEmail}
            style={{ marginTop: 10 }}
            label="Email"
            type="Email"
            variant="outlined"
            helperText={
              onValidateEmail ? "Email should be minimum 6 characters." : ""
            }
            onFocus={() => setonValidateEmail(false)}
            onChange={(event) => setUserName(event.target.value.toLowerCase())}
            onKeyDown={(event) => onEnterPress(event)}
          />

          <Button
            style={{ marginTop: "10px" }}
            mode="contained"
            color="#27AE60"
            uppercase={false}
            contentStyle={{ height: "50px" }}
            onPress={() => onLogin()}
            disabled={userName == "" ? true : false}
          >
            {/* disabled={passWord == "" || userName == "" ? true : false} */}
            <Text style={{ color: "white", fontWeight: "bold" }}>
              Login with Email
            </Text>
          </Button>
          <View
            style={{
              marginTop: "2%",
              flexDirection: "row",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <Text style={{ color: "red" }}>{message}</Text>
          </View>

          <View
            style={{
              flexDirection: "row",
              marginTop: 15,
              justifyContent: "center",
            }}
          >

            <Text style={{ fontSize: 13, marginTop: 2.5 }}>
              Dont have an account?{" "}
            </Text>
            <Text
              style={{ fontSize: 18, color: "#5C6AC4" }}
              onPress={() => {
                navigation.navigate("subscribe");
              }}
            >
              Sign Up
            </Text>
          </View>

        </Card.Content>
      </Card>

      {/* Modal will be open when user is unverified and tries to login */}
      <Modal visible={modalDisplay}>
        <Card style={styles.cardModal}>
          <Card.Content>
            <View style={{ flexDirection: "row" }}>
              <Title
                style={{
                  flex: 1,
                  fontWeight: "500",
                  letterSpacing: -0.475472,
                  fontSize: 28,
                  lineHeight: 34,
                }}>

              </Title>
              <View style={{ flex: 0.1, marginLeft: 150, marginRight: 5 }}>
                <IconButton
                  color="#75737C"
                  style={{ width: 15, height: 15, alignSelf: "flex-end" }}
                  onPress={() => setmodalDisplay(false)}
                  icon={require("../../assets/close.svg")}
                />
              </View>
            </View>
            <View
              style={{
                height: 2,
                width: "100%",
                marginTop: 5,
                marginBottom: 5,
              }}
            />
          </Card.Content>
          <View style={styles.cardView}>
            <View style={{ flex: 1 }}>
              <Text style={styles.cardLabelText}>An email has been sent to your email address for verification. Kindly verify to continue login.
              &nbsp;
             <Text
                  style={{ fontSize: 18, color: "#5C6AC4" }}
                  onPress={() => {
                    toResendEmailVerification()
                  }}
                >
                  Click Here
            </Text>&nbsp;
             to generate new verification email.</Text>
            </View>

          </View>
          <View
            style={{
              height: 2,
              width: "100%",
              marginTop: 5,
              marginBottom: 5,
            }}
          />
        </Card>
      </Modal>


    </View>


  );
};
//this is styleSheet
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#F2F2F2",
    alignItems: "center",
    justifyContent: "center",
  },
  card: {
    marginTop: 5,
    backgroundColor: "#FCFCFC",
    padding: "2vh",
    elevation: 10,
  },
  card_content: {
    backgroundColor: "#fff",
    justifyContent: "center",
    textAlignVertical: "center",
    height: "70vh",
    width: "50vh",
    verticalAlign: "center",
  },
  titleText: {
    fontSize: 20,
    fontWeight: "bold",
    alignSelf: "center",
  },
  textInput: {
    marginTop: 10,
    fontWeight: 500,
    fontSize: 22,
    height: 50,
  },

  // button css
  button: {
    backgroundColor: "#F9FAFB",
    color: "black",
    marginTop: "10px",
    tintColor: "transperent",
  },
  buttonText: {
    height: "50px",
    color: "black",
    tintColor: "transparent",
  },
  circle: {
    width: 60,
    height: 60,
    borderRadius: 100 / 2,
    borderColor: "#DCE1FF",
    borderWidth: 2,
    alignSelf: "center",
    marginBottom: 10,
    alignContent: "center",
    justifyContent: "center",
  },
  avetarText: {
    fontSize: 25,
    fontWeight: "bold",
    color: "#5C6AC4",
    marginTop: 2.5,
    marginRight: 2.5,
    textAlign: "center",
  },

  cardModal: {
    marginLeft: "30%",
    marginRight: "30%",
    marginBottom: "33%",
  },
  cardAction: {
    alignContent: "center",
    justifyContent: "center",
    padding: 12
  },
  cardView: {
    height: 50,
    flexDirection: "row",
    alignContent: "space-around",
    justifyContent: "space-evenly",
    marginBottom: 10,
  },
  cardLabelText: {
    fontSize: 18,
    letterSpacing: -0.30566,
    lineHeight: 22,
    marginLeft: 20,
  },
  submitButton: {
    width: 160,
    height: 45,
    paddingTop: 5,
    backgroundColor: "#27AE60",
    color: "#FFFFFF",
    letterSpacing: -0.271698,
    fontSize: 16,
    marginTop: 15,
  },
});
