import React from "react";
import {
  View,
  FlatList,
  StyleSheet,
} from "react-native";
import {dateDiffInYrs, formatMaturityDate, formatCoupon} from "../../../../shared/services/utilServices";
import { Chart } from "react-google-charts";
import { TableRowComponent } from "../../../../shared/component/ListComponent/TableRowComponent";

const BondGraph = (props: any) => {
  const [selectedBond, setSelectedBond] = React.useState(null);

  function getChartValue(elemnt: any) {
    let chartValue: any[] = [
      ["Yrs", "YTM", { role: "tooltip", type: "string", p: { html: true } }],
    ];

    elemnt.map((bond: any) => {
      if(bond.Head.LTY != undefined && bond.Head.LTY != null && bond.Head.LTY != ""){
        let maturitydate = formatMaturityDate(bond.Head.DateOfMaturity, false);
        if(maturitydate == "PERP"){
          maturitydate = formatMaturityDate(bond.callDate, false);
        }
        let yrdif = dateDiffInYrs(maturitydate, new Date());
        chartValue.push([yrdif, JSON.parse(parseFloat(bond.Head.LTY).toFixed(2)),
          `<label style="font-family: Inter; font-style: normal; font-weight: 600; font-size: 20px; line-height: 24px;
          padding:15px; letter-spacing: -0.339623px">  ${bond.Head.BondName} ` 
          + formatCoupon(bond.Head.Coupon) + formatMaturityDate(bond.Head.DateOfMaturity, true) + `</label>`
        ]);
      }
    });
    return chartValue;
  }

  /* VK: Can we not simply get the rowIndex from chart data here and use the same to look into props.bondListWithSection? */
  function showBond(bondName: string) {
    var nameArray = bondName.split(" ");
    props.bondListWithSection.map((element: any) => {
      let maturitydate = (nameArray[3] == undefined) ? nameArray[2] : (nameArray[2] + " " + nameArray[3])
      if ((element.Head.BondName === nameArray[0]) && 
        ((Math.round(element.Head.Coupon*10000) === Math.round(parseFloat(nameArray[1].replace("%", ""))*10000))) && 
        (formatMaturityDate(element.Head.DateOfMaturity, true) == maturitydate)) {
          setSelectedBond(element);
      }
    });
  }

  return (
    <View style={{ marginLeft: "8%", marginRight: 80, backgroundColor: "#FFF", width: "85%", }} >
      {selectedBond != null ? (
        <View style={{ backgroundColor: "#FFF" }}>
            <FlatList
              contentContainerStyle={{ padding: 5 }}
              data={ [selectedBond] }
              renderItem={(bond: any) => (
                <TableRowComponent
                  bondObject={bond.item}
                  navigation={props.navigation}
                  manageWatchList={props.manageWatchList}
                  isWatchListItem={props.isWatchListItem}
                  showOrderModal={props.showModelOnparent}
                />
              )}
            />
        </View>
      ) : null}

      <View style={{ backgroundColor: "#FFF" }}>
        <Chart
          style={{ height: 500, width: "100%" }}
          chartType="ScatterChart"
          data={getChartValue(props.bondListWithSection)}
          chartEvents={[
            {
              eventName: "select",
              callback: ({ chartWrapper }) => {
                const chart = chartWrapper.getChart();
                const selection = chart.getSelection();

                if (selection.length === 1) {
                  const [selectedItem] = selection;
                  const dataTable: any = chartWrapper.getDataTable();
                  const { row, column } = selectedItem;

                  const bondName: string = dataTable.getValue(row, 2);
                  let posOne = bondName.search(">");
                  let str = bondName.substr(posOne + 1);
                  let posTwo = str.search("<");
                  let fullName = str.substr(0, posTwo);
                  showBond(fullName.trim());
                }
              },
            },
          ]}
          options={{
            // Material design options
            hAxis: { title: "Yrs" },
            vAxis: { title: "LTY" },
            tooltip: { isHtml: true },
            legend: "none",
          }}
        />
      </View>
    </View>
  );
};
const styles = StyleSheet.create({
  picker: {
    borderWidth: 1,
    fontWeight: "normal",
    fontSize: 18,
    lineHeight: 22,
    color: "#637381",
    paddingLeft: "5px",
    marginLeft: 15,
    height: 44,
    width: 227,
    borderColor: "#DFE3E8",
    borderRadius: 6,
    letterSpacing: -0.30566,
    backgroundColor: "#FFF",
  },
});
export default BondGraph;
