import React from "react";
import { View, StyleSheet, Image, } from "react-native";
import { Button, Card, Text } from "react-native-paper";
import axios from "axios";
import { env } from "../../config/systemConfigs";
import { createStyles, makeStyles, Theme, TextField } from "@material-ui/core";


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexWrap: "wrap",
    },
    margin: {
      margin: theme.spacing(0.5),
    },
    height: {
      height: 10,
    },
  })
);
//this is Registration function component
export const Registration = ({ route, navigation }: any) => {
  
  //this is user firstName state
  const [FirstName, setFirstName] = React.useState("");
  const [FirstNameValid, setFirstNameValid] = React.useState(false);
  //this is user LastName state
  const [LastName, setLastName] = React.useState("");
  const [LastNameValid, setLastNameValid] = React.useState(false);
  //this is user Email state
  const [Email, setEmail] = React.useState("");
  const [EmailValid, setEmailValid] = React.useState(false);
  //this is user PhoneNo state
  const [PhoneNo, setPhoneNo] = React.useState("");
  const [PhoneNoValid, setPhoneNoValid] = React.useState(false);
  //to disable button
  const [disableButton, setdisableButton] = React.useState(false);

  const classes = useStyles();

  //createing json object and send to server
  const onSubmit = () => {

    setdisableButton(true);
    if (!route.params) {
      alert("Please select the subscription plan.");
      window.location.href = `${env.API_INRB_FE}/subscribe`;
      setdisableButton(false);
      return false;
    }

    //checking mandatory fields befire submitting
    if (FirstName == "") {
      setFirstNameValid(true);
      setdisableButton(false);
      return false;
    }
    if (LastName == "") {
      setLastNameValid(true);
      setdisableButton(false);
      return false;
    }
    if (PhoneNo == "") {
      setPhoneNoValid(true);
      setdisableButton(false);
      return false;
    }
    //here we do email validation for registration
    let emailMatchString = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
    if (!Email.match(emailMatchString)) {
      setEmailValid(true);
      return false;
    }

    let redirectTo = "", typeOfService = "";
    for(var i = 0; i < env.SUBSCRIPTIONS.length; i++){
      if(env.SUBSCRIPTIONS[i].title === route.params.plan){
        redirectTo = env.SUBSCRIPTIONS[i].link;
        typeOfService = "signup";
        break;
      }
    }
    if(redirectTo === ""){
      for(var i = 0; i < env.COURSES.length; i++){
        if(env.COURSES[i].title === route.params.plan){
          redirectTo = env.COURSES[i].link;
          typeOfService = "training_program";
          break;
        }
      }
    }
  //if any paid plan is selected
    if (redirectTo !== env.SUBSCRIPTIONS[0].link) {
      //to store data in instamojo transaction details    
      var paymentTran = {
          email: Email,
          TypeOfService: typeOfService,
          created: Date.now(),
          subscription: {plan: route.params.plan},
        }
      axios.post(`${env.API_INFRA_MODULE}/payments/paymentTransaction`, paymentTran)
        .then((response: any) => {
          if (response.data.status == 200){
            registerUser(redirectTo);
          }
          else{
            alert("Oops! Something went wrong. Please try again.");
            setdisableButton(false);
            console.log(response.data);
          }
        }).catch((err: any) => {
          alert("Oops! Something went wrong. Please try again.")
          setdisableButton(false);
          console.log("Error console:", err);
        });
    }
    else {    //if plan is free
      registerUser(redirectTo);
    }
  };

  function registerUser(routeURL: string) {
    //to save registrtaion fields  data
    var registerData = {
      firstName: FirstName,
      lastName: LastName,
      password: "",
      userName: (routeURL == env.SUBSCRIPTIONS[0].link) ? Email : '', //set userid only if no payment required...
      roleId: 2,
      email: Email,
      phoneNo: PhoneNo,
      isSocial: false,
      date: Date.now(),
      isVerified: "unverified",
      hash: btoa(Email),
    }
    
    axios  
      .post(`${env.API_INFRA_MODULE}/auth/registration`, registerData)
      .then((result: any) => {
        //here we check the status code and as per the code we show navigation
        if (result.status == 200) {
          if (routeURL == env.SUBSCRIPTIONS[0].link) {
            alert("Thank you for registration. We have sent you an email. Please check the same for next steps.");
          }
          else{
            alert("You will now be redirected to payments page. Upon sucessful payment, you will receive a welcome mail. Please check the same for next steps.");
          }
          window.location.href = routeURL
        }
        setdisableButton(false);     
      }).catch((err: any) => {
        console.log(err);
        if(JSON.stringify(err).includes("409")){
          alert("A user already exists with given details.");
        }
        else{
          alert("Oops! Something went wrong. Please try again.")
        }
        setdisableButton(false);     
      });
  } 

  //on enter hit button
  const onEnterPress = (event: any) => {
    if (event.nativeEvent.key == "Enter") {
      onSubmit();
    }
  };
  return (
    <View style={styles.container}>
      <Text style={styles.titleText}>Get Started</Text>
      <Card style={styles.card}>
        <Card.Content style={styles.card_content}>
          <View>
            <View style={{ width: "100%", justifyContent: "center", alignContent: "center", marginBottom: 25, }} >
              <Image resizeMode="contain" style={{ height: "50px", resizeMode: "center", }} source={require("../../assets/logo.svg")} />
            </View>
            <TextField
              className={classes.margin}
              error={FirstNameValid}
              style={{}}
              label="First Name"
              type="First Name"
              variant="outlined"
              helperText={ FirstNameValid ? "Please enter valid First Name." : "" }
              onFocus={() => setFirstNameValid(false)}
              onChange={(event) => setFirstName(event.target.value)}
              onKeyDown={(event) => onEnterPress(event)}
            />
            <TextField
              className={classes.margin}
              error={LastNameValid}
              style={{ marginTop: 10 }}
              label="Last Name"
              type="Last Name"
              variant="outlined"
              helperText={LastNameValid ? "Please enter valid Last Name." : ""}
              onFocus={() => setLastNameValid(false)}
              onChange={(event) => setLastName(event.target.value)}
              onKeyDown={(event) => onEnterPress(event)}
            />
            <TextField
              type="number"
              className={classes.margin}
              error={PhoneNoValid}
              style={{ marginTop: 10 }}
              label="Mobile No."
              variant="outlined"
              helperText={ PhoneNoValid ? "Please enter valid mobile number." : "" }
              onFocus={() => setPhoneNoValid(false)}
              onChange={(event) => setPhoneNo(event.target.value)}
              onKeyDown={(event) => onEnterPress(event)}
            />
            <TextField
              className={classes.margin}
              error={EmailValid}
              style={{ marginTop: 10 }}
              label="Email"
              type="Email"
              variant="outlined"
              helperText={EmailValid ? "Please enter valid Email id." : ""}
              onFocus={() => setEmailValid(false)}
              onChange={(event) => setEmail(event.target.value.toLowerCase())}
              onKeyDown={(event) => onEnterPress(event)}
            />
            <Button
              style={{ marginTop: "10px" }}
              mode="contained"
              color="#27AE60"
              uppercase={false}
              contentStyle={{ height: "50px" }}
              onPress={() => onSubmit()}
              disabled={disableButton ? true : false}
            >
              <Text style={{ color: "white", fontWeight: "bold" }}> Continue </Text>
            </Button>
          </View>
          <View
            style={{ flexDirection: "row", marginTop: 15, justifyContent: "center", }} >
            <Text style={{ fontSize: 13, marginTop: 2.5 }}> Already a user?{" "} </Text>
            <Text style={{ fontSize: 18, color: "#5C6AC4" }} onPress={() => { navigation.navigate("login"); }} > Sign In </Text>
          </View>
        </Card.Content>
      </Card>
    </View>
  );
};

//this is styleSheet

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#F2F2F2",
    alignItems: "center",
    justifyContent: "center",
  },
  card: {
    marginTop: 10,
    backgroundColor: "#FCFCFC",
    padding: "0vh",
    elevation: 10,
  },
  card_content: {
    backgroundColor: "#fff",
    justifyContent: "center",
    textAlignVertical: "center",
    height: "90vh",
    width: "50vh",
    verticalAlign: "center",
  },
  titleText: {
    fontSize: 20,
    fontWeight: "bold",
  },
  // button css
  button: {
    backgroundColor: "#F9FAFB",
    color: "black",
    marginTop: "10px",
    tintColor: "transperent",
  },
  buttonText: {
    height: "50px",
    color: "black",
    tintColor: "transparent",
  },
});
