import React from "react";
import {
  Text,
  StyleSheet,
  View,
  Image,
  FlatList,
  TouchableOpacity,
} from "react-native";
import { Card, IconButton, Button, DataTable } from "react-native-paper";
import {formatCoupon, formatMaturityDate} from '../../services/utilServices';

const BondCardView = (props: any) => {

  const [headerSwitch, setHeaderSwitch] = React.useState(false);
  const [footerShow, setFooterShow] = React.useState(false);

  const switchHeader = () => {
    setHeaderSwitch(!headerSwitch);
  };

  const nav = () => {
    if (props.bondObject.SpecialProduct == 'F') {
      props.navigation.navigate("FactorPortfolioDetails", props.bondObject);
    } else {
      props.navigation.navigate("bonddetails", { bondData: JSON.stringify(props.bondObject), inWatchList: props.isWatchListItem(props.bondObject)});
    }
  };

  return (
    <Card style={styles.bac_card}>
      {headerSwitch ? ( <CardHeader2 bondObject={props.bondObject} switchHeader={switchHeader} />
        ) : ( <CardHeader1 switchHeader={switchHeader} bondObject={props.bondObject} />
      )}
      <View style={{ width: "100%", position: "absolute", alignContent: "flex-end", paddingTop: 208, }} >
        <View style={{ flexDirection: "row", padding: 10, borderBottomLeftRadius: 16, borderBottomRightRadius: 16}}>
          <Text style={styles.containerTitle}><b>{props.bondObject.Tail.flags}</b></Text>
        </View>
      </View>

      <TouchableOpacity onFocus={() => setFooterShow(true)} onBlur={() => setFooterShow(false)} >
        <CardContent bondObject={props.bondObject.Body !== undefined ? props.bondObject.Body : null} />
      </TouchableOpacity>
      {footerShow ? (
        <View style={{ width: "100%", position: "absolute", alignContent: "flex-end", paddingTop: 208, }} >
          <View
            style={{ flexDirection: "row", backgroundColor: "rgba(80, 80, 107, 0.86)", padding: 10, borderWidth: 0.5,
              borderBottomLeftRadius: 16, borderBottomRightRadius: 16, justifyContent: "center", }}  >
            <Button
              icon={ props.isWatchListItem(props.bondObject) ? require("../../../assets/quit.svg") : require("../../../assets/addwithcircal.svg") }
              uppercase={false}
              color={"#FFF"}
              onPress={() => { props.manageWatchList(props.bondObject); }}
            >
              WatchList
            </Button>
            <Button
              style={{ backgroundColor: "#383E56", marginLeft: 15 }}
              uppercase={false}
              color={"#FFFFFF"}
              onPress={() => { nav(); }}
            >
              View
            </Button>
            <Button
              onPress={() => { props.showOrderModal(props.bondObject); }}
              style={{ backgroundColor: "#F95E68", marginLeft: 15 }}
              uppercase={false}
              color={"#FFFFFF"}
            >
                  Buy / Sell
            </Button>
          </View>
        </View>
      ) : null}
    </Card>
  );
};

const CardHeader1 = (props: any) => {

  return (
    <View style={styles.bac_card_header}>
      <View style={{ flex: 1 }}>
        <IconButton icon={require("../../../assets/leftArrow.png")} size={20} onPress={() => props.showHideHeader(false)} />
      </View>
      <View style={{ flex: 5 }}>
        <View style={{ flexDirection: "row", flexWrap: "nowrap", alignItems: "center", justifyContent: "space-between", }} >
          <Text style={styles.company_name}>
            {props.bondObject.Head.BondName}
          </Text>
          {props.bondObject.Head.PriceTrend != null && (
                ["U", "D", "V"].includes(props.bondObject.Head.PriceTrend)) ? (
                  <Image
                    style={{ width: 33, height: 22.79 }}
                    resizeMode={"contain"}
                    source={require(`../../../assets/image/${props.bondObject.Head.PriceTrend}.png`)}
                  />
              ) : (
                <View />
              )}
        </View>
        <View style={{ flexDirection: "row", flexWrap: "nowrap", alignItems: "center", justifyContent: "space-between", }} >
          <Text style={styles.date}>
            {formatMaturityDate(props.bondObject.Head.DateOfMaturity, false)}
          </Text>
          {props.bondObject.Head.LTY === undefined ? ( <Text style={styles.picker} /> ) : (
              <Text style={styles.picker}>LTY {props.bondObject.Head.LTY}</Text>
          )}
        </View>
      </View>
      <View>
        <IconButton style={{ height: 50, width: 0.9 }} icon={require("../../../assets/Line.png")} size={60} />
      </View>
      <View style={{ flex: 3 }}>
        <View style={{ flexDirection: "row", flexWrap: "nowrap", alignItems: "center", justifyContent: "space-between", }} >
          <Text style={styles.date}>Rating</Text>
          <Text style={styles.contentText}> {props.bondObject.Head.Rating} </Text>
        </View>
        <View style={{ flexDirection: "row", flexWrap: "nowrap", alignItems: "center", justifyContent: "space-between", }} >
          {props.bondObject.Head.Coupon === undefined ? ( <View /> ) : (
              <View style={{ flexDirection: "row", width: "100%" }}>
                <Text style={styles.date}>Coupon</Text>
                <Text style={styles.contentText}> {formatCoupon(props.bondObject.Head.Coupon)} </Text>
              </View>
          )}
        </View>
      </View>
      <View style={{ flex: 1 }}>
        <IconButton icon={require("../../../assets/rightArrow.png")} size={20} onPress={() => props.switchHeader(true)} />
      </View>
    </View>
  );
};
const CardHeader2 = (props: any) => {
  return (
    <View style={styles.bac_card_header}>
      <View style={{ flex: 1 }}>
        <IconButton icon={require("../../../assets/leftArrow.png")} size={20} onPress={() => props.switchHeader(false)} />
      </View>
      <View style={{ flex: 10, flexDirection: "row" }}>
        <View style={{ flex: 6 }}>
          <View style={{ flexDirection: "row", flexWrap: "nowrap", alignItems: "center", justifyContent: "space-evenly", }} >
            <Text style={styles.company_name}>
              <span style={{ color: "#5C95E5" }}>
                {props.bondObject.Head.Coupon === undefined ? " " : formatCoupon(props.bondObject.Head.Coupon)}
                {/* (8% FCI @ 6.73%) */}
              </span>
              {props.bondObject.Head.BondName}
              <span style={{ color: "#5C95E5" }}>
                {props.bondObject.Head.LTY === undefined ? "" : " @ " + props.bondObject.Head.LTY + "%"}
              </span>
            </Text>
          </View>
          <View style={{ flexDirection: "row", flexWrap: "nowrap", alignItems: "center", justifyContent: "space-around", }} >
            <View style={{ flexDirection: "row", flexWrap: "nowrap", alignItems: "center", justifyContent: "flex-start", }} >
              <Text style={styles.date}>
                {" " + formatMaturityDate(props.bondObject.Head.DateOfMaturity, false)}
              </Text>
              <IconButton
                style={{ height: "19.5px", width: "1px", marginLeft: "20px", marginTop: "15px", }}
                icon={require("../../../assets/Line.png")}
                size={20}
              />
            </View>
            <View style={{ flex: 1, flexDirection: "row", flexWrap: "nowrap", alignItems: "center", justifyContent: "flex-start", }} >
              <Text style={styles.risk_text}>Rating</Text>
              <Text style={styles.contentText}>
                {props.bondObject.Head.Rating}
              </Text>
            </View>
          </View>
        </View>
      </View>
      <View style={{ flex: 1, marginRight: 10 }}>
        <IconButton icon={require("../../../assets/rightArrow.png")} size={20} onPress={() => props.switchHeader(true)} />
      </View>
    </View>
  );
};

const CardContent = (props: any) => {
  //create the list of key valu of object
  let Data: any[] = [];
  if (props.bondObject != null) {
    Data = Object.entries(props.bondObject).map(
      ([key, val]) => key + ":" + val
    );
  }

  return (
    <FlatList
      data={Data}
      renderItem={(item) => (
        <View style={{ flexDirection: "row", marginLeft: "5%", marginRight: "5%", marginTop: "2.5%", }} >
          {item.item.split(":")[0] === "SpreadValuation"  ? (
              <DataTable.Cell numeric>
                {["O", "F", "UV"].includes(item.item.split(":")[1]) ? (
                    <View style={{ flexDirection: "column" }}>
                      <Image
                        style={{ height: 20, width: 20, alignSelf: "center", backgroundColor: "#FFF", }}
                        resizeMode={"contain"}
                        source={require(`../../../assets/image/${item.item.split(":")[1] }.svg`)}
                      />
                    </View>
                  ) : (
                    <View />
                  )}
              </DataTable.Cell>
            ) : (
              <DataTable>
                <View style={{ flexDirection: "row", width: "100%" }}>
                  <DataTable.Cell>
                    <Text style={styles.containerTitle}> {" "} {item.item.split(":")[0]} </Text>
                  </DataTable.Cell>
                  <DataTable.Cell numeric>
                    {" "}
                    <Text style={styles.containerValue}> {item.item.split(":")[1] === undefined ? "-" : item.item.split(":")[1]} </Text>
                  </DataTable.Cell>
                </View>
              </DataTable>
            )}

        </View>
      )}
    />
  );
};
const styles = StyleSheet.create({
  container: {
    alignItems: "center",
    justifyContent: "center",
  },
  bac_card: {
    margin: 10,
    height: "262px",
    width: "360px",
    borderRadius: 12,
    backgroundColor: "#EFF2F5",
    /* Dark */

    borderBottomWidth: 0,
    shadowColor: "#A9A9A9",
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 9,
    shadowRadius: 5,
    elevation: 150,
  },
  bac_card_header: {
    backgroundColor: "#FFFFFF",
    borderTopLeftRadius: 12,
    borderTopRightRadius: 12,
    height: "76px",
    width: "360px",
    // flex properties
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
  },
  company_name: {
    fontFamily: "Inter",
    fontStyle: "normal",
    fontWeight: "600",
    fontSize: 20,
    lineHeight: 24,
    letterSpacing: -0.339623,
  },
  date: {
    fontFamily: "Inter",
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: 16,
    lineHeight: 19,
    color: "#637381",
    marginTop: "6px",
    height: "19px",
    width: "100%",
  },
  picker: {
    fontFamily: "Inter",
    fontSize: 14,
    lineHeight: 19,
    fontWeight: "normal",
    height: "19px",
    width: "60%",
    boxSizing: "border-box",
    borderRadius: 4,
    marginTop: 5,
    color: "#454F5B",
    textAlign: "right",
  },
  contentText: {
    fontWeight: "500",
    marginLeft: 5,
    fontSize: 18,
  },
  card_content_text: {
    color: "#637381",
    fontWeight: "400",
  },
  risk_text: {
    fontFamily: "Inter",
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: 16,
    color: "#637381",
    height: "19px",
    width: "35%",
  },
  containerTitle: {
    fontFamily: "Inter",
    fontSize: 16,
    fontStyle: "normal",
    fontWeight: "100",
    color: "#637381",
    lineHeight: 19,
    letterSpacing: -0.271698,
  },
  containerValue: {
    fontFamily: "Inter",
    fontSize: 16,
    fontStyle: "normal",
    fontWeight: "100",
    color: "#454F5B",
    lineHeight: 19,
    letterSpacing: -0.271698,
  },
});

export default BondCardView;
