//here we import all necessary lib
import React from "react";
import {
  View,
  StyleSheet,
  Text,
  FlatList,
  Image,
  TouchableOpacity,
} from "react-native";
import {
  widthPercentageToDP as wp,
  heightPercentageToDP as hp,
} from "react-native-responsive-screen";
import BondCardView from "../../../../shared/component/ListComponent/CardViewComponent";
import { TableRowComponent } from "../../../../shared/component/ListComponent/TableRowComponent";
import { Dimensions } from "react-native";
const { width, height } = Dimensions.get("screen");
//here is the interface for props
interface WatchListComponentProps {
  watchList: any;
  view: any;
  navigation: any;
  showOrderModal: any;
  manageWatchList: any;
}
//here is the interface for state
interface WatchListComponentState {}
//here is the WatchListComponent is start
export class WatchListComponent extends React.Component<
  WatchListComponentProps,
  WatchListComponentState
> {
  state = { hideWatchList: true, };
  col: number | undefined;
  //here is the constructor
  constructor(props: WatchListComponentProps) {
    super(props);
 
    if (width >= 1024) {
      this.col = 3;
    } else if (width >= 500) {
      this.col = 2;
    } else {
      this.col = 1;
    }
  }

  render() {
    if (this.props.watchList.length == 0) {
      return null;
    } else {
      return (
        <View>
          <TouchableOpacity style={{ width: "200px" }} onPress={() => { this.setState({hideWatchList: !this.state.hideWatchList}) }} >
            <View style={{ flexDirection: "row" }}>
              <Text style={{ width: "75%", height: "24px", fontWeight: "600", fontSize: 20,
                  lineHeight: 24, letterSpacing: -0.339623, color: "#637381", }}
              >
                WatchList
              </Text>
              <Image
                resizeMode={"contain"}
                style={{ height: 25, width: 15, marginLeft: 10 }}
                source={ this.state.hideWatchList ? require("../../../../assets/down_arrow.svg") : require("../../../../assets/up_arrow.svg") }
              />
            </View>
          </TouchableOpacity>
          {this.state.hideWatchList ? (
            <>
            {this.props.view === "Card"?(
              <FlatList
                data={this.props.watchList}
                contentContainerStyle={{ width: "100%", paddingLeft: 8, paddingRight: 8, }}
                renderItem={(bond: any) => (
                    <BondCardView
                      bondObject={bond.item}
                      navigation={this.props.navigation}
                      manageWatchList={this.props.manageWatchList}
                      isWatchListItem={(bondObject: any) => {return true;}}
                      showOrderModal={this.props.showOrderModal}
                    />
                )}
                numColumns={this.col}
                key={this.col}
              />
            ): null}
            {this.props.view === "Row"?(
              <FlatList
                data={this.props.watchList}
                contentContainerStyle={{ width: "100%", paddingLeft: 8, paddingRight: 8, }}
                renderItem={(bond: any) => (
                  <TableRowComponent
                    bondObject={bond.item}
                    navigation={this.props.navigation}
                    manageWatchList={this.props.manageWatchList}
                    isWatchListItem={(bondObject: any) => {return true;}}
                    showOrderModal={this.props.showOrderModal}
                  />
                )}
              />
            ): null}
            </>
          ): null}
          <View style={{ backgroundColor: "#E6E6E6", width: "100%", height: 1.8, marginBottom: 10, }} />
        </View>
      );
    }
  }
}
const styles = StyleSheet.create({
  headCell: {
    height: hp("5%"),
    justifyContent: "center",
    alignItems: "center",
    flex: 0.2,
  },
  cell: {
    height: hp("10%"),
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "#FFF",
    flex: 0.2,
  },
  cellBlank: {
    height: hp("10%"),
    justifyContent: "center",
    alignItems: "center",
    flex: 0.2,
  },
});
