import {
  Checkbox,
  createStyles,
  FormControl,
  InputBase,
  ListItemText,
  makeStyles,
  MenuItem,
  Select,
  Theme,
  withStyles,
} from "@material-ui/core";
import React from "react";
import { View } from "react-native";
import { StyleSheet, Text } from "react-native";
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
      maxWidth: 300,
    },
    chips: {
      display: "flex",
      flexWrap: "wrap",
    },
    chip: {
      margin: 2,
    },
    noLabel: {
      marginTop: theme.spacing(3),
    },
    select: {
      "&:before": {
        // normal
        borderBottom: "1px solid orange",
      },
      "&:after": {
        // focused
        borderBottom: `3px solid green`,
      },
      "&:hover:not(.Mui-disabled):not(.Mui-focused):not(.Mui-error):before": {
        // hover
        borderBottom: `2px solid purple`,
      },
    },
  })
);

const BootstrapInput = withStyles((theme: Theme) =>
  createStyles({
    root: {
      "label + &": {
        marginTop: theme.spacing(3),
      },
    },
    input: {
      borderRadius: 4,
      position: "relative",
      backgroundColor: theme.palette.background.paper,
      border: "1px solid #31BC98",
      fontSize: 16,
      padding: "10px 26px 10px 12px",
      transition: theme.transitions.create(["border-color", "box-shadow"]),
      // Use the system font instead of the default Roboto font.
      fontFamily: ["Inter"].join(","),
      "&:focus": {
        borderRadius: 4,
        borderColor: "#31BC98",
      },
    },
  })
)(InputBase);

const CategorySelectionList = (props: any) => {

  if(props.categories.length == 0){
    return (<></>);
  }

  let selectedCategories = props.categories[0];
  //Ideally selectedCategories will come from props, but putting this condition just to avoid any unexpected behaviour
  if(props.selectedCategories.length > 0){
    selectedCategories = props.selectedCategories;;
  }

  const classes = useStyles();

  const [categoryName, setCategoryName] = React.useState<string>(selectedCategories);

  const handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    setCategoryName(event.target.value as string);
    props.onCategoryChange(event.target.value);
  };

  return (
    <View>
      <View style={styles.mainView}>
        <Text style={{ width: 42, fontFamily: "Inter", fontStyle: "normal", fontWeight: "500", fontSize: 18, lineHeight: 22, paddingTop: 15, letterSpacing: -0.30566, }} >
          Type
        </Text>
        <FormControl className={classes.formControl}>
          <Select
            disableUnderline
            className={classes.select}
            labelId="demo-mutiple-checkbox-label"
            id="demo-mutiple-checkbox"
            multiple
            value={categoryName}
            onChange={handleChange}
            input={<BootstrapInput />}
            renderValue={(selected) => (selected as string[]).join(", ")}
          >
            {props.categories.map((name: any) => (
              <MenuItem key={name} value={name}>
                <Checkbox style={{ color: "#31BC98" }} checked={categoryName.indexOf(name) > -1} />
                <ListItemText primary={name} />
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </View>
    </View>
  );
};
const styles = StyleSheet.create({
  mainView: {
    flexDirection: "row",
    marginRight: "8%",
  },
});
export default CategorySelectionList;
