import React from "react";
import { StackActions } from "@react-navigation/native";

import {
  Button,
  Card,  
} from "react-native-paper";
import {
  View,
  StyleSheet,
  Text,
} from "react-native";

export default function AlertDialog(props: any) {

  const handleClose = () => {
    if(props.bttonText=="Subscribe"){
      props.navigation.dispatch(StackActions.replace("subscribe"));
    }else{
      let redirectTo = window.location.href;
      props.navigation.dispatch(StackActions.replace("login", {redirect: redirectTo}));
    }   
  };

  return (
    <div>
     <Card style={styles.card}>
              <Card.Content>
              </Card.Content>
              <View style={styles.cardView}>
                <Text style={styles.cardLabelText}> {props.message} </Text>
              </View>
              <Card.Actions style={styles.cardAction}>
                <View style={{ width: 5 }} />
                <Button mode={"contained"} style={styles.submitButton} uppercase={false} onPress={() => handleClose() } >
                  {props.bttonText}
                </Button>
              </Card.Actions>
            </Card>
    </div>
  );
}
const styles = StyleSheet.create({
  card: {
    marginTop:'5%',
    marginLeft:'30%',
    marginRight:'30%',
    marginBottom:'15%',
    
  },
  cardAction: {
    alignContent: "center",
    justifyContent: "center",
  },
  cardView: {
    height: 10,
    flexDirection: "row",
    alignContent: "space-around",
    justifyContent: "space-evenly",
  },
  cardLabelText: {
    width: "100%",
    fontFamily: "Inter",
    fontSize: 15,
    fontStyle: "normal",
    padding: 10,
    textAlign:'center',
    color:'#454F5B',
    letterSpacing: -0.30566,
    fontWeight:'600',
  },
  cardInputText: {
    height: 50,
    width: 130,
    backgroundColor: "#EFF2F5",
    color: "#525252",
    marginLeft: 20,
    padding: 5,
    fontSize: 20,
    lineHeight: 24,
    marginTop: 10,
    marginBottom: 10,
  },
  submitButton: {
    width: 100,
    backgroundColor: "#2E6DA4",
    color: "#FFFFFF",
    letterSpacing: -0.271698,
    fontSize: 16,
    marginTop: 35,
  },
});