import React, { useState } from "react";
import { View, StyleSheet } from "react-native";
import axios from "axios";
import { env } from "../../config/systemConfigs";
//this is ChangeUserPassword Function component
export const UserVerification = ({ navigation }: any) => {

    var url_string: any
    url_string = window.location; //overall URL
    var url = new URL(url_string); //in string
    var userToVerify: any
    userToVerify = url.searchParams.get("hash");


    React.useEffect(() => {

        //to verify user
        axios({
            method: "POST",
            data: {
                hash: atob(userToVerify)
            },
            withCredentials: true,
            url: `${env.API_INFRA_MODULE}/auth/verification`,
        })
            .then((result) => {

                if (result.data.status === "200") {
                    //to show message
                    window.location.href = `${env.API_INRB_FE}`
                } else {
                    alert("Oops something went wrong. Please try again.");
                        window.location.href = `${env.API_INFO_RECOM_MODULE}`
                }
            })//axios ends

    })//useEffect ended

    return (
        <View>
        </View>
    );
};