export function dateDiffInYrs(date1, date2) {
	return ((new Date(date1).getTime() - new Date(date2).getTime()) / (1000 * 3600 * 24 * 365));
}
export const formatAmount = (value)=> {
	let val = Math.abs(value)
	if (val >= 10000000) {
     value = (value / 10000000).toFixed(2) + ' Cr';
   	} else if (val >= 100000) {
     value = (value / 100000).toFixed(2) + ' Lac';
   	}
   	return value;
}
//VK: To fix perp should actually be 31/12/9999
export const formatMaturityDate = (value, noDate)=> {
	Moment.locale("en");
	let perp = new Date("2120/12/31");
	let dateStr = "";
	if(new Date(Moment(value).format("yyyy-MM-DD")) > perp){
		dateStr = "PERP";
	}
	else if (noDate){
		dateStr = Moment(value).format("MMM YYYY" );
	}
	else{
		dateStr = Moment(value).format("DD-MMM-YYYY");
	}
	return dateStr;
}

export const formatCoupon = (value)=> {
	let couponStr = "";
	if (value < 0){
		couponStr = "MLD";
	}
	else if (value == 0){
		couponStr = "ZCB";
	}
	else{
		couponStr = value + "% ";
	}
	return couponStr;
}

export function getSearchJsonOnSearchString(input) {
    // var post_url = "food corporation Institute%207.65%25%20oct%20%2D22"
    // var query = decodeURIComponent(post_url).split(" ");
    var query = input.split(" ");
    var issuer = "";
    var coupon = 0.0;
    var month = -1;
    var year = 0;
    var yearStr = "";
    var monthStr = "";
    var isinNo = "";

    if (input.startsWith("INE") || input.startsWith("ine")) {
        isinNo = input.toUpperCase();
    } else {
        for (let i = 0; i < query.length; i++) {
            if (query[i].indexOf("%") >= 0) // is this coupon?
                coupon = parseFloat(query[i]);
            else if (yearStr = query[i].match(/\d+/g)) { // is this maturity?
                if (month < 0) { //month and year are together?
                    monthStr = query[i].replace(/[^a-z]/gi).toLowerCase();
                    month = new Date('01-' + monthStr + '-1').getMonth() + 1;
                }
                if (yearStr.length == 2) //if only 2 digits of year are specified then take current century
                    yearStr = '20' + yearStr;
                year = parseInt(yearStr[0]);
            } else if (month < 0) { //month is yet to be parsed
                month = new Date('01-' + query[i] + '-1').getMonth() + 1;
                if (!month) { // we thought it was a month but it actually wasn't
                    issuer += " " + query[i];
                    month = -1;
                }
            } else //if nothing it must be issuer
                issuer += ' ' + query[i];
        }
    }



    return { year: year, coupon: coupon, shortName: issuer.trim(), month: month, isinNo: isinNo }
}
