import React from "react";
import { View } from "react-native";

import { env } from "../../../config/systemConfigs";
import axios from "axios";
import AsyncStorage from "@react-native-community/async-storage";
import { useCookies } from "react-cookie";

//to logout from home page
export const Logout = () => {

	const eraseCookie = (name: any) => {
		document.cookie =
		  name + "=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;";
	  };

	  const [cookies, setCookie, remove] = useCookies(["name"]);

	axios({
		method: "GET",
		withCredentials: true,
		url: env.API_INFRA_MODULE + "/auth/logout",
	}).then((result) => {
		if (result.data.status == 200) {
			AsyncStorage.clear();
			remove("user",{domain: env.SUBDOMAIN });
			remove("UserName",{domain: env.SUBDOMAIN });
			remove("userid",{domain: env.SUBDOMAIN });
			remove("roleid",{domain: env.SUBDOMAIN });
			remove("UserFullName",{domain: env.SUBDOMAIN });
			remove("r",{domain: env.SUBDOMAIN });
			remove("connect.sid",{domain: env.SUBDOMAIN });
			window.location.href = env.API_INFO_RECOM_MODULE;
		}
	});

	return <View />;
};
