import React from "react";
import {
  View,
  TextInput,
  Image,
  FlatList,
  StyleSheet,
  Text,
  ScrollView,
  TouchableOpacity,
} from "react-native";
import axios from "axios";
import { env } from "../../../../config/systemConfigs";
import { getSearchJsonOnSearchString, formatCoupon, formatMaturityDate } from "../../../../shared/services/utilServices";

//interface for props
interface BondSearchBarProps {
  onParentGetItem: any;
  context: any;
}
//interface for state
interface BondSearchBarState {}

export class BondSearchBar extends React.Component<
  BondSearchBarProps,
  BondSearchBarState
> {
  state = {
    suggestionList: [] as any[],
    searchQuery: "",
  };

  constructor(props: BondSearchBarProps) {
    super(props);
  }

  searchBonds(searchQuery: string) {
    this.setState({searchQuery: searchQuery});
    if(searchQuery.length > 5){  //let the user input atleast 4 char
      let postJson = {};
      if(this.props.context != ""){
        postJson["SpecialProduct"] = this.props.context;
      }
      postJson = {...postJson, ...getSearchJsonOnSearchString(searchQuery)};
      axios({
        method: "POST",
        data: postJson,
        withCredentials: true,
        url: env.API_INFRA_MODULE + "/bondManagement/search",
      })
        .then((result) => {
          //seting the bonds array in the bonds
          if (result.status === 200) {
            this.setState({ suggestionList: result.data });
          }
        })
        .catch((err) => {
          alert("Oops! Something went wrong. Please try again.")
          console.log("Error console:", err);
        });
    }
  }

  onKeyPress(event: any) {
    if (event.nativeEvent.key == "Enter" ) {
      this.props.onParentGetItem(this.state.suggestionList);
      this.setState({suggestionList: [], });
    }
  }

  render() {
    return (
      <>
        <View style={styles.searchView}>
          <Image style={styles.searchImage} source={require("../../../../assets/icons/search.svg")} />
          <TextInput style={styles.searchTextInput }
            placeholder="Search Bonds"
            onKeyPress={(event) =>  {this.onKeyPress(event);}}
            onChangeText={(text) => this.searchBonds(text)}
            value={this.state.searchQuery}
          />
          {this.state.searchQuery != "" ? (
            <TouchableOpacity 
              onPress={() =>{
                this.setState({suggestionList: [], searchQuery: ""});
                this.props.onParentGetItem([]);
              }}
            >
              <Image style={{ marginTop: 15, marginLeft: 25, height: 12, width: 12 }} source={require("../../../../assets/close.svg")} />
            </TouchableOpacity>
          ) : null}
        </View>
          <ScrollView style={ this.state.suggestionList.length > 0 ? { zIndex: -1, position: "absolute", height: 230, width: "80%" } : {} }>
            <FlatList
              data={this.state.suggestionList}
              renderItem={(element: any) => (
                <View style={styles.item} >
                  <Text style={{ fontFamily: "Inter", textAlign: "left", textAlignVertical: "center", }}
                    onPress={() => {
                      this.props.onParentGetItem([element.item]);
                      this.setState({suggestionList: [],});
                    }}
                  >
                    <Text style={{ fontWeight: "200", fontFamily: "Inter" }}>
                      {element.item.isinNo + " "}
                    </Text>
                    {element.item.Head.BondName + " " + formatCoupon(element.item.Head.Coupon) + 
                      formatMaturityDate(element.item.Head.DateOfMaturity, true) +
                      (" (" + element.item.nameOfIssuer + ")")}
                  </Text>
                </View>
              )}
            />
          </ScrollView>
      </>
    );
  }
}
const styles = StyleSheet.create({
  searchView: {
    width: "100%",
    height: "45px",
    flexDirection: "row",
    backgroundColor: "#EFF2F5",
    borderRadius: 6,
    elevation: 10,
  },
  searchTextInput: {
    color: "#808C9A",
    fontSize: 20,
    lineHeight: 24,
    width: "80%",
    paddingLeft: 10,
    borderColor: "#EFF2F5",
    borderWidth: 0.1,
  },
  searchImage: {
    height: "30px",
    width: "30px",
    marginLeft: 15,
    marginTop: 5,
    marginRight: 15,
  },
  item: {
    width: "100%",
    height: "45px",
    backgroundColor: "#FFF",
    padding: 10,
    borderBottomWidth: 0.1,
    borderBottomColor: "#DEDEDE",
  },
});
