import React, { useEffect, useState } from "react";
import {
  View,
  Text,
  StyleSheet,
  Image,
  ScrollView,
  FlatList,
} from "react-native";
import { IconButton, Card } from "react-native-paper";
import axios from "axios";
import Moment from "moment";
import { env } from "../../../../config/systemConfigs";
const FactorPortfolioDetails = (props: any) => {
  const [bondData, setBondData] = useState({
    _id: "",
    issuerShortName: "",
    nameOfIssuer: "",
    isinNo: "",
    couponRate: 0.0,
    dateOfMaturity: "",
    creditRating: "",
    faceValue: 0,
    lty: 0.0,
    fairYTM: "0",
    credit_note: "",
    creditSpreadScore: 0.0,
    categoryOfIssue: 0,
    typeOfInstrument: 0,
    modeOfIssue: 0,
    issueSize: 0,
    dateOfAllotment: "",
    couponType: 0,
    frequencyOfInterestPayment: 0,
    businessSector: 0,
    typeOfIssuerOwnership: "",
    typeOfIssuerNature: "",
    instrumentStatus: 0,
    spreadBPS: 0,
    priceTrend: "",
    riskProfile: "",
    spreadValuation: "",
    specialProduct: "",
    averageMaturity: "",
    averageLTY: "",
    duration: "",
    latestTradeDate: "",
    latestTradePrice: "",
    latestTradeYield: "",
    latestSpreads: "",
    latestTradingVolume: "",
    weights: [
      {
        _id: "",
        productId: "",
        isinNos: "",
        overviewDescription: "",
        weights: [
          {
            couponRate: 0.0,
            dateOfMaturity: "",
            issuerShortName: "",
            businessSector: 0,
            weight: "",
          },
        ],
      },
    ],
  });

  useEffect(() => {
    const id = props.route.params._id;
    console.log("===========>" + JSON.stringify(props.route.params));

    axios({
      method: "POST",
      data: { _id: id, type: "f" },
      withCredentials: true,
      url:
        env.API_INFRA_MODULE + "/bondManagement/getSpecificCategoryBondDetails",
    })
      .then((result) => {
        console.log(">>>>>>>>>>>>>>>>>>" + JSON.stringify(result.data.result));
        let bond = result.data.result[0];
        delete bond._id;
        console.log(bond);
        setBondData(result.data.result[0]);
      })
      .catch((err) => {
        console.log("Error console:", err);
      });
  }, [1]);

  return (
    <ScrollView>
      <View style={styles.container}>
        <View
          style={{
            flexDirection: "row",
            alignSelf: "flex-start",
            marginLeft: "12%",
          }}
        >
          <IconButton
            style={{
              height: "15.25px",
              width: "15.41px",
              marginBottom: "10px",
            }}
            icon={require("../../../../assets/backarrow.png")}
            onPress={() => props.navigation.goBack()}
          />
          <Text
            style={{
              fontWeight: "600",
              fontSize: 18,
              textAlign: "left",
              marginTop: "5px",
            }}
          >
            Back
          </Text>
        </View>
        <View style={styles.card_container}>
          <View style={styles.row_one}>
            <View style={styles.row_one_section_one}>
              <View style={styles.row_one_section_one_part_one}>
                <Text
                  style={{
                    fontWeight: "600",
                    fontSize: 25,
                    color: "#454F5B",
                    textAlign: "left",
                  }}
                >
                  {bondData.nameOfIssuer}
                </Text>
              </View>
              <View style={styles.row_one_section_one_part_two}>
                <View>
                  <Text
                    style={{
                    
                      fontSize: 16,
                      color: "#637381",
                    }}
                  >
                    Avg maturity
                    <Text
                      style={{
                       
                        fontWeight: "500",
                        fontSize: 20,
                        marginLeft: 10,
                        color: "#454F5B",
                      }}
                    >
                      {bondData.averageMaturity}
                    </Text>
                  </Text>
                </View>

                <View
                  style={{
                    height: 30,
                    width: 1,
                    backgroundColor: "#DFE3E8",
                    marginLeft: "30px",
                    marginRight: "30px",
                  }}
                />

                <View style={{ flexDirection: "row" }}>
                  <Text
                    style={{
                      fontWeight: "normal",
                      fontSize: 18,
                      lineHeight: 22,
                      letterSpacing: -0.271698,
                      color: "#637381",
                    }}
                  >
                    {" "}
                    AvgLTY
                    <Text
                      style={{
                        marginStart: "10px",
                      
                        fontWeight: "500",
                        fontSize: 20,
                        color: "#454F5B",
                      }}
                    >
                      {bondData.lty}
                    </Text>
                  </Text>
                  <Image
                    style={{
                      height: "17px",
                      width: "17px",
                      marginLeft: "40px",
                      alignSelf: "center",
                    }}
                    source={bondData.spreadValuation != ""
                    ? require(`../../../../assets/image/${
                        bondData.spreadValuation
                      }.svg`)
                    : ""
                }
                  />
                </View>
              </View>
            </View>

            {/* <View style={styles.row_one_section_two}>
              <Surface style={{ elevation: 10, shadowColor: "#5C6AC4" }}>
                <View style={styles.row_one_section_two_button}>
                  <Button
                    style={{
                      backgroundColor: "#5C6AC4",
                      borderRadius: 3,
                    }}
                    mode="contained"
                    uppercase={false}
                    onPress={() => console.log("Pressed")}
                  >
                    <Text
                      style={[
                        styles.row_one_section_two_button_text,
                        { color: "white" },
                      ]}
                    >
                      Buy Portfolio
                    </Text>
                  </Button>
                </View>
              </Surface>
            </View> */}
          </View>
          <View
            style={{
              height: 1,
              width: "100%",
              backgroundColor: "#DFE3E8",
              marginRight: "10%",
            }}
          />

          {/* second */}
          <View style={styles.row_two}>
            <View style={styles.row_two_section}>
              <Text
                style={{
                  fontWeight: "bold",
                  fontSize: 20,
                  color: "#454F5B",
                }}
              >
                Overview
              </Text>
              <Text
                style={{
                  fontWeight: "normal",
                  fontSize: 16,
                  lineHeight: 19,
                  display: "flex",
                  alignItems: "flex-end",
                  marginTop: "10px",
                  marginRight: "20px",
                  marginBottom: "20px",
                  color: "#637381",
                }}
              >
                {bondData.weights[0].overviewDescription}
              </Text>
            </View>

            <View
              style={{
                height: 165,
                opacity: 1,
                marginTop: "10px",
                paddingTop: "10px",
                marginBottom: "10px",
                paddingBottom: "10px",
                width: 1,
                backgroundColor: "#DFE3E8",
              }}
            />

            <View style={styles.row_two_section_two}>
              <Text
                style={{
                  fontWeight: "normal",
                  fontSize: 14,
                  color: "#637381",
                  marginRight: "3%",
                }}
              >
                Duration
              </Text>
              <Text
                style={{
                  marginTop: "10px",
                  fontWeight: "bold",
                  fontSize: 18,
                  color: "#454F5B",
                }}
              >
                8.90%
              </Text>
            </View>
            <View style={styles.row_two_section_two}>
              <Text
                style={{
                  fontWeight: "normal",
                  fontSize: 14,
                  color: "#637381",
                }}
              >
                Spread BPS
              </Text>
              <Text
                style={{
                  marginTop: "10px",
                  fontWeight: "bold",
                  fontSize: 18,
                  color: "#454F5B",
                }}
              >
                {bondData.spreadBPS}
              </Text>
            </View>
            <View style={styles.row_two_section_two}>
              <Text
                style={{
                  fontWeight: "normal",
                  fontSize: 14,
                  color: "#637381",
                }}
              >
                Fair YTM
              </Text>
              <Text
                style={{
                  marginTop: "10px",
                  fontWeight: "bold",
                  fontSize: 18,
                  color: "#454F5B",
                }}
              >
                {bondData.fairYTM}
              </Text>
            </View>
            <View style={styles.row_two_section_two}>
              <Text
                style={{
                  fontWeight: "normal",
                  fontSize: 14,
                  color: "#637381",
                }}
              >
                Credit Spread Score
              </Text>
              <Text
                style={{
                  marginTop: 10,
                  fontWeight: "bold",
                  fontSize: 18,
                  color: "#454F5B",
                }}
              >
                {bondData.creditSpreadScore}
              </Text>
            </View>
          </View>

          <View
            style={{
              height: 1,
              width: "100%",
              marginLeft: "20px",
              marginRight: "10px",
              backgroundColor: "#DFE3E8",
            }}
          />

          <View style={styles.row_four}>
            <View style={styles.row_four_section}>
              <Text style={styles.row_four_header}>Weights</Text>
              <ScrollView style={{ height: 200 }}>
                <FlatList
                  data={bondData.weights[0].weights}
                  renderItem={(bond: any) => (
                    <Card
                      style={{
                        elevation: 4,
                        borderColor: "#DFE3E8",
                        borderWidth: 1,
                        margin: 5,
                      }}
                    >
                      <View
                        style={{
                          flexDirection: "row",
                          flex: 7,
                          borderRadius: 6,
                        }}
                      >
                        <Text style={styles.row_four_bold}>
                          {bond.item.issuerShortName +
                            " " +
                            bond.item.couponRate +
                            " " +
                            Moment(bond.item.dateOfMaturity).format("MMM YY")}
                        </Text>
                        <Text style={styles.row_four_light}>
                          {bond.item.businessSector}
                        </Text>
                        <Text style={styles.row_four_number}>
                          {bond.item.weight + "%"}
                        </Text>
                      </View>
                    </Card>
                  )}
                />
              </ScrollView>
            </View>
          </View>
        </View>
      </View>
    </ScrollView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    marginTop: "3%",
    marginBottom: "3%",
    backgroundColor: "#F2F2F2",
    alignItems: "center",
    justifyContent: "center",
  },
  card_container: {
    display: "flex",
    backgroundColor: "#FFFFFF",
    borderRadius: 4,
    flexDirection: "column",
    width: "75%",
  
  },
  row_one: {
    flexDirection: "row",
    alignItems: "center",
    marginTop: "25px",
    marginBottom: "25px",
    paddingRight: "5%",
  },
  row_one_section_one: {
    flex: 1,
    flexDirection: "column",
    justifyContent: "flex-start",
  },
  row_one_section_one_part_one: {
    marginLeft: "20px",
    flexDirection: "row",
    justifyContent: "flex-start",
  },
  row_one_section_one_part_two: {
    marginTop: "10px",
    marginLeft: "20px",
    flexDirection: "row",
    justifyContent: "flex-start",
  },
  row_one_section_two: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "flex-start",
  },
  row_one_section_two_button: {
    flexDirection: "row",
    alignItems: "flex-start",
  },
  row_one_section_two_button_text: {
    fontWeight: "500",
    fontSize: 15,
    textAlign: "right",
    color: "#5C6AC4",
  },
  row_two: {
    marginTop: "15px",
    flexDirection: "row",
    justifyContent: "flex-start",
    alignItems: "flex-start",
  },
  row_two_section: {
    flex: 20,
    flexDirection: "column",
    justifyContent: "flex-start",
    marginLeft: "20px",
  },
  row_two_section_two: {
    flex: 8,
    flexDirection: "column",
    paddingRight: 5,
    justifyContent: "center",
    marginLeft: 10,
  },
  row_content: {
    marginTop: "10px",
    fontFamily: "Inter",
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: 16,
    color: "#637381",
    width: "100%",
    backgroundColor: "#F4F6F8",
    flexDirection: "row",
    alignSelf: "flex-start",
    paddingTop: 10,
  },
  row_four: {
    flexDirection: "row",
    marginTop: "10px",
    marginBottom: "15px",
  },
  row_four_section: {
    marginTop: "10px",
    marginLeft: "20px",
    textAlignVertical: "center",
  },
  row_four_header: {
    fontWeight: "600",
    fontSize: 20,
    color: "#454F5B",
    marginBottom: 5,
  },
  row_four_bold: {
    margin: "20px",
    fontFamily: "Inter",
    fontStyle: "normal",
    fontWeight: "600",
    fontSize: 20,
    color: "#454F5B",
  },
  row_four_light: {
    margin: "20px",
    fontFamily: "Inter",
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: 16,
    color: "#637381",
  },
  row_four_number: {
    margin: "20px",
    marginLeft: "190px",
    fontFamily: "Inter",
    fontStyle: "normal",
    fontWeight: "600",
    fontSize: 20,
    color: "#454F5B",
  },
});
export default FactorPortfolioDetails;
