//VK: This should actually be a child component of MarketWatch. No need to do special routing. That way it will be easier to share data between this screen and MarketWatch
import React, { useState } from "react";
import { Text, StyleSheet, View, Image } from "react-native";
import { Button, IconButton, Card } from "react-native-paper";
import axios from "axios";
import {formatMaturityDate, formatCoupon} from "../../../../shared/services/utilServices";
import { CreateNewOrderModal } from "../../orderbook/child-component/CreateNewOrderModal";
import { env } from "../../../../config/systemConfigs";

const BondDetails = (props: any) => {

  let bondData = JSON.parse(props.route.params.bondData);
  
  const [orderBond, setOrderBond] = useState(null);
  const [modalShow, setShowModal] = useState(false);
  const [inwatchList, setinwatchList] = useState(props.route.params.inWatchList);

  function showOrderModal() {
    setOrderBond(bondData);
    setShowModal(true);
  }

  function showArticle() {
    axios({
      method: "GET",
      withCredentials: true,
      url: env.API_INFRA_MODULE + "/bondManagement/getBondArticleDetails?_id=" + bondData._id,
    })
      .then((result) => {
        if (result.data.result.length == 0) {
          //VK: To check what happens if report API is called with null ID
          alert("Sorry we couldnt find the information you are looking for. If you need it please contact Ketan Verma at ketan.verma@zephyr.org.in");
        } else {
            window.location.href = env.API_INFO_RECOM_MODULE + `/reports/${result.data.result.reportType}/${result.data.result.category}/${result.data.result._id}`;
        }
      })
      .catch((err) => {
        alert("Oops! Something went wrong. Please try again.");
        console.log("Error console:", err);
      });
  }

  return (
    <View style={styles.container}>
      <View style={{ flexDirection: "row", alignSelf: "flex-start", justifyContent: "center", marginLeft: "130px", }}  >
        <IconButton
          style={{height: 20, width: 15, paddingLeft: 10, }}
          icon={require("../../../../assets/backarrow.png")}
          onPress={() => props.navigation.navigate("marketwatch")}
        />
        <Text style={{ fontStyle: "normal", fontWeight: "600", fontSize: 18, textAlign: "left", marginTop: 2, }} > Back</Text>
      </View>
      <Card style={styles.card_container}>
        <View>
          <View style={styles.row_one}>
            <View style={styles.row_one_section_one}>
              <View style={styles.row_one_section_one_part_one}>
                <Text style={{ fontStyle: "normal", fontWeight: "600", fontSize: 28, color: "#454F5B", textAlign: "left", }} >
                  {bondData.nameOfIssuer}
                </Text>
              </View>
              <View style={styles.row_one_section_one_part_two}>
                <Text style={{ height: "22px", fontStyle: "normal", fontWeight: "normal", fontSize: 20, color: "#637381", }} >
                  ISIN
                  <Text style={{ marginStart: 10, height: 22, fontStyle: "normal", fontWeight: "500", fontSize: 15, color: "#454F5B", }} >
                    {bondData.isinNo}
                  </Text>
                </Text>
              </View>
              <View style={styles.row_one_section_one_part_two}>
                <Text style={{height: 22, fontStyle: "normal", fontWeight: "500", fontSize: 15, color: "#454F5B", }} >
                    {bondData.Tail.flags}
                </Text>
              </View>
            </View>
            <View style={styles.row_one_section_two}>
              <View style={styles.row_one_section_two_button}>
                <Image
                  style={{ height: "18px", width: "18px", borderColor: "#5C6AC4", marginRight: "10px", }}
                  source={inwatchList ? require("../../../../assets/cross.svg") : require("../../../../assets/add.png")}
                />
                <Text
                  style={[styles.row_one_section_two_button_text, { textAlignVertical: "center", fontSize: 14 }, ]}
                  onPress={() => {
                    if (inwatchList) {
                      axios({
                        method: "POST",
                        data: { myWatchList: bondData._id, type: bondData.specialProduct },
                        withCredentials: true,
                        url: env.API_INFRA_MODULE + "/userManagement/removeFromMyWatchList",
                      })
                        .then((result) => { 
                          setinwatchList(false)
                        })
                        .catch((err) => {
                          alert("Oops! Something went wrong. Please try again.")
                          console.log("Error console:", err);
                        });
                    } else {
                      axios({
                        method: "POST",
                        data: { myWatchList: bondData._id },
                        withCredentials: true,
                        url: env.API_INFRA_MODULE + "/userManagement/addToMyWatchList",
                      })
                        .then((result) => {                      
                          setinwatchList(true)
                        })
                        .catch((err) => {
                          alert("Oops! Something went wrong. Please try again.")
                          console.log("Error console:", err);
                        });
                    }
                  }}
                >
                  WatchList
                </Text>
              </View>
              <View style={styles.row_one_section_two_button}>
                <Button style={{backgroundColor: "#DCE1FF", borderRadius: 4, }} mode="contained" uppercase={false} onPress={() => showArticle()} >
                  <Text style={styles.row_one_section_two_button_text}>
                    Credit Note
                  </Text>
                </Button>
              </View>
              <View style={styles.row_one_section_two_button}>
                <Button style={{ backgroundColor: "#5C6AC4", }} mode="contained" uppercase={false} onPress={() => showOrderModal()} >
                  <Text style={[ styles.row_one_section_two_button_text, { color: "white" }, ]} >
                    Buy / Sell
                  </Text>
                </Button>
              </View>
            </View>
          </View>
          <View style={{ height: 1, width: "100%", backgroundColor: "#DFE3E8" }} />

          {/* second */}
          <View style={styles.row_two}>
            <View style={styles.row_two_section}>
              <Text style={{ fontStyle: "normal", fontWeight: "normal", fontSize: 16, color: "#637381", }} >
                Coupon
              </Text>
              <Text style={{ marginTop: "10px", fontStyle: "normal", fontWeight: "500", fontSize: 18, color: "#454F5B", }} >
                {bondData.Head.Coupon === undefined ? "-" : formatCoupon(bondData.Head.Coupon)}
              </Text>              
            </View>
            <View style={styles.row_two_section}>
              <Text style={{ fontStyle: "normal", fontWeight: "normal", fontSize: 16, color: "#637381", }} > Issue Date </Text>
              <Text style={{ marginTop: "10px", fontStyle: "normal", fontWeight: "500", fontSize: 18, color: "#454F5B", }} >
                {bondData.dateOfAllotment != "" ? formatMaturityDate(bondData.dateOfAllotment, false) : "-"}
              </Text>
            </View>
            <View style={styles.row_two_section}>
              <Text style={{ fontStyle: "normal", fontWeight: "normal", fontSize: 16, color: "#637381", }} > Maturity Date </Text>
              <Text style={{ marginTop: "10px", fontStyle: "normal", fontWeight: "500", fontSize: 18, color: "#454F5B", }} >
                {bondData.Head.DateOfMaturity != "" ? formatMaturityDate(bondData.Head.DateOfMaturity, false) : ""}
              </Text>
            </View>
            <View style={styles.row_two_section}>
              <Text style={{ fontStyle: "normal", fontWeight: "normal", fontSize: 16, color: "#637381", }} > Spread (bps) </Text>
              <Text style={{ marginTop: "10px", fontStyle: "normal", fontWeight: "500", fontSize: 18, color: "#454F5B", }} >
                {bondData.Body["Spread(bps)"] != "" ? bondData.Body["Spread(bps)"] : "-"}
              </Text>
            </View>
            <View style={styles.row_two_section}>
              <Text style={{ fontStyle: "normal", fontWeight: "normal", fontSize: 16, color: "#637381", }} > Rating </Text>
              <View style={{ flexDirection: "row" }}>
                <Text style={{ marginTop: 10, fontStyle: "normal", fontWeight: "500", fontSize: 18, color: "#454F5B", }} >
                  {bondData.Head.Rating!= "" ? bondData.Head.Rating : "-"}
                </Text>
              </View>
            </View>
            <View style={styles.row_two_section}>
              <Text style={{ fontStyle: "normal", fontWeight: "normal", fontSize: 16, color: "#637381", }} > Fair YTM </Text>
              <Text style={{ marginTop: "10px", fontStyle: "normal", fontWeight: "500", fontSize: 18, color: "#454F5B", }} >
                {bondData.Body["Fair YTM"] != "" ? bondData.Body["Fair YTM"] : "-"}
              </Text>
            </View>
            <View style={styles.row_two_section}>
              <Text style={{ fontStyle: "normal", fontWeight: "normal", fontSize: 16, color: "#637381", }} > Credit Spread Score </Text>
              <Text style={{ marginTop: "10px", fontStyle: "normal", fontWeight: "500", fontSize: 18, color: "#454F5B", }} >
                {bondData.Body["Credit Spread Score"] != "" ? bondData.Body["Credit Spread Score"] : "-"}
              </Text>
            </View>
          </View>
          <View style={{ height: 1, width: "100%", backgroundColor: "#DFE3E8" }} />
          {/* third */}
          <View style={styles.row_three}>
            <View style={styles.row_three_section_one}>
              <View>
                <Text style={styles.row_three_section_one_text}> Last Trade Date{" "} </Text>
                <Text style={styles.row_three_section_one_text}> Last Trade Price{" "} </Text>
                <Text style={styles.row_three_section_one_text}> Last Trade Yield{" "} </Text>
              </View>
              <View>
                <Text style={styles.row_three_section_two_text}>
                  {bondData.latestTradeDate != undefined ? formatMaturityDate(bondData.latestTradeDate, false) : "-"}
                </Text>
                <Text style={styles.row_three_section_two_text}>
                  {bondData.latestTradePrice != undefined ? 'INR ' + bondData.latestTradePrice : "-"}
                </Text>
                <Text style={styles.row_three_section_two_text}>
                  {bondData.Head.LTY != undefined ? bondData.Head.LTY + '%' : "-"}
                </Text>
              </View>
            </View>
          </View>
          <View style={{ height: 1, width: "100%", backgroundColor: "#DFE3E8" }} />

          {/* fourth */}
          <View style={styles.row_four}>
            <View style={styles.row_four_section}>
              <Text style={styles.row_four_header}>Liquidity Analysis</Text>
              <View style={{ flexDirection: "row" }}>
                <Text style={styles.row_four_value}>Latest Trading Volume</Text>
                <View>
                  <Text style={styles.row_four_value_text}>
                    {bondData.latestTradingVolume != undefined ? bondData.latestTradingVolume : "-"}
                  </Text>
                </View>
              </View>
            </View>
          </View>
        </View>
      </Card>
      <CreateNewOrderModal 
        BondToOrder={orderBond} 
        OrderToEdit={null} 
        showModal={modalShow} 
        hideModal={() => {setShowModal(false);}}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#F2F2F2",
    alignItems: "center",
    justifyContent: "center",
  },
  card_container: {
    display: "flex",
    backgroundColor: "#FFFFFF",
    borderRadius: 4,
    flexDirection: "column",
    width: "80%",
    paddingLeft: "30px",
    paddingRight: "30px",
    elevation: 5,
  },
  row_one: {
    flexDirection: "row",
    alignItems: "center",
    marginTop: "25px",
    marginBottom: "25px",
  },
  row_one_section_one: {
    flex: 1,
    flexDirection: "column",
    justifyContent: "flex-start",
  },
  row_one_section_one_part_one: {
    marginLeft: "20px",
    flexDirection: "row",
    justifyContent: "flex-start",
  },
  row_one_section_one_part_two: {
    marginTop: "10px",
    marginLeft: "20px",
    flexDirection: "row",
    justifyContent: "flex-start",
  },
  row_one_section_two: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
  },
  row_one_section_two_button: {
    marginLeft: "10px",
    marginRight: "10px",
    flexDirection: "row",
    alignItems: "center",
  },
  row_one_section_two_button_text: {
    fontSize: 16,
    lineHeight: 19,
    textAlign: "right",
    color: "#5C6AC4",
  },
  row_two: {
    marginTop: "15px",
    flexDirection: "row",
    justifyContent: "space-around",
    alignItems: "center",
    marginBottom: "25px",
  },
  row_two_section: {
    marginLeft: "",
  },
  row_three: {
    flexDirection: "row",
    justifyContent: "space-around",
    alignItems: "center",
    marginTop: "10px",
    marginBottom: "25px",
  },
  row_three_section_one: {
    marginLeft: "20px",
    flex: 1,
    // alignItems: "flex-start",
    flexDirection: "row",
  },
  // row_three_section_two: {
  //   flex: "1",
  //   justifyContent: "flex-start",
  // },
  row_three_section_one_text: {
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: 18,
    color: "#454F5B",
    textAlign: "left",
    marginTop: "10px",
  },
  row_three_section_two_text: {
    fontWeight: "500",
    fontSize: 18,
    lineHeight: 22,
    color: "#454F5B",
    textAlign: "right",
    marginLeft: "80px",
    marginTop: "10px",
  },

  row_four: {
    flexDirection: "row",
    marginTop: "15px",
    marginBottom: "25px",
  },
  row_four_section: {
    marginTop: "10px",
    marginLeft: "20px",
    textAlignVertical: "center",
  },
  row_four_header: {
    fontStyle: "normal",
    fontWeight: "600",
    fontSize: 28,
    color: "#454F5B",
  },
  row_four_value: {
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: 18,
    lineHeight: 36,
    letterSpacing: -0.271698,
    color: "#637381",
    textAlign: "left",
    marginTop: 10,
  },
  row_four_value_text: {
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: 18,
    lineHeight: 22,
    color: "#454F5B",
    textAlign: "right",
    marginLeft: "50px",
    marginTop: "20px",
  },
});
export default BondDetails;
