import React from "react";
import { View, StyleSheet, TextInput, Text, Picker } from "react-native";
import { Button } from "react-native-paper";
import { Slider, withStyles } from "@material-ui/core";

interface FilterComponentProps {
  sendFilterJsontoParent: any;
}

interface FilterComponentState {}


const PrettoSlider = withStyles({
  root: {
    color: '#919EAB',
    height: 8,
  },
  thumb: {
    height: 24,
    width: 24,
    backgroundColor: '#fff',
    border: '2px solid currentColor',
    marginTop: -8,
    marginLeft: -12,
    '&:focus, &:hover, &$active': {
      boxShadow: 'inherit',
    },
  },
  active: {},
  valueLabel: {
    left: 'calc(-50% + 4px)',
  },
  track: {
    height: 8,
    borderRadius: 4,
  },
  rail: {
    height: 8,
    borderRadius: 4,
  },
})(Slider);


export class FilterComponent extends React.Component<
  FilterComponentProps,
  FilterComponentState
> {
  state = {
    couponRate: "0.0",
    min: "0",
    max: "8",
    valueSlider: [2, 6],
  };

  constructor(props: FilterComponentProps) {
    super(props);
  }

  onCouponRate(value: any) {
    this.setState({
      couponRate: value,
    });
  }
  onCouponRateConvertInFloat(value: any) {
    //var abc = value
    console.log(value);
    // value = parseFloat(value)
    //JSON.parse(abc)
    console.log("bye///////////" + value);
    // alert(abc)

    this.setState({
      couponRate: value,
    });
  }

  handleInputChangeOfMin = (text: string) => {
    if (text.length <= 1 && parseInt(text) <= 7) {
      if (/^\d+$/.test(text)) {
        this.setState({
          min: text,
        });
      }
    }
  };
  handleInputChangeOfMax = (text: string) => {
    if (parseInt(text) <= 8) {
      if (/^\d+$/.test(text)) {
        this.setState({
          max: text,
        });
      }
    } else {
      alert("You Can ernter below 8 years.");
    }
  };
  handleInputChangeOfCouponRate = (text: string) => {
    this.setState({
      couponRate: text,
    });
  };

  sendFilterJsonToParent() {
    if (parseFloat(this.state.couponRate) === 0.0) {
      let filterJson = {
        maturityRange: {
          min: parseInt(this.state.min),
          max: parseInt(this.state.max),
        },
      };
      this.props.sendFilterJsontoParent(filterJson);
    } else {
      let filterJson = {
        couponRate: JSON.parse(parseFloat(this.state.couponRate).toFixed(1)),
        maturityRange: {
          min: parseInt(this.state.min),
          max: parseInt(this.state.max),
        },
      };
      console.log(filterJson);
      this.props.sendFilterJsontoParent(filterJson);
    }
  }

  handleChange(event: any, newValue: any) {

    this.setState({
      valueSlider: newValue,
      min:JSON.stringify(newValue[0]),
      max:JSON.stringify(newValue[1])
    });
    this.sendFilterJsonToParent()
  }

  render() {
    return (
      <View style={{ flexDirection: "row", alignItems: "flex-start", marginTop: "5%", }} >
        <Text style={{ height: 22, width: 80, fontWeight: "500", fontSize: 18, marginTop: 10, lineHeight: 22, color: "#637381", }} >
          Maturity
        </Text>

        <View
          style={{
            width: "50%",
            backgroundColor: "#FFFFFF",
            borderColor: "#DFE3E8",
            borderWidth: 1,
            padding: 5,
            flexDirection: "row",
            borderRadius: 5,
            justifyContent: "space-evenly",
          }}
        >
          <Text
            style={{
              fontFamily: "Inter",
              fontSize: 16,
              fontStyle: "normal",
              color: "#919EAB",
              marginLeft: 2,
              marginRight: 2,
              marginTop: 5,
            }}
          >
            0
          </Text>
          <View style={{width:'85%'}}>
          <PrettoSlider 
            value={this.state.valueSlider}
            onChange={(e: any, newval: any) => this.handleChange(e, newval)}
            valueLabelDisplay="auto"
            aria-labelledby="range-slider"
            min={0}
            max={8}
          />
          </View>
          <Text style={{
              fontFamily: "Inter",
              fontSize: 16,
              fontStyle: "normal",
              color: "#919EAB",
              marginLeft: 2,
              marginRight: 2,
              marginTop: 5,
            }}>8 years</Text>
        </View>

        {/* <Text
          style={{
            textAlignVertical: "auto",
            marginRight: 3,
          }}
        >
          Coupon Rate :
        </Text>
        <TextInput
          style={{
            width: 80,
            textAlign: "auto",
            height: 25,
            padding: 2,
            marginRight: 10,
          }}
          value={this.state.couponRate}
          onChangeText={(value) => this.handleInputChangeOfCouponRate(value)}
        />

        <Text
          style={{
            textAlignVertical: "auto",
            marginRight: 3,
          }}
        >
          Maturity Min :
        </Text>
        <TextInput
         textContentType="telephoneNumber"
          style={{
            width: 80,
            textAlign: "auto",
            height: 25,
            padding: 2,
            marginRight: 10,
          }}
          value={this.state.min.toString()}
          onChangeText={(value) => this.handleInputChangeOfMin(value)}
        />

        <Text
          style={{
            textAlignVertical: "auto",
            marginRight: 3,
          }}
        >
          Maturity Max :
        </Text>
        <TextInput
          style={{ width: 80, textAlign: "auto", height: 25, padding: 2 }}
          value={this.state.max.toString()}
          onChangeText={(value) => this.handleInputChangeOfMax(value)}
        /> */}

        {/* <Button
          mode="contained"
          style={{
            marginLeft: 10,
          }}
          onPress={() => this.sendFilterJsonToParent()}
        >
          Apply
        </Button> */}
      </View>
    );
  }
}

const styles = StyleSheet.create({
  picker: {
    borderWidth: 1,
    fontWeight: "normal",
    fontSize: 18,
    lineHeight: 22,
    color: "#637381",
    paddingLeft: "5px",
    marginLeft: 15,
    height: 44,
    width: 227,
    borderColor: "#DFE3E8",
    borderRadius: 6,
    letterSpacing: -0.30566,
    backgroundColor: "#FFF",
  },
});
